export default class AttachFileTableSercive {

  //note 获取我的资料库信息
  static async getDataBase(data){
    return ServiceRequest.post('certificate/getMyDataLibrary',data);
  }
  //note 上传附件
  static async uploadAttach(data) {
    return ServiceRequest.post('fileAttrInfo/addAttrInfo', data);
  };
  //note 图片识别
  static async ocrAbility(data) {
    return ServiceRequest.post1('ability/ocr/ocrAbility', data);
  };

  //note 智能核查
  static async queryGroupOcrFlag(data) {
    return ServiceRequest.post('ability/ocr/queryGroupOcrFlag', data);
  };

  //note 智能填充
  static async autoFindAttr(data) {
    return ServiceRequest.post('custDatum/autoFindAttr', data);
  };

}



