<template>
  <iframe ref="iframe" :src="realModel.url||''" width="100%" height="500px" frameborder="none"></iframe>
</template>

<script>
  /**
   *@desc
   *@author
   *@date
   */
  import {realModel} from "../mixin-page-template";
  export default {
    mixins: [realModel],
    name: "ty-content-element-iframe",
    methods: {
      postMessageHandler(e) {
        if (e.data.type === 'payRedirect') {
          util.navigate({
            location:top.location,
            href:e.data.url
          });

        }
      }
    },
    created() {
      window.addEventListener('message', this.postMessageHandler)
    },
    destroyed() {
      window.removeEventListener('message', this.postMessageHandler)
    }
  }
</script>
<style scoped>
  .ty-content-element-iframe {
    width: 100%;
    height: 100%;
  }
</style>



