<template>
  <div class="plate-input">
    <label style="margin-right:5px">陕</label>
    <Input v-model="cardNumber" v-bind="extend" style="width:300px" @on-blur="search"/>
  </div>
</template>

<script>
  /**
   *@author       高琛
   *@date         2018/12/17 22:16
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./content-box/mixin-page-template";
  import SingleIndexService from '../service/single-index-service'

  export default {
    name: "plate-input",
    mixins: [realModel],
    props: {
      prop: {
        type: [String, Array]
      },
      extend: {
        type: Object,
        default() {
          return {}
        }
      },
    },
    data() {
      return {
        cardNumber: ''
      }
    },
    watch: {
      cardNumber(a, b) {
        this.realModel[this.prop] = `陕${a}`;
      }
    },
    mounted() {
      if (this.realModel[this.prop]) {
        this.cardNumber = this.realModel[this.prop].substr(1)
      }
    },
    methods: {
      search() {
        var reg = /^[A-HJ-NP-Z]{1}[A-Z0-9]{4}[A-Z0-9挂学警港澳]{1}$/g;
        if (!reg.test(this.cardNumber)) {
          return;
        }
        SingleIndexService.QueryCarId(
          {
            "cardNumber": this.cardNumber
          }
        ).then(res => {
          // console.log(res)
          if (res) {
            console.log(res.plateVIN)
            this.realModel.plateVIN = res.plateVIN;
            this.realModel.CLPP1 = res.cLPP1;
            this.realModel.FDJH = res.fDJH;
          }

        }).catch(e => {
          util.alert({type: 'error', content: e.message})
        });

      }
    }
  }
</script>

<style scoped>

</style>



