<template>
   <div class="form-select">
     <Select v-model="value" style="width:200px" @on-change="handleChange" :label-in-value="true" :style="{width:'318px'}">
       <Option v-for="item in itemList" :value="item.regionCode" :key="item.regionCode">{{ item.regionName }}</Option>
     </Select>
   </div>
</template>

<script>
  /**
   *@author       冯朝晖
   *@date         2018-12-11 15:45
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */

  import Service from '../../service/suggestion/home-suggestion-suggest-service';
  import {realModel} from "./../content-box/mixin-page-template";

  export default {
    name: 'ty-form-select',
    mixins: [realModel],
    props: {
      //note 字段在绑定对象中对应的key值
      prop: {
        type: [String, Array]
      }
    },
    data() {
      return {
        itemList: [],
        value: ''
      }
    },
    methods: {
      handleChange({value,label}) {
        this.realModel[this.prop] = value;
        this.realModel.streetOfficeradeDesc=label;
      },
      getInitData(regionCode) {
        Service.getFeedbackDepartment({parentRegionCode: regionCode}).then((res) => {
          if (!res) return;
          this.dealData(res);
        })
      },
      dealData(data) {
        data.map((item) => {
          let {regionCode, regionName} = item;
          this.itemList.push({regionCode, regionName});
        })
      }
    },
    mounted () {
      // 请求数据
      this.value = this.realModel ? this.realModel[this.prop] : '';
      let regionCode = localStorage.areaCoded || '';
      if (regionCode) {
        this.getInitData(regionCode);
      }
    }
  }
</script>

<style lang="less" scoped>

</style>



