<template>
  <div>
    <processHand v-bind="obj"></processHand>
  </div>
</template>

<script>
  import processHand from '../../../components/process-handing'
  export default {
    name: "example-process-handing",
    components: {processHand},
    data(){
      return {
        obj:{
          processList:[
            {handTitle:'网上办理',handList:[
                {handMsg:'申请人在市政务中心导办台抽号后，依据《西安市户外广告设置管理条例》第二十二条，到市城市管理局窗口递交申报材料。管理部门对材料进行初审，材料初审合格后予以受理，发放受理通知书；材料不齐或不符合法定形式的，当场告知申请人或告知申请人需补齐的全部材料。',value:1},
                {handMsg:'管理部门组织现场勘察；',value:2},
                {handMsg:'现场勘察后,管理部门组织相关部门联合会审；',value:3},
                {handMsg:'会审合格者，申请人交纳费用；',value:4},
                {handMsg:'管理部门发放施工证；',value:5},
                {handMsg:'管理部门按照标准组织验收设施；',value:6},
                {handMsg:'验收合格后，申请人凭受理通知书在市城市管理局窗口领取《西安市户外广告设置许可证》',value:7},
              ],extend:{type:'lookup-fee'},urlRoute:''},
            {handTitle:'窗口办理',handList:[
                {handMsg:'申请人在市政务中心导办台抽号后，依据《西安市户外广告设置管理条例》第二十二条，到市城市管理局窗口递交申报材料。管理部门对材料进行初审，材料初审合格后予以受理，发放受理通知书；材料不齐或不符合法定形式的，当场告知申请人或告知申请人需补齐的全部材料。',value:1},
                {handMsg:'管理部门组织现场勘察；',value:2},
                {handMsg:'现场勘察后,管理部门组织相关部门联合会审；',value:3},
                {handMsg:'会审合格者，申请人交纳费用；',value:4},
                {handMsg:'管理部门发放施工证；',value:5},
                {handMsg:'管理部门按照标准组织验收设施；',value:6},
                {handMsg:'验收合格后，申请人凭受理通知书在市城市管理局窗口领取《西安市户外广告设置许可证》',value:7},
              ],extend:{type:'lookup-map'},urlRoute:''},
          ],
        }

      }
    }
  }
</script>



