<template>
    <examples-demo title="下拉列表组件 select" desc="" :code="code.formSelect"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-form-select slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateFormSelect from "./example-page-template-form-select";

  export default {
    name: "example-page-template-form-radio-demo",
    components: {ExamplePageTemplateFormSelect, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns:[
          {
            title: '属性',
            key: 'prop'
          },
          {
            title: '说明',
            key: 'desc'
          },
          {
            title: '类型',
            key: 'type'
          },
          {
            title: '默认值',
            key: 'default'
          }
        ],
        data:[
          {
          prop: 'fullModel',
          desc: '全局绑定对象,',
          type: '*',
          default: '空'
        },
          {
            prop: 'model',
            desc: '无fullModel情况下,给model赋值对象直接绑定model,' +
            '有fullModel情况下,默认$root取fullModel根对象绑定,如果去内部需要设置model,' +
            '例如"a.b.c",就会绑定fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop:'rules',
            desc:'表单验证规则，具体配置查看 async-validator',
            type:'Object|Array',
            default:'空'
          },
          {
            prop:'extend',
            desc:'form的拓展配置',
            type:'Object',
            default:'空'
          },
          {
            prop:'labelWidth',
            desc:'form-item中label的宽度',
            type:'Number',
            default:'205'
          },
          {
            prop:'span',
            desc:'form-item的所占宽度（此处用到了栅格 共24）',
            type:'Number',
            default:'24'
          },
          {
            prop:'items',
            desc:'form-item的集合',
            type:'Array',
            default:'空'
          },
          {
            prop:'extend',
            desc:'form-item中的拓展，一些基本属性placeholder,span',
            type:'Object',
            default:'空'
          },
          {
            prop:'items',
            desc:'form-item中的拓展，配置下拉列表每一项的属性，写死下拉数据的时候使用',
            type:'Array',
            default:'空'
          },
          {
            prop:'excat',
            desc:'type的值有lookup-map，lookup-fee，lookup-project，lookup-number，lookup-query，tip-adv，可以给form-item拓展组件',
            type:'Array',
            default:'空'
          },
          {
            prop:'dynamic',
            desc:'下拉查询配置,url查询的接口，params查询时候的入参,label:"",value:"" 把后台返回的值转为对应的label或value，不需要转的时候不需要写',
            type:'Object',
            default:'空'
          }
        ]
      }
    }
  }
</script>



