let Code = {};
Code.base=`
<template>
    <ExtendTable :title='tableTitle' :data1="tableData"></ExtendTable>
</template>

<script>
  export default {
    name: "index",
    data() {
      return {
        tableTitle:['操作', '工会协商代表姓名','工会协商代表身份证号'],
        tableData:[{name1:'111',name2:'222'}],
      }
    }
  }
</script>
`;
export default Code;



