let Code = {};
Code.base=`
<template>
  <div>
    <GuideItem :guideStepData="StepData"></GuideItem>
  </div>
</template>

<script>
  import GuideItem from '../../../components/guide-item'
  export default {
    name: "user-info",
    components: {GuideItem},
    data(){
      return {
        StepData: [
          {title:'支付方式分为以下几种',content:'在线支付：请您进入“支付缴费”，按提示进行操作；目前在线支付支持支付宝支付、微信支付、银行卡、银联在线、信用卡等方式进行支付，具体请以支付页面信息为准，您可根据您的使用喜好进行选择。'},
          {title: '找不到支持的银行怎么办？',content:'如果您在银行卡支付方式内找不到可以使用的银行，可以选择银联在线支付，银联在线支持全国各地市城镇银行以及农村信用社。'},
          {title:'为什么要开通快捷支付，开通有什么好处？',content:'只要您拥有信用卡即可进行网上支付，无需开通网银；验证银行卡信息后，只需输入手机验证码或支付密码一步支付，方便快捷；同时您的支付卡信息，在输入和存储的过程中进行严格加密，保障您的支付安全。'},
          {title:'生成的支付二维码或者条形码是否可以重复支付？',content:'不能重复支付，当该笔订单支付成功后，支付二维码或条形码将无效。'},
          {title: '扫码支付可以使用哪些支付方式？',content:'可以使用支付宝支付、微信支付等支付方式付款'}
        ]
      }
    }
  }
</script>
`;
export default Code;



