/**
 *@desc
 *@author  冯朝晖
 *@date    2019.9.3
 */
let Code = {};
Code.base = `<template>
  <ty-page-template :data="pageData"/>
</template>
<script>
  /**
   *@desc
   *@author  冯朝晖
   *@date  2018.12.21
   */
  export default {
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: 'time label',
            fullModel: {
              date: ''
            }
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    rules: {
                    },
                    //Form扩展配置
                    extend: {
                      labelWidth:200,
                      span:12,
                    },
                    items:
                      [ {
                        type: 'timeLabel',
                        label: '选择',
                        prop: 'date',
                      }]
                  }
                },
              ]
            }
          ]
        },
      }
    }
  }
</script>
`;
export default Code;



