<template>
  <div>
    <DatePicker
      :value="this.realModel[this.prop]"
      type="datetimerange"
      :format="extend.format"
      :placeholder="extend.placeholder"
      :style="{ minWidth: extend.itemWidth || '100%' }"
      @on-change="handleChange"
      :editable="false"
    ></DatePicker>
  </div>
</template>

<script>
  /**
   *@author       冯朝晖
   *@date         2018-12-06 15:43
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./../content-box/mixin-page-template";

  export default {
    mixins: [realModel],
    name: 'ty-data-time-picker',
    props:{
      //note 字段在绑定对象中对应的key值
      prop: {
        type: [String, Array]
      },
      extend: {
        type: Object,
        default() {
            return {}
        }
      },
    },
    data() {
      return {}
    },
    methods: {
      handleChange(duration) {
        this.realModel[this.prop] = duration;
      }
    }
  }
</script>

<style lang="less" scoped></style>



