<template>
  <div class="multiple">

  </div>
</template>

<script>
  /**
   *@author       叶章浩
   *@date         2018/12/6 15:50
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./content-box/mixin-page-template";
  import Service from '../service/home-procceeds-service';
  export default {
    name: "qualificationCheck",
    mixins: [realModel],
    props: {
      //note 字段在绑定对象中对应的key值
      COMPANYBASEUUID:{
        type:String,
        default:"-1"
      },
      QYLX:{
        type:String,
        default:"7"
      },
      QYMC:{
        type:String,
        default:"张三的小餐福安"
      },
      DZ:{
        type:String,
        default:"南窑头东区4排2号"
      },
      ID:{
        type:String,
        default:"0"
      },

      prop: {
        type: [String, Array]
      },
      extend: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    watch: {
    },
    computed: {

    },
    data() {
      return {
        //模拟radio的数据
        checkBoxData:this.extend,
        //文本框数据
        textVal: ''
      }
    },
    methods: {
      //note 监听多选框
      changeCheck(val) {
        // debugger;
        // console.log(val)
      },
      //note 初始化
      init() {
         debugger;
        //您已办理社保，不能继续办理
        let params={
          ID: this.ID,
          COMPANYBASEUUID:this.COMPANYBASEUUID,
          QYLX: this.QYLX,
          QYMC:this.realModel[this.QYMC],
          DZ: this.realModel[this.DZ]
        }
      /*  let params={
          ID: "0",
          COMPANYBASEUUID:"-1",
          QYLX: "7",
          QYMC:this.realModel["QYMC"],
          DZ: this.realModel["DZ"]
        }*/
        Service.qualificationCheck(params).then(data => {

          if(data.state!="200"&&data.state!="300"){
            this.$Modal.confirm({
              title: '提示!',
              content:'<div>您已办理社保，不能继续办理</div>',
              onOk(){
                util.login();
              },
              onCancel(){
                util.login();
              }
            })
          }

        })
      }
    },
    created() {
      this.init();
    }
  }
</script>

<style scoped lang="less">
  .ty-radio {

  .radio {
    display: inline-block;
    width: 160px;
    height: 32px;
    border: 2px solid #ccc;
    margin-right: 10px;
    text-align: center;
    line-height: 32px;
    color: #999;
    cursor: pointer;
    box-sizing: border-box;
  }

  .radioactive {
    border-color: #4489DC;
    color: #4489DC;
    background: url("../assets/img/u1380.png") no-repeat right bottom;
    background-size: 25px 17px;
  }

  }

  .multiple {

  .ivu-checkbox-group {

  .ivu-checkbox-wrapper {
    margin-right: 32px;

  .checkInput {
    margin-left: 5px;
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom: 1px solid black;
    width: 92px;
  }

  }
  }
  }
</style>



