<template>
  <examples-demo title="标题 title" desc="" :code="code.title">
    <example-page-template-title slot="demo"/>
  </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateTitle from "./example-page-template-title";

  export default {
    name: "example-page-template-title-demo",
    components: {ExamplePageTemplateTitle, ExamplesDemo},
    data() {
      return {
        code: Code
      }
    }
  }
</script>



