export default class SingleIndexService{

  //NOTE 查询环评批复文号
  static async Query(data){
    return ServiceRequest.post('certificate/getEiaRelayInfo',data);
  }
  //NOTE 查询身份证号
  static async QueryIdCard(data){
    return ServiceRequest.post('ability/driverLicenseQuery',data);
  }
  //NOTE 查询车牌号
  static async QueryCarId(data){
    return ServiceRequest.post('ability/vehicleLicenseQuery',data);
  }
  //NOTE 查询网签合同号
  static async queryContract(data){
    return ServiceRequest.post('ability/queryContract',data);
  }

  //NOTE 查询申请号
  static async QueryApplicationNumber(data){
    return ServiceRequest.post('certificate/qryGroupInfo',data);
  }

  //note 买方基本信息查询按钮
  static async QueryScals(data){
    return ServiceRequest.post('certificate/qryGroupInfo',data);
  }
  //note 卖方房屋产权证号查询
  static async QuerySellsHome(data){
    return ServiceRequest.post('ability/housePropertyCard',data);
  }
  //note 小餐饮基本信息查询
  static async QueryXiaocanyin(data){
    return ServiceRequest.post('ability/selectArchivesCompany',data);
  }

  //note  查询监管协议号
  static async queryAgreementNumber(data){
    return ServiceRequest.post('ability/queryAgreement',data);
  }

  //note  二手房合同变更 申请方校验
  static async houseContractChange(data){
    return ServiceRequest.post('ability/houseContractChange',data);
  }
  //note 能力平台-房管局-FG014资格查询数据接口
  static async qualificationEnquiry(data){
    return ServiceRequest.post('ability/qualificationEnquiry',data);
  }
  // note 查询医保号
  static async queryHealthNo(data){
    return ServiceRequest.post('certificate/qryListKeyValueInfo',data);
  }
  // note 异地就医网上查询
  static async elsewhereSeekDoctor(data){
    return ServiceRequest.post('ability/elsewhereSeekDoctor',data);
  }
  // note 医保卡个人账户查询
  static async qryPersonYBAccount(data){
    return ServiceRequest.post('ability/qryPersonYBAccount',data);
  }
  // note 出具医疗（大额）保险费、生育保险费缴费电子发票查询
  static async medicalTreatmentQuery(data){
    return ServiceRequest.post('ability/medicalTreatment',data);
  }
}





