<template>
    <div class="time-label">
      <label>{{date}}</label>
    </div>
</template>

<script>
  /**
   *@author       冯朝晖
   *@date         2018-12-18 01:40
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./../components/content-box/mixin-page-template";
  export default {
    name: 'ty-time-label',
    mixins: [realModel],
    props:{
      prop: {
        type: [String, Array]
      }
    },
    computed: {
      date() {
        this.realModel[this.prop] = this.realModel[this.prop] || moment(new Date()).format('YYYY-MM-DD')
        return moment(this.realModel[this.prop]).format('YYYY年MM月DD日')
      }
    }
  }
</script>




