let Code = {};
Code.base = `<template>
  <ty-page-template :data="pageData"/>
</template>
<script>
  /**
   *@desc
   *@author  冯朝晖
   *@date  2018.12.6
   */
  export default {
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: 'select',
            fullModel: {
            }
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //Form扩展配置
                    extend: {
                      labelWidth:0,
                      span:24,
                    },
                    items:
                      [ {
                        type: 'pureTable',
                        label: '',
                        prop: '',
                        extend: {
                          placeholder: '请选择',
                        }
                      }]
                  }
                },
              ]
            }
          ]
        },
      }
    },
    methods: {
    }
  }
</script>
`;
export default Code;



