<template>
  <Row class="ty-retire-info">
    <Col span="3">
      <img src="../../../../assets/img/testPhoto.png" style="width:80px;height: 100px;"/>
    </Col>
    <Col span="10">
      <div class="ty-retire-info-name">{{data.name}}</div>
      <div class="ty-retire-info-phone">{{data.phone}}</div>
      <div class="ty-retire-info-address">{{data.address}}</div>
    </Col>
    <Col span="1">
      <Divider class="ty-retire-info-divider" type="vertical"></Divider>
    </Col>
    <Col span="10">
      <div>
        <div class="ty-retire-info-label">性别:</div>
        <div>{{data.name}}</div>
      </div>
      <div>
        <div class="ty-retire-info-label">出生年月:</div>
        <div>{{data.name}}</div>
      </div>
      <div>
        <div class="ty-retire-info-label">民族:</div>
        <div>{{data.name}}</div>
      </div>
      <div>
        <div class="ty-retire-info-label">籍贯:</div>
        <div>{{data.name}}</div>
      </div>
    </Col>
  </Row>
</template>

<script>
  /**
   *@author       谢辉
   *@date         2018/9/24 20:48
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  export default {
    name: "ty-retire-info",
    props: {
      data: {
        type: Object,
        default() {
          return {};
        }
      }
    },
    data() {
      return {}
    }
  }
</script>

<style lang="less" scoped>
  @retire-cls-prefix: ~'ty-retire-info';
  .@{retire-cls-prefix} {
    text-align: left;
    color: #666666;
    height: 136px;
    line-height: 2;
    > div {
      height: 100px;
      top: 13%;
      &:not(:first-child) {
        padding-left: 20px;
      }
    }
    &-name {
      font-size: 24px;
      font-weight: bold;
    }
    &-phone {
      font-size: 14px;
      color: #4A89DC;
    }
    &-divider {
      padding: 0 !important;
      height: 100%;
      /*top:10%;*/
    }
    &-label{
      float:left;
      width:80px;
      padding-right:20px;
      text-align: right;
      clear:both;
    }
  }
</style>



