export default class CommonService {

  //NOTE 获取token
  static async getToken(){
    return ServiceRequest.get('casRoute/getToken',{});
  };
  //NOTE 获取用户信息
  static async getUserInfo(){
    return ServiceRequest.get('user/users/signup/getCurrentUser',{});
  };
  //note 弃用,目前采用本地加载menu
  static async getMenuData(){
    return ServiceRequest.get('');
    // return ServiceRequest.get('static/data/menu.json');
  }
}



