import Vue from 'vue'
import Router from 'vue-router'
import _ from 'lodash'

Vue.use(Router)

function importRoutes(r) {
  let routes=r.keys().map(key => r(key).default);
  return _.flatten(routes);
}

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '*',
      redirect: '/index'
    },
    {
      path: '/',
      name: 'index',
      component: require('@/examples/pages/index').default,
      children: importRoutes(require.context('@/', true, /^\.\/examples\/pages\/((?!\/)[\s\S])+\/route\.js$/)),
    },
    {
      path: '/apps',
      name: 'apps',
      component: require('@/examples/apps/index').default,
      children: importRoutes(require.context('@/', true, /^\.\/examples\/apps\/((?!\/)[\s\S])+\/route\.js$/)),
    },
  ]
})




