<template>
  <div class="multiple">

  </div>
</template>

<script>
  /**
   *@author       张利萍
   *@date         2018/12/6 15:50
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./content-box/mixin-page-template";
  import Service from '../service/home-procceeds-service';
  export default {
    name: "identityCheck",
    mixins: [realModel],
    props: {
      //note 字段在绑定对象中对应的key值
      idNumber:{
        type:String,
        default:""
      }

    },
    watch: {
    },
    computed: {

    },
    data() {
      return {
        //模拟radio的数据
        checkBoxData:this.extend,
        //文本框数据
        textVal: ''
      }
    },
    methods: {
      //note 初始化
      init() {
        //您已办理社保，不能继续办理
        let params={
          idNumber:this.realModel[this.idNumber]
        };
        let _this = this;
        Service.identityCheck(params).then(data => {
          let res = JSON.parse(data);
          if(res.sfcf!="1"){
            this.$Modal.info({
              title: '提示!',
              content:'<div>您已参加西安市居民医疗保险</div>',
              onOk(){
                _this.$router.push({
                  name:Consts.ROUTES.COM_HOME
                })
              }
            })
          }
        })
      }
    },
    created() {
      this.init();
    }
  }
</script>

<style scoped lang="less">
  .ty-radio {

  .radio {
    display: inline-block;
    width: 160px;
    height: 32px;
    border: 2px solid #ccc;
    margin-right: 10px;
    text-align: center;
    line-height: 32px;
    color: #999;
    cursor: pointer;
    box-sizing: border-box;
  }

  .radioactive {
    border-color: #4489DC;
    color: #4489DC;
    background: url("../assets/img/u1380.png") no-repeat right bottom;
    background-size: 25px 17px;
  }

  }

  .multiple {

  .ivu-checkbox-group {

  .ivu-checkbox-wrapper {
    margin-right: 32px;

  .checkInput {
    margin-left: 5px;
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom: 1px solid black;
    width: 92px;
  }

  }
  }
  }
</style>



