<template>
  <div class="ty-page-template">
    <template v-for="item,key in data.elements">
      <ty-content-box v-if="item.type==='box'" v-bind="item" :fullModelData="data.page.fullModel" />
      <ty-button-oprator v-if="item.type==='btns-oprator'" v-bind="item.data" :fix="true"/>
    </template>
    <slot></slot>
  </div>
</template>
<script>
  /**
   *@desc
   *@author
   *@date
   */
  export default {
    name: "ty-page-template",
    provide(){
      return {
        template:this
      }
    },
    props: {
      data: {
        default() {
          return {
            page: {
              fullModel:{
                default(){
                  return {}
                }
              }
            },
            //页面元素数据
            elements: {
              default() {
                return [
                  {
                    type: 'step',
                    current: 1,
                    steps: [],
                    extend: {}//扩展配置
                  }
                ]
              }
            }
          }
        }
      },
    },
    data(){
      return {
        flag:'',// 1 验证通过 0 验证不通过
        code:''
      }
    },
    computed:{
      tplData(){
        return {
          page:this.data.page,
          fullModel:this.data.page.fullModel,
          element:this.data.elements
        }
      }
    },
    methods:{
      clear(){
        this.code="";
        this.flag="";
      },
      getData(){
        return this.data.page.fullModel || {};
      },
      //note 全量校验
      validate(){
        this.data.page.fullModel.templateCache.validators.forEach((item,index)=>{
          if(this.code!==0){
            if(item.formValidator()){
              this.flag=true;
            }else{
              this.code=0;
              this.flag=false;
            }
          }else{
            return false;
          }
        })
        if(this.flag){
          this.clear()
          return true;
        }else{
          this.clear()
          return false;
        }
      }
    },
    created(){

    }
  }
</script>

<style lang="less">

</style>



