<template>
  <div class="date-time-picker-list">
      <Col class="date-time-picker-content" span="24">
        <template v-for="(item, key) in items">
          <div :style="{ marginTop: key !== 0 ? extend.marginTop : 0 }" class="date-time-picker-list-margin">
            <ty-date-time-picker-special
              :value="prop ? realModel[prop][Number(key)] : ''"
              :id="key"
              :extend="extend.child"
              @on-change="handleChange"/>
            <div @click="delSurplusPeriod(key)"
                 class="del-data-time-picker-btn"
                 :style="{ display: key === 0 ? 'none' : 'block'}"
            >
              <Icon type="md-remove-circle" class="del-data-time-picker-btn-icon"/>
            </div>
          </div>
        </template>
      </Col>
      <Col class="add-data-time-picker">
        <div @click="addNextPeriod"
             :class="[{ 'active': isActive }, 'add-data-time-picker-btn']">
              <Icon type="md-add-circle" class="add-data-time-picker-btn-icon"/>
        </div>
      </Col>
  </div>
</template>

<script>
  /**
   *@author       冯朝晖
   *@date         2018-12-06 17:10
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import TyDateTimePickerSpecial from './data-time-picker-special';
  import {realModel} from "./../content-box/mixin-page-template";

  export default {
    name: 'date-time-picker-list',
    mixins: [realModel],
    components: {
      TyDateTimePickerSpecial
    },
    props:{
      //note 字段在绑定对象中对应的key值
      prop: {
        type: [String, Array]
      },
      extend: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed: {
      isActive () {
        return this.items.length < this.extend.maxNum;
      }
    },
    data() {
      return {
        items : []
      }
    },
    created() {

        let tempArr = [];
        let itemData = this.realModel[this.prop];
        if (!itemData || !itemData.length) {
          tempArr.push(1);
        } else {
          itemData.forEach(() => {
            tempArr.push(1);
          })
        }
        this.items = tempArr;
    },
    methods: {
      handleChange(duration, id) {
        this.realModel[this.prop][Number(id)] = duration;
      },
      addNextPeriod() {
        if (!this.isActive) {
          return;
        }
        this.items.push(1);
        this.realModel[this.prop][this.items.length - 1] = ['', ''];
      },
      delSurplusPeriod(id) {
        this.items.pop();
        this.realModel[this.prop].splice(Number(id), 1)
        Bus.$emit('valiteFormItem', this.prop);
      }
    }
  }
</script>



