<template>
  <examples-demo title="点击查看收费标准 lookup-map" desc="" :code="code.lookupFee"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-lookup-fee slot="demo"/>
  </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateLookupFee from "./example-page-template-lookup-fee";

  export default {
    name: "example-page-template-lookup-fee-demo",
    components: {ExamplePageTemplateLookupFee, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns: [
          {
            title: '属性',
            key: 'prop'
          },
          {
            title: '说明',
            key: 'desc'
          },
          {
            title: '类型',
            key: 'type'
          },
          {
            title: '默认值',
            key: 'default'
          }
        ],
        data: [
          {

          }
        ]
      }
    }
  }
</script>



