import Service from "../utils/service";

export default class InstanceService  {
  //note  根据自然人姓名、身份证号码创建自然人信息,身份认证
  static async getNaturalPerson(data){
    return ServiceRequest.post('customer/insertInfoPerson',data);
  }
  //note 身份证件的修改认证
  static async idVerificate(data) {
    return ServiceRequest.post(`user/users/signup/idNumberCheck`, data)
  }
  //note 手机短信的修改认证
  static async telVerficate(data) {
    return ServiceRequest.post(`user/users/signup/cellphoneCheckSendRandomCode`, data)
  }
  //note 邮箱的修改认证

  //note 枚举list
  static async dicQuery(data) {
    return ServiceRequest.post(`util/selectDicBypDicVal`, data)
  }
  //note 校验图形验证码
  static async getCaptchaCheck(data) {
    return ServiceRequest.post(`captcha/contrastCode`, data)
  }
  //note 自然人认证
  static async getPersonIdentity(data) {
    return ServiceRequest.post(`customer/personIdentityCa`, data)
  }
  //note 个人用户管理查询
  static async getPersonIQuery(data) {
    return ServiceRequest.post(`user/qryUserInfoManager`, data)
  }
  //note 密码
  static async changePwd(data) {
    return ServiceRequest.post(`user/changeUserPwd`, data)
  }
  //note 校验手机验证码
  static async changePhoneCode(data) {
    // return ServiceRequest.post(`captcha/contrastPhoneCode`, data)
    return ServiceRequest.post(`captcha/users/signup/contrastPhoneCode`, data)
  }
  //note 修改用户名
  static async changeUserName(data) {
    return ServiceRequest.post(`user/changeLoginName`, data)
  }
  //note 修改手机号
  static async changeTel(data) {
    return ServiceRequest.post(`user/changeCellphone`, data)
  }
  //note 修改邮箱
  static async changeEmail(data) {
    return ServiceRequest.post(`user/changeEmail`, data)
  }

  //    忘记密码的接口
  //  note 修改密码用户名校验
  static async pwdRepeatName(data) {
    return ServiceRequest.post(`user/users/signup/checkUserLoginName`, data)
  }
  //  note 修改密码身份认证，手机号+用户名
  static async pwdIdentity(data) {
    return ServiceRequest.post(`user/users/signup/selectByCellphone`, data)
  }
  //  note 修改密码重置
  static async pwdRepeatPwd(data) {
    return ServiceRequest.post(`user/users/signup/changePasswordByUserId`, data)
  }
   //  note 获取民族信息
  static async getNation(data) {
    return ServiceRequest.post(`util/selectDicCacheForMap`, data)
  }
  //note 获取不加密的手机号
  static async getPhone(data){
    return ServiceRequest.post(`user/users/signup/passwordSendVerificationCode`,data)
  }
  //note 修改法人的手机号
  static async changefaRenPhone(data){
    return ServiceRequest.post(`user/users/signup/changeEnterpriseCellphone`,data)
  }
  //note 获取认证情况
  static async getVerifySituation(data){
    return ServiceRequest.post(`/customer/qryPersonAuthenticateStatus`,data)
  }

  //note 自然人各种身份认证：身份证，电信，护照，银联
  static async doVerify(data){
    return ServiceRequest.post(`/customer/personAuthenticate`,data)
  }

  //note 自然人认证详情
  static async getVerifyDetailByType(data){
    return ServiceRequest.post(`/customer/qryPersonAuthenticate`,data)
  }
}



