<template>
  <div class="pickAddress">


    <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="0">

      <RadioGroup v-model="pickType" vertical>
        <Radio label="0">
          <span>自取</span>
        </Radio>
        <div class="zqDiv">
          <FormItem prop="yjAddresskId">
            <Select v-model="formValidate.yjAddresskId" :label-in-value="true" style="width:500px"
                    @on-change="v=>{ zqOptionChange(v)}">
              <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
          <div class="hideSelfPick" v-show="showSelfPick">

          </div>
        </div>
        <Radio label="1">
          <div class="displayI">
            <span>邮寄</span>
            <router-link :to="{path:'home-member-center-personalUser',query:{}}" class="addressManager">
              地址管理
            </router-link>
          </div>
        </Radio>
        <div class="yjDiv">
          <div class="addressList">
            <FormItem prop="mydefaultAddress">
              <RadioGroup v-model="defaultAddress" vertical>
                <template v-for="(item ,key) in getAddressListData">
                  <template v-if="item.isSelected">
                    <Radio :label="item.addressId" :id="item.addressId" class="addressListLi defaultClass">

                      <span class="marginR10 fn-middle">{{item.addressProvinceName}}</span>
                      <span class="marginR10 fn-middle">{{item.addressCityName}}</span>
                      <span class="marginR10 fn-middle">{{item.addressAreaName}}</span>
                      <span class="marginR10 fn-middle">{{item.addressStreetName}}</span>
                      <span class="marginR10 fn-middle">({{item.fullName}} 收)</span>
                      <span class="marginR10 fn-small">{{item.telePhone}}</span>
                      <template v-if="item.defaultAddress==1">
                        <span class="marginL10 fn-middle">默认地址</span>
                      </template>
                      <template v-else>
                        <span class="marginL10 fn-small setDefault"
                              @click="setDefaultAddress(item.addressId)">设为默认地址</span>
                      </template>
                      <div class="updateAddress">
                        <router-link
                          :to="{path:'home-member-center-addressAdd',query:{addressId:item.addressId,editType:'addressEdit'}}">
                          修改地址
                        </router-link>
                      </div>
                    </Radio>
                  </template>
                  <template v-else>
                    <Radio :label="item.addressId" :id="item.addressId" class="addressListLi ">
                      {{item.addressId}}
                      <span class="marginR10 marginL14">{{item.addressProvinceName}}</span>
                      <span class="marginR10">{{item.addressCityName}}</span>
                      <span class="marginR10">{{item.addressAreaName}}</span>
                      <span class="marginR10">{{item.addressStreetName}}</span>
                      <span class="marginR10">({{item.fullName}} 收)</span>
                      <span class="marginR10 fn-small">{{item.telePhone}}</span>
                      <template v-if="item.defaultAddress==1">
                        <span class="marginL10 fn-middle">默认地址</span>
                      </template>
                      <template v-else>
                        <span class="marginL10 fn-small setDefault"
                              @click="setDefaultAddress(item.addressId)">设为默认地址</span>
                      </template>
                    </Radio>
                  </template>
                </template>
              </RadioGroup>
            </FormItem>
            <div class="hideAddressList" v-show="showAddress">

            </div>
          </div>
          <router-link :to="{path:'home-member-center-addressAdd',query:{editType:'addressAdd'}}"
                       class="marginT10 displayB">
            + 使用新地址
          </router-link>
        </div>
      </RadioGroup>
    </Form>

    <!--  <router-link :to="{path:'home-member-center-addressAdd',query:{addressId:item.id,editType:'addressAdd'}}" tag="div" class="list" v-for="(item,index) in listName" @click="toDetail()">
          + 使用新地址
      </router-link>-->

  </div>

</template>

<script>
  /**
   *@author       叶章浩
   *@date         2018/12/7 15:09
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./content-box/mixin-page-template";
  import Bus from '../utils/bus'
  import {mapGetters} from 'vuex'
  import Service from '../service/home-member-center-addressAdd'

  export default {
    name: "address-select",
    mixins: [realModel],
    props: {
      //note 字段在绑定对象中对应的key值
      prop: {
        type: String
      },
      labelInValue: {
        type: Boolean,
        default: true,
      },
      //note 是否参与校验
      validator: {
        type: Boolean,
        default: true,
      },
      extend: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed: {
      ...mapGetters([
        'storeUserInfo'
      ]),
      getAddressListData() {
        return this.addressListData;
      }
    },
    data() {

      return {
        formValidate: {
          yjAddresskId: ""
        },
        ruleValidate: {
          yjAddresskId: [
            {required: true, message: '请选择自取地址', trigger: 'change'}
          ]
        },
        //下拉框里所有的值
        cityList: [{
          label: "西安市长安区郭杜北街49号市公安局交通警察支队车辆管理总所业务办理大厅9号窗口",
          value: "1"
        }],
        model8: '',//note 下拉框对应选中的值
        pickType: '',//note 取件类型  自取 邮寄
        showAddress: true,//note 是否遮挡寄送区域
        showSelfPick: true,//note 是否遮挡自取区域
        defaultAddress: "",//note 寄送区域的model
        addressListData: [],//note 寄送区域的数据
        yjAddresskId: "",//note 存放邮寄里面单选的值
        selfPickId: "",//note 存放自取里面下拉框的值
        saveSelectAddressId: "",//note 存放自取里面下拉框的值
        addressName: "",//note 存放选择地址ID对应的value
      }
    },
    methods: {

      //note 重置校验
      handleReset() {
        this.$refs["formValidate"].resetFields();
      },
      //note 删除校验
      delValidate() {

        this.handleReset();
      },
      //note 添加校验
      addValidate() {
        this.ruleValidate = {
          yjAddresskId: [
            {required: true, message: '请选择自取地址', trigger: 'change'}
          ]
        };
        this.formValidator();
      },
      //note 校验
      formValidator() {
        debugger;
        if (this.pickType == "1") {
          if (this.fullModel[this.extend.prop] == '') {
            return false
          }
          return true;
        }

        this.$refs['formValidate'].validate((valid) => {
          if (valid) {
            this.flag = true;
          } else {
            this.flag = false
          }
        })
        return this.flag
      },

      //note 监听下拉框改变的值
      zqOptionChange(yjAddresskId) {
        debugger;
        console.log(this.yjAddresskId);
        if (yjAddresskId) {
          this.fullModel[this.extend.prop] = yjAddresskId.value;
          this.fullModel[this.extend.propName] = yjAddresskId.label;
        }

      },
      //NOTE 设置为默认地址
      setDefaultAddress(id) {
        Service.updateDefaultAddress({
          userId: this.storeUserInfo.userId,
          addressId: id,
          operSys: '0'
        }).then(res => {
          this.setAddressListData();
        })
      },
      //note 修改自取或邮寄的背景颜色 0 自取  1 邮寄
      changePickTypeBg(val) {
        // debugger;
        if (val == 0) {
          this.addValidate();
          this.showAddress = true;
          this.showSelfPick = false;
          //note 这里要判断当前下面是否又被选中的，如果有，不变，否则，设置选项属性为空
          if (this.selfPickId == "") {
            this.fullModel[this.extend.prop] = "";
          }
          this.updateDefault("");
        } else if (val == 1) {
          this.delValidate();
          this.showAddress = false;
          this.showSelfPick = true;
          //this.yjAddresskId="";//note 设置自取里为空+
          if (this.yjAddresskId == "") {
            this.fullModel[this.extend.prop] = "";
          }
          this.updateDefault(this.yjAddresskId);
        }

        //修改fullmodel中的值
        this.fullModel[this.prop] = val;
      },
      //note 给radio加选中的class
      addRadioSelectClass(id) {
        let radioDom = document.getElementById(id);
        if (radioDom && radioDom != null) {
          radioDom.childNodes[0].className += ' ivu-radio-checked';
        }
      },
      //note 给radio删除选中的class
      delRadioSelectClass(id) {
        let radioDom = document.getElementById(id);
        if (radioDom && radioDom != null) {
          radioDom.childNodes[0].className = ' ivu-radio';
        }
      },
      //note 初始化默认地址
      initDefaultAddress() {
        let _this = this;
        if (_this.pickType == "") {
          _this.pickType = "0";
        }

        this.addressListData.map((item, key, arr) => {
          if (item.defaultAddress == 1) {


            _this.yjAddresskId = item.addressId;
            if (_this.pickType == "1") {
              _this.defaultAddress = item.addressId;
            }
            //if(_this.pickType=="1"){
            item.isSelected = true;
            _this.$nextTick(() => {
              if (_this.pickType == "1") {
                _this.addRadioSelectClass(item.addressId);
              }

            })
            // }
          } else {
            item.isSelected = false;
          }
        })
        if (this.fullModel[this.extend.prop] || this.fullModel[this.extend.prop] != "") {
          this.updateDefault(this.fullModel[this.extend.prop]);
          this.defaultAddress = this.fullModel[this.extend.prop];
        }
      },
      //更新默认地址
      updateDefault(id) {
        this.addressListData.map((item, key, arr) => {
          // debugger;
          if (item.addressId == id) {
            item.isSelected = true;
            //note 加个name
            let addressName = item.addressProvinceName + item.addressCityName + item.addressAreaName + item.addressStreetName;
            this.fullModel[this.extend.propName] = addressName;
            this.$nextTick(() => {
              this.addRadioSelectClass(item.addressId);
            })
            this.yjAddresskId = item.addressId;
          } else {
            item.isSelected = false;
            this.$nextTick(() => {
              this.delRadioSelectClass(item.addressId);
            })
          }
        })
        //this.yjAddresskId=id;
        //判断是否是当前类型下的
        this.fullModel[this.extend.prop] = id;

      },
      setAddressListData() {
        Service.getAllAddressData({customerId: this.storeUserInfo.customerNo}).then(res => {
          this.addressListData = res;
          //初始化 默认的地址
          this.initDefaultAddress();
        })

      },
      setDisabled() {

      }
    },
    created() {
      // debugger;
      //给下拉框或单选按钮赋值
      this.yjAddresskId = this.fullModel[this.extend.prop];
      this.selfPickId = this.fullModel[this.extend.prop];
      //设置区域信息
      this.setAddressListData();


    },
    mounted() {
      // debugger;
      if (this.validator === true) {
        this.fullModel.templateCache.validators.push(this);
      }
      this.initDefaultAddress();
      //初始化选择类型
      this.changePickTypeBg(this.fullModel[this.prop]);
      this.pickType = this.fullModel[this.prop];
      if (this.pickType == "0") {
        this.formValidate.yjAddresskId = this.yjAddresskId;
      }
    },
    watch: {

      gender() {

      },

      //监听是自取还是邮寄
      pickType(val, oldVal) {
        this.changePickTypeBg(val);
        console.log('new: %s, old: %s', val, oldVal)
      },
      //监听邮寄里面选中地址的变化
      defaultAddress(val, oldVal) {
        // debugger;
        if (val) {
          //避免初始化赋值时候，这个监听是个undefined
          this.updateDefault(val);
        }
        console.log('new: %s, old: %s', val, oldVal)
      }
    }
  }
</script>

<style scoped lang="less">
  .pickAddress {
    margin-left: 100px;
    /* 当前间距的样式
*/
    .displayB {
      display: block;
    }
    .marginT10 {
      margin-top: 10px;
    }
    .marginR10 {
      margin-right: 10px;
    }
    .marginL10 {
      margin-left: 10px;
    }
    .fn-middle {
      font-size: 14px;
    }
    .fn-small {
      font-size: 12px;
    }
    .displayI {
      display: inline;
    }
    .addressManager {
      text-align: right;
      float: right;
      margin-right: 120px;
    }
  }

  /*
note 自取组件
*/
  .zqDiv {
    position: relative;
    width: 990px;
    margin-left: 30px;
    .hideSelfPick {
      position: absolute;
      bottom: 0px;
      width: 500px;
      background: gray;
      left: 0px;
      top: 0px;
      opacity: 0.1;
    }
  }

  /*
  note 邮寄组件
   */
  .yjDiv {
    .paddingL14 {
      margin-left: 14px;
    }

    position: relative;
    width: 900px;
    margin-left: 30px;
    .defaultClass {
      background: #FEF4E8;
      border-top: 1px solid #FF9933;
      border-bottom: 1px solid #FF9933;
    }
    .addressList {
      width: 863px;
      height: auto;
      border: 1px solid #FF9933;
      position: relative;
      .addressListLi:first-child {
        border-top: 0px !important;
      }
      .addressListLi:last-child {
        border-bottom: 0px !important;
      }

      .addressListLi {
        width: 860px;
        overflow: hidden;
        padding-left: 10px;
        .updateAddress {
          float: right;
          margin-right: 16px;
          color: blue;
        }
      }
      .addressListLi:hover {
        background: #FEF4E8;
      }
      .setDefault {
        color: blue;
        display: none;
      }
      .addressListLi:hover .setDefault {
        display: inline-block;
      }

      /*  .setDefault:hover{
            color:red;
        }*/
    }
    .hideAddressList {
      position: absolute;
      bottom: 0px;
      width: 863px;
      background: gray;
      left: 0px;
      top: 0px;
      z-index: 2;
      opacity: 0.1;

    }
  }


</style>



