<template>
    <div class="guideItem">
      <Steps :current="0" direction="vertical">
        <Step v-for="step,index in guideStepData" :title="step.title" :content="step.content" :key="index"></Step>
      </Steps>
    </div>
</template>

<script>
    /**
     *@author       杨波
     *@date         2018/9/6 11:53
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
      name: "ty-guide-item",
      props : {
        guideStepData: {
          type:Array
        }
      }
    }
</script>



