<template>
  <ty-button-oprator v-bind="demoData" :switchFlag.sync="flag"/>
</template>
<script>
  /**
   *@desc
   *@author  谢辉
   *@date    2019.9.3
   */
  export default {
    data() {
      return {
        flag:false,
        demoData: {
          fix:true,
          buttons: [{label: '下一步', type: 'primary', click: this.nextStepClick}, {
            label: '保存',
            type: 'warning',
            click: this.saveClick
          },{type:'switch'}]
        }
      }
    },
    methods: {
      nextStepClick(e) {
        console.log(e)
      },
      saveClick(e) {
        console.log(e)
      }
    }
  }
</script>



