<template>
    <examples-demo title="标签组件 label" desc="" :code="code.formLabel"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-form-label slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateFormLabel from "./example-page-template-form-label";

  export default {
    name: "example-page-template-all-demo",
    components: {ExamplePageTemplateFormLabel, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns: [
          {
            title: '属性',
            key: 'prop'
          },
          {
            title: '说明',
            key: 'desc'
          },
          {
            title: '类型',
            key: 'type'
          },
          {
            title: '默认值',
            key: 'default'
          }
        ],
        data: [
          {
            prop: 'fullModel',
            desc: '全局绑定对象,',
            type: '*',
            default: '空'
          },
          {
            prop: 'model',
            desc: '无fullModel情况下,给model赋值对象直接绑定model,' +
            '有fullModel情况下,默认$root取fullModel根对象绑定,如果去内部需要设置model,' +
            '例如"a.b.c",就会绑定fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop: 'extend',
            desc: 'form和formItem都存在这一拓展',
            type: 'Object',
            default: '空'
          },
          {
            prop: 'form:labelWidth',
            desc: '设置form表单中每一项label值的宽度',
            type: 'Number',
            default: '空'
          },
          {
            prop: 'form:span',
            desc: '设置form表单中每一项value值的宽度',
            type: 'Number',
            default: '空'
          },
          {
            prop: 'items',
            desc: '所有form-item的一个合集',
            type: 'Array',
            default: '空'
          },
          {
            prop: 'type',
            desc: '标签类型',
            type: 'String',
            default: '空'
          },
          {
            prop: 'prop',
            desc: '双向绑定某项数据，相当于v-model',
            type: 'String',
            default: '空'
          },
          {
            prop: 'extend:linkageChangeValue',
            desc: '改变与此标签联动的值',
            type: 'Array',
            default: '空'
          },
          {
            prop: 'extend:linkageChangeValue:type',
            desc: 'type=out时 触发其他元素数据改变  type=into时 其他元素触发自身元素数据改变',
            type: 'String',
            default: 'out'
          },
          {
            prop: 'extend:linkageChangeValue:changeKey',
            desc: '联动的值',
            type: 'String',
            default: '空'
          },
          {
            prop: 'extend:linkageChangeValue:noChangeExist',
            desc: '需不需要改变changeKey的值',
            type: 'Boolean',
            default: '空'
          },
          {
            prop: 'extend:linkageChangeValue:values',
            desc: '给changeKey的赋值是通过changeParam获取的',
            type: 'Array',
            default: '空'
          }
        ]
      }
    }
  }
</script>



