<template>
    <examples-demo title="单选组件 radio" desc="" :code="code.formRadio"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-form-radio slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateFormRadio from "./example-page-template-form-radio";

  export default {
    name: "example-page-template-form-radio-demo",
    components: {ExamplePageTemplateFormRadio, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns:[
          {
            title: '属性',
            key: 'prop'
          },
          {
            title: '说明',
            key: 'desc'
          },
          {
            title: '类型',
            key: 'type'
          },
          {
            title: '默认值',
            key: 'default'
          }
        ],
        data:[
          {
            prop: 'fullModel',
            desc: '全局绑定对象,',
            type: '*',
            default: '空'
          },
          {
            prop: 'model',
            desc: '无fullModel情况下,给model赋值对象直接绑定model,' +
            '有fullModel情况下,默认$root取fullModel根对象绑定,如果去内部需要设置model,' +
            '例如"a.b.c",就会绑定fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop: 'extend:eventType',
            desc: '通过给eventType一个给定的事件（例如：checkShowHide），从而触发对应的显隐事件',
            type: 'String',
            default: '空'
          },
          {
            prop: 'extend:showFlag',
            desc: '格式为[{"key":"withBalloon","value": 1}],通过获取key从而取到fullModel与之绑定的value值，和value匹配,决定显隐',
            type: 'Array',
            default: '空'
          },

        ]
      }
    }
  }
</script>



