<template>
  <div class="ty-retire-require-state">
    <div :class="`ty-retire-require-state-step-${data.stepState}`">
      {{stepText}}
    </div>
    <div :class="`ty-retire-require-state-info-${data.infoState}`">
      <img class="ty-retire-require-state-ico" :src="infoIco">{{infoText}}
    </div>
  </div>
</template>

<script>
  /**
   *@author       谢辉
   *@date         2018/9/24 23:00
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import ico0 from '../../../../assets/img/ico-retire-info-0.png'
  import ico1 from '../../../../assets/img/ico-retire-info-1.png'

  export default {
    name: "ty-retire-require-state",
    props: {
      data: {
        type: Object,
        default() {
          return {
            stepState: 0,
            infoState: 0
          }
        }
      }
    },
    computed: {
      stepText() {
        switch (this.data.stepState) {
          case 0:
            return '未填写';
          case 1:
            return '待审核';
          case 2:
            return '审核中';
          case 3:
            return '审核驳回';
          default: {
            return '';
          }
        }
      },
      infoText() {
        switch (this.data.infoState) {
          case 0:
            return '资料未完善';
          case 1:
            return '资料完善';
          default: {
            return '';
          }
        }
      },
      infoIco() {
        switch (this.data.infoState) {
          case 0:
            return ico0;
          case 1:
            return ico1;
          default: {
            return '';
          }
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  @tyRetireRequireState:~'ty-retire-require-state';
  .@{tyRetireRequireState} {
    &-ico {
      margin-right: 3px;
      width: 18px;
    }
    &-step{
      &-0{
        color:#3BAB78;
      }
      &-1{
        color:#FF9933;
      }
      &-2{
        color:#999999;
      }
      &-3{
        color:#E84709;
      }
    }
    &-info{
      &-0{
        color:#CC0000;
      }
      &-1{
        color:#2BA738;
      }
    }
  }
</style>



