<template>
  <div class="ListTable">
    <Row>
      <Col :span="col" v-for="(item,index) in prop_data" :key="index">
        <Card>
          <p class="header" slot="title">
            <!--<img :src="item.img">-->
            <img src="/static/img/list-img.png" alt="">
          </p>
          <div class="content">
            <span class="title">{{item.name}}</span>
            <div class="mechanism">
              <span class="s_one">办理机构：</span><span class="s_two">&nbsp;{{item.mechanism || '办理机构'}}</span>
              <span class="s_c">件</span><span class="s_b">&nbsp;{{item.num || 3}}&nbsp;</span><span
              class="s_a">已办理：</span>
            </div>
            <div class="rate">
              <span class="star">评价：</span>
              <Rate :disabled="disabled" :allow-half="allow" :value="item.star || 3"/>
            </div>
            <div class="btnGroup">
              <Button @click="handle(item.id)" class="handle">办事指南</Button>
              <Button @click="consultation" class="consultation">咨询</Button>
              <Button @click="complaint" class="complaint">投诉</Button>
              <span @click="collect">收藏</span>
              <Button @click="onlineHandling(item)" :disabled="item.isOnLine==1 ? false : true"
                      :class="item.isOnLine==1 ? 'onLine' : 'invalid' ">在线办理
              </Button>
              <!--<Button @click="onlineHandling(item.id)"   :class="'onLine' ">在线办理</Button>-->
            </div>
          </div>
        </Card>
      </Col>
    </Row>
    <div class="page">
      <Page show-elevator show-total @on-change="changeNo" @on-page-size-change="changeSize" :page-size="prop_pageSize"
            :current="prop_pageNo" :total="prop_total"/>
    </div>
  </div>
</template>

<script>
  import Service from '../../../src/service/home-procceeds-service'
  import {mapActions} from 'vuex'
  export default {
    name: "list-table",
    props: {
      listData: {
        type: Object
      },
      Data: {
        type: Array
      },
      init: {
        type: Boolean,
        default: false
      },
      span: {
        type: Number,
        default: 4
      },
      pageSize: {
        type: [Number, String],
        default: 10
      },
      pageNo: {
        type: [Number, String],
        default: 1
      },
      total: {
        type: [Number, String],
      },
      allow: {
        type: Boolean,
        default: true
      },
      disabled: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        //note 计算每列分为多少份
        col: _.divide(24, this.span),
        prop_total: _.toNumber(this.total),
        prop_pageSize: _.toNumber(this.pageSize),
        prop_pageNo: _.toNumber(this.pageNo),
        prop_data: this.Data
      }
    },
    methods: {
      ...mapActions([
        'updateProcceed'
      ]),
      query(params) {
        Service.getListData({...params}).then(res => {
          this.prop_pageNo = res.pageNo;
          this.prop_pageSize = res.recordsTotal / res.total;
          this.prop_total = res.recordsTotal;
          this.prop_data = res.rows;
        }).catch(err=>{
          console.log(err)
        })
      },
      changeNo(page) {
        console.log(page);
        this.query({
          pageNo: page
        });
      },
      changeSize(size) {
        this.query({
          pageNo: 1,
          pageSize: size
        });
      },
      //note 办事指南
      handle(id) {
        let routeData = this.$router.resolve({
          name: 'home-hand-guide',
          query: {
            // id:'7DA71DD0C0674DECB2267DCE827FC403'
            id: id,
          }
        });
        window.open(routeData.href, '_blank');
        // this.$router.push({
        //   name: 'home-hand-guide',
        //   query: {
        //     // id:'7DA71DD0C0674DECB2267DCE827FC403'
        //     id: id,
        //
        //   }
        // })
      },
      //note 咨询
      consultation() {
        this.$router.push({
          name: '',
          query: {}
        })
      },
      //note 投诉
      complaint() {
        this.$router.push({
          name: '',
          query: {}
        })
      },
      //note 收藏
      collect() {
        this.$router.push({
          name: '',
          query: {}
        })
      },
      //note 在线办理
      onlineHandling(item) {
        // this.updateProcceed(item);
        this.$router.push({
          name: 'home-procceeds-template-single-index',
          query: {
            projId: '',
            serviceCode: item.id,
            step: 1,
            name:item.name
          }
        })
      }
    },
    mounted() {
      Bus.$on('query', this.query);
    },
    created() {
      if (this.init === true) {
        this.query();
      }

    }

  }
</script>



