// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from 'vue'
import App from './App'
//引入UI组件
import ElementUI from 'element-ui'
import iView from 'iview'
//引入样式文件
import 'element-ui/lib/theme-chalk/index.css'
import '@/styles/index.less'
import './styles/examples.less'
//引入第三方库
import axios from '@/utils/http'
import lodash from 'lodash'
import moment from 'moment'
import accounting from 'accounting'
import lockr from 'lockr'
//引入自定义组件
import CustormComs from '@/components'
//加载自定义工具模块
import appConfig from '@/utils/config'
import util from '@/utils/util'
import Consts from '@/utils/consts'
//引入高亮js
import VueHighlightJS from 'vue-highlight.js';
import 'highlight.js/styles/atelier-heath-light.css';
import router from '@/examples/router'
import ServiceRequest from "../utils/service";
import Bus from "../utils/bus";

/*******************下面模块是可选的************/
Vue.use(VueHighlightJS);

Vue.use(ElementUI);
Vue.use(iView,{
  transfer: true,
  size: 'default'
});
Vue.use(CustormComs);
Vue.config.productionTip = false;
window.axios=Vue.prototype.axios = axios;
window.appConfig=Vue.prototype.appConfig = appConfig;
window.moment=Vue.prototype.moment=moment;
window.accounting=Vue.prototype.accounting=accounting;
window.lockr=Vue.prototype.lockr=lockr;
window.Bus=Vue.prototype.$bus=Bus;
window.util=Vue.prototype.util=util;
window.Consts=Consts;
window._=Vue.prototype._=lodash;
window.axios=axios;
window.$alert=Vue.prototype.$alert;
window.$Modal=Vue.prototype.$Modal;
window.ServiceRequest=ServiceRequest;

/* eslint-disable no-new */
new Vue({
  router,
  template: '<App/>',
  components: {App}
}).$mount('#app')



