<template>
  <div class="input-link">
    <!--<Input v-model.trim="value" :placeholder="extend.placeholder"/>-->
    <label>{{value}}</label>
    <span style="display:none">{{multiply}}</span>
  </div>
</template>

<script>
  /**
   *@author       冯朝晖
   *@date         2018-12-16 16:38
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./../components/content-box/mixin-page-template";
  export default {
    name: 'ty-link-input',
    mixins: [realModel],
    //note 字段在绑定对象中对应的key值
    data() {
      return {
        value: null
      }
    },
    props:{
      prop: {
        type: [String, Array]
      },
      extend: {
        type: Object,
        default() {
          return {}
        }
      },
    },
    computed: {
      multiply() {
        let dependFields = this.extend.dependFields;
        if (!dependFields || !dependFields.length) return;

        var multiplyRes = dependFields.map((item) => {
          return this.realModel[item] || ''
        }).reduce((a, b) => {
          return a * b;
        })
        // str.replace(/^[\d]*(\.[0]*)$/, (x, y) => {return x.replace(y, '')})
        if (String(multiplyRes) === '0') {
          return ''
        } else {
          this.value =  Number(multiplyRes).toFixed(2).replace('.00', '')
        }

        return multiplyRes
      }

    },
    watch: {
      value: function (newVal) {
        this.realModel[this.prop] = newVal;
      }
    }



  }
</script>

<style lang="less" scoped>

</style>



