<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     点击查看地图
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-lookup-map",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: ''
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'lookup-map',
                  data: {
                    title: '点击查看地图组件 type:lookup-map'
                  }
                }
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



