<template>
  <div class="guideContent">
    <Table :data="dataLast" :columns="columns" v-bind="$props" disabled-hover></Table>
  </div>
</template>

<script>
  /**
   *@author       王婷婷
   *@date         2018/9/5 15:09
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  export default {
    name: "guideContent",
    props:{
      columns: {
        type: Array,
        default:[]
      },
      data1: {
        type: Array,
        default:[]
      }
    },
    data () {
      return {
        dataLast:[],
      }
    },
    created() {
      for (let i = 0; i < this.data1.length; i++) {
        if (i % 2 == 0 && (i + 1 < this.data1.length)) {
          this.data1[i].name1 = this.data1[i + 1].name
          this.data1[i].content1 = this.data1[i + 1].content
        }
      }
      this.dataLast = _.filter(this.data1, function(name, index){return index%2 == 0});
    }
  }
</script>




