<template>
  <div class="dynamic-search">
    <Input v-model="value" :placeholder="extend.placeholder" :disabled="extend.disabled" style="width: 150px"/>
    <Select v-model="realModel[prop]" :disabled="extend.disabled" style="width:200px" :label-in-value="true"
            @on-change="changeListener">
      <Option v-for="(item,index) in selectData" :key="index" :value="item.ID">
        {{item.MC}}
      </Option>
    </Select>
  </div>
</template>

<script>
  /**
   *@author       liuwenjun
   *@date         2018/12/8 20:04
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import Service from '../service/component-service'
  import {realModel} from "./content-box/mixin-page-template";

  export default {
    name: "dynamicSearch",
    mixins: [realModel],
    props: {
      prop: {
        type: String
      },
      extend: {
        type: Object
      }
    },
    data() {
      return {
        value: '莲湖区',
        selectData: [
          // {
          //   "xzqhm":"1",
          //   "mc":"食药监所1"
          // },
          // {
          //   "xzqhm":"2",
          //   "mc":"食药监所2"
          // },
          // {
          //   "xzqhm":"3",
          //   "mc":"食药监所3"
          // }
        ]
      }
    },
    methods: {
      textBlur() {
        //认证
        // Service.selectQualificatoryCheck({ID:'0',COMPANYBASEUUID:'-1',QYLX:'7',QYMC:'张三的小餐福安',
        //   DZ:'南窑头东区4排2号'}).then(res=>{
        //       if(res){
        //         this.getSelectData();
        //       }
        // })
      },
      //获取下拉框数据
      getSelectData() {
        Service.getSysOrganizationBaseList({PID: '610001003'}).then(res => {
          let resData = JSON.parse(res.json);
          resData.infos.shift();
          this.selectData = resData.result === 0 ? resData.infos : [];
        })
      },
      changeListener(val) {
        this.realModel.ORGID = val.value;
        this.realModel.ORGMC = val.label;
        this.realModel.JGJG = val.label;
        this.realModel.JGJG_ID = val.value;
        this.realModel.SLJG = val.label;
        this.realModel.SLJG_ID = val.value;
        this.realModel.SLJG_JB = 4;
        if (val.value == "610001003001") {
          this.realModel.FZJG = '莲湖区行政审批服务局';
          this.realModel.FZJG_ID = '610001003001'
        } else {
          this.realModel.FZJG = '莲湖区食品药品监督管理局';
          this.realModel.FZJG_ID = '610001003'
        }
      }
    },
    created() {
      this.getSelectData();
    }
  }
</script>

<style scoped lang="less">
  .dynamic-search {

  }
</style>



