<template>
  <examples-demo title="证件下载扫描 pap-download-scan" desc="" :code="code.papDownloadScan"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-pap-download-scan slot="demo" />
  </examples-demo>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/9/10 14:40
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description  证件下载扫描
     */
    import Code from './code';
    import ExamplesDemo from "../../components/demo";
    import ExamplePageTemplatePapDownloadScan from "./example-page-template-pap-download-scan";
    export default {
        name: "example-page-template-pap-download-scan-demo",
        components: {ExamplePageTemplatePapDownloadScan, ExamplesDemo},
        data() {
          return {
            code: Code,
            columns:[
              {
                title: '属性',
                key: 'prop'
              },
              {
                title: '说明',
                key: 'desc'
              },
              {
                title: '类型',
                key: 'type'
              },
              {
                title: '默认值',
                key: 'default'
              }
            ],
            data:[
              {
                prop: 'fullModel',
                desc: '全局绑定对象,',
                type: '*',
                default: '空'
              },
              {
                prop: 'model',
                desc: '无fullModel情况下,给model赋值对象直接绑定model,' +
                '有fullModel情况下,默认$root取fullModel根对象绑定,如果去内部需要设置model,' +
                '例如"a.b.c",就会绑定fullModel.a.b.c',
                type: 'String,Object',
                default: '$root'
              },
              {
                prop: 'title',
                desc: '证照标题,',
                type: 'String',
                default: '空'
              },
              {
                prop: 'btnName',
                desc: '按钮名称,',
                type: 'String',
                default: '空'
              },
            ]
          }
        }
        }
</script>

<style scoped>

</style>



