</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExampleInstance from "./example-instance";

  /**
   *@desc
   *@author
   *@date
   */
  export default {
    name: "index",
    components: {ExamplesDemo,ExampleInstance},
    data() {
      return {
        code: Code
      }
    }
  }
</script>



