<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     步骤组件
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-step",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '步骤组件'
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'steps',
                  data:
                    {
                      current: 2,
                      steps:
                        [
                          {
                            title: '步骤1'
                          },
                          {
                            title: '步骤2'
                          },
                          {
                            title: '步骤1'
                          },
                          {
                            title: '步骤2'
                          }
                        ]
                    }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



