/**
 *@desc
 *@author  冯朝晖
 *@date    2019.9.3
 */
let Code = {};
Code.base = `<template>
  <ty-page-template :data="pageData"/>
</template>
<script>
/**
 *@desc
 *@author  冯朝晖
 *@date  2018.12.21
 */
export default {
  data() {
    return {
      pageData: {
        //页面基础数据
        page: {
          title: "area link input",
          fullModel: {
            areaMeasure: ""
          }
        },
        //页面元素数据
        elements: [
          {
            type: "box",
            data: [
              {
                type: "form",
                data: {
                  rules: {},
                  //Form扩展配置
                  extend: {
                    labelWidth: 200,
                    span: 12
                  },
                  items: [
                    {
                      type: "linkageInput",
                      label: "面积（平方米）",
                      prop: "areaMeasure",
                      extend: {
                        placeholder: "请输入面积",
                        itemWidth: 322,
                        dependFields: ["width", "length"]
                      }
                    },
                    {
                      type: "input",
                      label: "高（米）",
                      prop: "length",
                      extend: {
                        placeholder: "请输入",
                        maxlength: 10,
                        eventType: "advTipUpdate",
                        itemWidth: 320
                      }
                    },
                    {
                      type: "input",
                      label: "长（米）",
                      prop: "width",
                      extend: {
                        placeholder: "请输入",
                        maxlength: 10,
                        eventType: "advTipUpdate",
                        itemWidth: 320
                      }
                    }
                  ]
                }
              }
            ]
          }
        ]
      }
    };
  }
};
</script>
`;
export default Code;



