<template>
  <div class="calculator">
    <div class="title">2019个税计算器(新税改包含专项)</div>
    <Form ref="form" :model="formItem" :label-width="180" :rules="ruleValidate">
      <FormItem label="城市">
        <Label>{{formItem.city}}</Label>
      </FormItem>
      <FormItem label="月薪" prop="pay">
        <Input v-model="formItem.pay" placeholder="请输入月薪"></Input>
      </FormItem>
      <FormItem label="社保公积金扣除" prop="socialDeduction">
        <Input v-model="formItem.socialDeduction" placeholder="请输入社保公积金个人扣除部分"></Input>
      </FormItem>
      <FormItem label="专项扣除" prop="specialDeduction">
        <Input v-model="formItem.specialDeduction" placeholder="请输入专项扣除总额"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="sumbit">计算</Button>
        <Button style="margin-left: 8px" @click="reset">重置</Button>
      </FormItem>
    </Form>
    <Table :columns="result.columns" :data="result.data"></Table>
    <Table :columns="base.columns" :data="base.data"></Table>
  </div>
</template>

<script>
  /**
   *@author       谢辉
   *@date         2019/2/2 09:49
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   *
   * 具体计算公式如下：
   * 本期应预扣预缴税额=（累计预缴应纳税所得额×税率-速算扣除数）-已预扣预缴税额。
   * 累计预缴应纳税所得额=累计收入-累计免税收入-累计基本减除费用（5000/月×当前月份）-累计专项扣除（累计社保公积金数）-累计专项附加扣除-累计依法确定的其他扣除。
   */
  export default {
    name: "calculator",
    props: {},
    data() {
      return {
        //表单数据
        formItem: {
          city: '北京',
          pay: '',                //月薪
          socialDeduction: '',    //社保公积金扣除
          specialDeduction: '',   //专项扣除
        },
        ruleValidate: {
          pay: [
            {required: true, message: '月薪不能为空', trigger: 'blur'},
            {validator:this.validator, trigger: 'blur'}
          ],
          socialDeduction: [
            {required: true, message: '社保公积金扣除不能为空', trigger: 'blur'},
            {validator:this.validator, trigger: 'blur'}
          ],
          specialDeduction: [
            {required: true, message: '专项扣除不能为空', trigger: 'blur'},
            {validator:this.validator, trigger: 'blur'}
          ]
        },
        formulae: {
          calculator: {
            month: 1,            //当前月份
            pay: 0,                             //月收入
            socialDeduction: 0,     //社保公积金扣除
            specialDeduction: 0,   //专项扣除
            rate: 0,             //税率
            quickDeduction: 0,   //速算扣除
            accumulatedRate: '',   //累计税额扣除,
            taxAmount: 0,                //当月扣税
          },
          //累计预缴应纳税所得额
          taxCount: '(%s-%s-%s-5000)*%s|pay,socialDeduction,specialDeduction,month',
          //当月应交税额
          taxAmount: '(%s-%s-%s-5000)*%s*%s-%s%s|pay,socialDeduction,specialDeduction,month,rate,quickDeduction,accumulatedRate',
          //当月税后所得
          taxIncome: '%s-%s-%s|pay,socialDeduction,taxAmount'
        },
        //累计收入归档数据
        base: {
          //参考税率表表头
          columns: [
            {
              title: '档次',
              key: 'level'
            },
            {
              title: '年应纳税所得额',
              key: 'annualTaxableIncom'
            },
            {
              title: '税率',
              key: 'rate',
              render: (h, {row}) => {
                return h('span', row.rate + "%");
              }
            },
            {
              title: '年扣除数(速算扣除)',
              key: 'quickDeduction'
            }
          ],
          //参考税率表基础数据
          data: [
            {
              level: 1,
              annualTaxableIncom: '不超过36000',
              min: Number.MIN_SAFE_INTEGER,
              max: 36000,
              rate: 3,
              quickDeduction: 0
            },
            {
              level: 2,
              annualTaxableIncom: '36000-144000',
              min: 36001,
              max: 144000,
              rate: 10,
              quickDeduction: 2520
            },
            {
              level: 3,
              annualTaxableIncom: '144000-300000',
              min: 144001,
              max: 300000,
              rate: 20,
              quickDeduction: 16920
            },
            {
              level: 4,
              annualTaxableIncom: '300000-420000',
              min: 300001,
              max: 420000,
              rate: 25,
              quickDeduction: 31920
            },
            {
              level: 5,
              annualTaxableIncom: '420000-660000',
              min: 420001,
              max: 660000,
              rate: 30,
              quickDeduction: 52920
            },
            {
              level: 6,
              annualTaxableIncom: '660000-960000',
              min: 660001,
              max: 960000,
              rate: 35,
              quickDeduction: 85920
            },
            {
              level: 7,
              annualTaxableIncom: '大于960000',
              min: 960000,
              max: Number.MAX_SAFE_INTEGER,
              rate: 45,
              quickDeduction: 181920
            },
          ]
        },
        //个人收入月表
        result: {
          columns: [
            {
              title: '月份',
              key: 'month'
            },
            {
              title: '累计预缴应纳税所得额',
              key: 'taxIncomeAmount',
              render: (h, {row}) => {
                return h('span', accounting.formatMoney(row.taxIncomeAmount, "", 0))
              }
            },
            {
              title: '税率',
              key: 'rate',
              render: (h, {row}) => {
                return h('span', row.rate * 100 + "%");
              }
            },
            {
              title: '税额计算公式',
              key: 'taxAmountStr'
            },
            {
              title: '税额',
              key: 'taxAmount',
              render: (h, {row}) => {
                return h('span', accounting.formatMoney(row.taxAmount, "", 2))
              }
            },
            {
              title: '税后所得',
              key: 'taxIncome',
              render: (h, {row}) => {
                return h('span', accounting.formatMoney(row.taxIncome, "", 2))
              }
            },
          ],
          data: []
        }
      }
    },
    computed: {},
    methods: {
      validator(rule, value, callback) {
        if(rule.field==='pay'){
          if (value&&Number(value)>=5000) {
            callback();
          } else {
            callback(new Error(rule.message || '输入值必须大于5000'));
          }
        }else{
          if (value&&Number(value)>=0) {
            callback();
          } else {
            callback(new Error(rule.message || '输入值必须大于等于0'));
          }
        }

      },
      //note 计算明细
      calculatorCall(month) {
        if (month <= 12) {
          if (month === 1) {
            this.resetCaculator();
          }
          //累计应税收入
          const taxIncomeAmount = this.util.mathFormatStr(this.formulae.calculator, this.formulae.taxCount);
          //档位数据
          let levelData = this.getLevelData(taxIncomeAmount);
          if(!!levelData===false){
            this.util.alert({
              type:"error",
              content:'输入有误!'
            });
            return;
          }
          //获取该档位速算扣除额
          this.formulae.calculator.quickDeduction = levelData.quickDeduction;
          //该档位税率
          const rate = this.formulae.calculator.rate = levelData.rate / 100;
          //当月税额计算公示替换字符后的字符串
          const taxAmountStr = this.util.getMathFormatStr(this.formulae.calculator, this.formulae.taxAmount)
          //当月税额
          const taxAmount = this.formulae.calculator.taxAmount = eval(taxAmountStr);
          //当月税后收入
          const taxIncome = this.util.mathFormatStr(this.formulae.calculator, this.formulae.taxIncome);
          //扣除税额累计字符串
          this.formulae.calculator.accumulatedRate += '-' + taxAmount
          //添加一条月收入明细计算数据
          this.result.data.push(
            {
              month,
              taxIncomeAmount,
              rate,
              taxAmountStr,
              taxAmount,
              taxIncome
            }
          );
          this.formulae.calculator.month++
          this.calculatorCall(this.formulae.calculator.month);
        }
      },
      sumbit(){
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.calculatorCall(1)
          }
        })
      },
      //note 重置计算参数
      resetCaculator() {
        this.result.data = [];
        this.formulae.calculator = {
          month: 1,
          pay: this.formItem.pay,
          socialDeduction: this.formItem.socialDeduction,
          specialDeduction: this.formItem.specialDeduction,
          rate: 0,
          quickDeduction: 0,
          accumulatedRate: '-0',
          taxAmount: 0
        }
      },
      //note 获取档位数据
      getLevelData(money) {
        return _.find(this.base.data, function (o) {
          return money <= o.max && money >= o.min;
        });
      },
      //note 重置
      reset() {
        this.$refs.form.resetFields();
      }
    },
    created() {
      //修改页面标题
      document.title = "2019新税改计算器"
    },
  }
</script>

<style lang="less">
  .calculator {
    width: 1000px;
    margin: 30px auto;
    .title {
      text-align: center;
      width: 100%;
      font-weight: 400;
      font-size: 20px;
      margin-bottom: 10px;
    }
    form {
      width: 500px;
      border: #cccccc 1px solid;
      padding: 0 40px;
      margin: 10px auto;
    }
  }
</style>



