export default class HomeMemberCenterZiLiaoService {

  //note 获取我的资料库信息
  static async getCustDatumData(data){
    return ServiceRequest.post('custDatum/qryCustDatum',data);
  }

  //note 删除单个附件信息
  static async delCustDatumOnly(data){
    return ServiceRequest.post('custDatum/deleteCustDatumById',data);
  }
  //note 批量删除附件信息
  static async delMoreCustDatum(data){
    return ServiceRequest.post('custDatum/batchDeleteCustDatum',data);
  }
}



