<template>
  <ty-steps :steps="steps"></ty-steps>
</template>

<script>
  import TySteps from "../../../components/steps";

  /**
   *@desc
   *@author
   *@date
   */
  export default {
    components: {TySteps},
    data() {
      return {
        steps: [
          {title: '上传文件'},
          {title: '提交审核'},
          {title: '支付缴费'},
          {title: '已办结'}
        ]
      }
    }
  }
</script>



