<template>
  <div class="ty-search">
    <Input autocomplete="on" class="ty-search-input" v-model="params" :placeholder="placeholder"/>
    <Button class="ty-search-button" type="error" icon="ios-search" @click="searchClick">搜索</Button>
  </div>
</template>

<script>
  /**
   *@author       谢辉
   *@date         2018/9/13 12:33
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description  搜索组件
   */
  export default {
    name: "ty-search",
    props: {
      placeholder: {
        default: '搜索服务  资讯  办件编号',
      },
      queryStr: {
        default: ''
      },
      searchHandler: {
        type: Function
      }
    },
    data() {
      return {
        params: this.queryStr
      }
    },
    methods: {
      searchClick() {
        if (this.searchHandler) {
          this.searchHandler.apply(this.params)
        } else {
          this.$router.push({
            name: 'home-procceeds-list',
            query: {
              search: this.params,
            }
          })
        }
      }
    }
  }
</script>



