<template>
  <div class="formTable" :style="styles" v-show="showStatus">
    <Table ref="table" border :columns="checkColumns(columns)" :data="realModel">
      <template slot="input" slot-scope="props">
        <Form :ref="_.uniqueId('formDynamic_')" :model="props.row">
          <FormItem :prop="props.column.key" :rules="checkRules(props.column.rules)" :show-message="true">
            <Input v-model="props.row[props.column.key]" size="small" :maxlength="props.column.maxLength||500" :placeholder="props.column.placeholder"></Input>
          </FormItem>
        </Form>
      </template>

      <template slot="label" slot-scope="props">
        <Form :ref="_.uniqueId('formDynamic_')" :model="props.row">
          <FormItem :prop="props.column.key" :rules="checkRules(props.column.rules)" :show-message="true">
            <Label class="ivu-label">
              {{props.row[props.column.key]}}
            </Label>
          </FormItem>
        </Form>
      </template>

      <template slot="dataRange" slot-scope="props">
        <Form :ref="_.uniqueId('formDynamic_')" :model="props.row">
          <FormItem :prop="props.column.key" :show-message="true" :rules="checkRules(props.column.rules)">
            <DatePicker type="daterange" :options="optionsData"
                        :placeholder="props.column.placeholder"
                        :value="props.row[props.column.key]"
                        @on-change="dataChange"
                        @on-open-change="handleClose(props)"
                        :editable="false">
            </DatePicker>
          </FormItem>
        </Form>
      </template>

      <template slot="singleDate" slot-scope="props">
        <Form :ref="_.uniqueId('formDynamic_')" :model="props.row">
          <FormItem :prop="props.column.key" :show-message="true" :rules="checkRules(props.column.rules)">
            <DatePicker type="date"
                        :placeholder="props.column.placeholder"
                        :value="props.row[props.column.key]"
                        @on-change="dataChange"
                        @on-open-change="handleClose(props)"
                        :editable="false">
            </DatePicker>


          </FormItem>
        </Form>
      </template>
      <template slot="singleMonth" slot-scope="props">
        <Form :ref="_.uniqueId('formDynamic_')" :model="props.row">
          <FormItem :prop="props.column.key" :show-message="true" :rules="checkRules(props.column.rules)">

            <DatePicker type="month" :placeholder="props.column.placeholder"
                        :value="props.row[props.column.key]" @on-change="dataChange"
                        @on-open-change="handleClose(props)"></DatePicker>

          </FormItem>
        </Form>
      </template>
      <template slot="checkbox" slot-scope="props">
        <Form :ref="_.uniqueId('formDynamic_')" :model="props.row">
          <FormItem :prop="props.column.key" :show-message="true" :rules="checkRules(props.column.rules)">

            <Checkbox v-model="props.row[props.column.key]">{{props.column.label}}</Checkbox>


          </FormItem>
        </Form>
      </template>
      <template slot="select" slot-scope="props">
        <Form :ref="_.uniqueId('formDynamic_')" :model="props.row">
          <FormItem :prop="props.column.key" :rules="checkRules(props.column.rules)" :show-message="true">
            <Select v-model="props.row[props.column.key]" clearable style="width:200px">
              <Option v-for="item in props.options" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </FormItem>
        </Form>

      </template>
      <template slot="remoteSelect" slot-scope="props">
        <Form :ref="_.uniqueId('formDynamic_')" :model="props.row">
          <FormItem :prop="props.column.key" :rules="checkRules(props.column.rules)" :show-message="true">
            <ty-remote-select :remoteData="props.column.data" :idx="props.idx" :title="props.column.key"
                              :prop="props.column.prop"></ty-remote-select>
          </FormItem>
        </Form>
      </template>
      <!-- 操作 -->
      <template slot="icoDel" slot-scope="props">
        <Icon v-if="props.idx>=disableDelRows" type="ios-trash-outline" @click="delRow(props.idx)"
              style="font-size: 24px">删除
        </Icon>
      </template>
    </Table>
    <Button @click="addRow" style="margin-top: 10px" v-if="isBtnMsgInner">{{btnMsg}}</Button>
    <div style="display:none;"> {{act}}</div>
  </div>
</template>

<script>
  /**
   *@author       谢辉
   *@date         2018/10/16 02:12
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import formTableRender from './formTableRender'
  import validators from '../../utils/validators'
  import {showStatus} from "../content-box/mixin-showStatus";
  import TyRemoteSelect from "../remoteSelect"

  export default {
    mixins: [showStatus],
    name: "tyFormTable",
    components: {
      TyRemoteSelect
    },
    props: {
      columns: {
        type: Array,
        default() {
          return []
        }
      },
      //note 添加行时候默认添加的数据,包括隐藏字段数据
      appendRowParams: {
        type: Object,
        default() {
          return {}
        }
      },
      //note 添加行时候默认添加的数据,包括隐藏字段数据,数据取自fullModel中的字段值
      appendRowParamsField: {
        type: Object,
        default() {
          return {}
        }
      },
      //note 添加行时候默认添加的数据,包括隐藏字段数据,数据取自随数组带回的第一行的数据
      referFirstValue: {
        type: Object,
        default() {
          return {}
        }
      },
      //note fullModel中的栏位需要form-table中某一列值的总和
      totalValue:{
        type: Object,
        default() {
          return {}
        }
      },
      // note 按钮是否要显示
      isBtnMsg: {
        type: Boolean,
        default: true,
      },
      //note 按钮名称
      btnMsg: {
        type: String
      },
      prop: {
        type: [String, Array]
      },
      //note 是否参与校验
      validator: {
        default: false,
      },
      //note 默认初始行数
      minRows: {
        default: 2,
      },
      //note 不能删除的最少行数
      disableDelRows: {
        default: 2,
      },
      //note 样式 padding
      padding: {
        type: [String, Number],
        default: '50px',
      },
      //note 样式 margin
      margin: {
        type: [String, Number],
        default: 'auto',
      },
      //note 宽度
      width: {
        type: [String, Number],
        default: 'auto',
      },
      // note 表格最大行数
      maxRow: {
        type: [String, Number]
      }
    },
    data() {
      return {
        isBtnMsgInner: this.isBtnMsg, // 是否显示按钮
        act: false, // 用于触发渲染
        flag: true,
        //当前日期前面的日期不可点击
        optionsData: {
          disabledDate(date) {
            return date && date.valueOf() < Date.now() - 86400000;
          }
        },
        tempIdx: 0, // 记录当前数据在表格数据里的下表
        key: null  // 记录对应的字段
      }
    },
    computed: {
      show() {
        if (this.isShow) {
          if (this.fullModel[this.isShow] === true) {
            return true
          } else {
            return false
          }
        }
        return true
      },
      styles() {
        let styles = {};
        styles.margin = `${this.margin}`;
        styles.padding = `${this.padding}`;
        styles.width = `${this.width}px`;
        return styles;
      }
    },
    methods: {
      //note 校验
      formValidator() {
        if(!this.showStatus){
          return true;
        }else{
          let flag = true;
          if (this.$refs instanceof Object) {
            for (let key in this.$refs) {
              if (key.match(/^formDynamic_/) && this.$refs[key]) {
                this.$refs[key].validate(valid => {
                  if (valid) {
                  } else {
                    flag = false;
                  }
                })
              }
            }
          }
          return flag;
        }
      },
      //note 删除行
      delRow(idx) {
        this.realModel.splice(idx, 1);
        if (this.maxRow && this.realModel.length < this.maxRow) {
          this.isBtnMsgInner = true
        }
        this.assignSumTotalValue();
        this.$nextTick(() => {
          this.act = !this.act
        })
      },
      //note 添加行
      addRow() {
        //note key为需要赋值的栏位名称,value为需要取外部fullModel值的栏位名称
        let cloneObj = Object.assign({}, this.appendRowParamsField)
        if(Object.keys(this.appendRowParamsField).length>0){
          for (let key in cloneObj){
            cloneObj[key] = this.fullModel[cloneObj[key]];
          }
        }
        //note key为需要赋值的栏位名称,value为key栏位需要取第一行值的栏位名称
        let cloneDefaultFirst = Object.assign({}, this.referFirstValue)
        if(Object.keys(this.referFirstValue).length>0){
          if(this.realModel.length>0){
            for(let key in cloneDefaultFirst){
              cloneDefaultFirst[key] = this.realModel[0][cloneDefaultFirst[key]];
            }
          }else{
            cloneDefaultFirst = {};
          }
        }
        this.realModel.push({...this.appendRowParams,...cloneObj,...cloneDefaultFirst});
        this.assignSumTotalValue();
        if (this.maxRow && this.realModel.length >= this.maxRow) {
          this.isBtnMsgInner = false
        }
        this.$nextTick(() => {
          this.act = !this.act
        })
      },
      assignSumTotalValue(){
        if(this.realModel<1) return;
        //note key为需要赋值的外部fullModel的栏位名,value为需要加总的form-table中的栏位名
        if(Object.keys(this.totalValue).length>0){
          for (let o of Object.keys(this.totalValue)) {
            this.fullModel[o] = this.sumTotalValue(o);
          }
        }
      },
      sumTotalValue(o){
        let totalArr = this.realModel.map((item)=>{
          return item[this.totalValue[o]];
        });
        let total = totalArr.reduce(function(prev,cur){
          return Number(prev) + Number(cur)||"";
        });
        return total;
      },
      //note 处理列对象
      checkColumns(cols) {
        let result = []
        if (cols instanceof Array && this.$refs.table) {
            cols.forEach(item => {
              result.push({...item, ...{render: formTableRender[item.type](this, this.realModel, this.fullModel, item)}})
            })
        }
        return result
      },
      //note 检测校验规则类型
      checkRules(rules) {
        if (rules instanceof Array) {
          let result = [];
          rules.forEach(item => {
            result.push(this.checkRulesObject(item));
          });
          return result;
        } else if (rules instanceof Object) {
          return this.checkRulesObject(rules);
        } else {
          return rules;
        }
      },
      //note 根据类型校验
      checkRulesObject(obj) {
        if (!obj) {
          return obj;
        }
        switch (obj.type) {
          case 'mobile':
          case 'phone':
          case 'mobilePhone':
          case 'daterange':
          case 'number':
          case 'number5':
          case 'identification':
          case 'identification2':
          case 'checkIdcardList':
          case 'checkAddress':
          case 'area':
          case 'faxx':
          case 'number1':
          case 'workerNum':
          case 'noMoreDay':
          case 'stamp':
          case 'userName':
          case 'notNumber':
          case 'number2':
          case 'socialCode':
          case 'sex':
          case 'numAndLetter':
          case 'realEstateUnit':
            return {
              ...obj, ...{
                required: obj.required,
                validator: validators[obj.type].validator,
                trigger: obj.trigger || 'change',
                message: obj.message
              }
            };
          case 'linkage':
            return {
              ...obj, ...{
                required: obj.required, validator: (rule, value, callback) => {
                  validators[obj.type].validator(rule, value, callback, this.fullModel)
                }, trigger: obj.trigger || 'change', message: obj.message
              }
            };
          case 'custom':
            return {
              ...obj, ...{
                required: obj.required, validator: (rule, value, callback) => {
                  validators[obj.type].validator(rule, value, callback, {modal: this.fullModel})
                }, trigger: obj.trigger || 'change', message: obj.message
              }
            };
          default:
            return obj;
        }
      },
      //note 记录下标和key
      handleClose(props) {
        this.tempIdx = props.idx;
        this.key = props.column.key
      },
      //note 在表格对应位置赋值
      dataChange(value) {
        this.realModel[this.tempIdx][this.key] = value
      }
    },
    created(){
      this.assignSumTotalValue();
    },
    mounted() {
      if (this.validator === true) {
        this.fullModel.templateCache.validators.push(this);
      }

      if (this.realModel instanceof Array) {
        if (this.realModel.length < this.minRows) {
          while (this.realModel.length < this.minRows) {
            this.addRow();
          }
        }
      }
      this.act = !this.act;
    }
  }
</script>

<style lang="less">
  .formTable {

  .ivu-input {
    height: 32px;
    margin-top: 10px;
    margin-bottom: 5px;
  }

  .ivu-label {
    display: inline-block;
    margin-top: 15px;
  }

  .ivu-input-icon {
    margin-top: 10px;
  }

  .ivu-form-item-error-tip {

    background: none;
    line-height: normal;
    padding-left: 0px;
    padding-bottom: 0px;
    width: auto;
    right: 0px;
    top: 37px;
    left: 0px;

  }

  .ivu-select-default {
    width: 150px !important;
  }

  .ivu-input-wrapper-small .ivu-input-icon {
    top: 10px;
    line-height: 32px;
  }

  .ivu-select {
    margin-top: 10px;
  }

  }
</style>



