<template>
  <examples-demo title="广告组件 tip-adv" desc="" :code="code.tipAdv"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-tip-adv slot="demo"/>
  </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateTipAdv from "./example-page-template-tip-adv";

  export default {
    name: "example-page-template-tip-adv-demo",
    components: {ExamplePageTemplateTipAdv, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns: [
          {
            title: '属性',
            key: 'prop'
          },
          {
            title: '说明',
            key: 'desc'
          },
          {
            title: '类型',
            key: 'type'
          },
          {
            title: '默认值',
            key: 'default'
          }
        ],
        data: [
          {
            prop: 'fullModel',
            desc: '全局绑定对象,',
            type: '*',
            default: '空'
          },
          {
            prop: 'model',
            desc: '无fullModel情况下,给model赋值对象直接绑定model,' +
            '有fullModel情况下,默认$root取fullModel根对象绑定,如果去内部需要设置model,' +
            '例如"a.b.c",就会绑定fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop: 'eventType',
            desc: 'bus总线事件名称,针对组件的事件触发,可以自定义事件名称,此组件会在组件初始化后触发',
            type: 'String',
            default: '空'
          },
          {
            prop: 'title',
            desc: '标题,',
            type: 'String',
            default: '空'
          },
          {
            prop: 'feeType',
            desc: '收费类型,',
            type: 'String',
            default: '空'
          },
          {
            prop: 'feeScale',
            desc: '收费标准,',
            type: 'String',
            default: '空'
          },
          {
            prop: 'feeUnit',
            desc: '单位,',
            type: 'String',
            default: '空'
          },
          {
            prop: 'fee',
            desc: '总费用,',
            type: 'String',
            default: '空'
          },
        ]
      }
    }
  }
</script>



