  <div
  :class="[{'fadeIn': showBar}, {'fadeOut': !showBar}, 'quick-info']"> 　　　　
    <ul class="tool-hover">
      <li @click="alertZhi" @mouseenter="changeImg(2)" @mouseleave="hideImg(2)">
        <img src="../assets/svg/icon-zhixiaoxin.svg" v-if="showImgThree"/>
        <img src="../assets/svg/u684.svg" alt="" v-if="showImgThour"/>
        <p class="title">智能客服</p>
      </li>
      <li @mouseenter="changeImg(1)" @mouseleave="hideImg(1)" @click="jump(2)">
        <img src="../assets/svg/icon-complaint.svg" v-if="showImgOne"/>
        <img src="../assets/svg/u701.svg" alt="" v-if="showImgTwo"/>
        <p class="title">我要投诉</p>
        <!--<ul class="tool-hover-show" v-show="active1" :style="[topHeight,leftWidth]">-->
        <!--<li>-->
        <!--<img src="../assets/svg/icon-complaint-tell.svg"/>-->
        <!--<p class="title" @click="jump">我要投诉</p>-->
        <!--</li>-->
        <!--<li>-->
        <!--<img src="../assets/svg/icon-error.svg"/>-->
        <!--<p class="title">我要建议</p>-->
        <!--</li>-->
        <!--<li>-->
        <!--<img src="../assets/svg/icon-common-problem.svg" alt="">-->
        <!--<p class="title">我要咨询</p>-->
        <!--</li>-->
        <!--</ul>-->
      </li>
      <li @click="jump(3)" @mouseenter="changeImg(3)" @mouseleave="hideImg(3)">
        <img src="../assets/img/u711.svg" alt="" v-if="showImgnight"/>
        <img src="../assets/img/u718.svg" alt="" v-if="showImgTen"/>
        <p class="title">我要咨询</p>
      </li>
      <li @click="jump(1)" @mouseenter="changeImg(4)" @mouseleave="hideImg(4)">
        <img src="../assets/svg/u728.png" alt="" v-if="showImgSeven">
        <img src="../assets/svg/u735.png" alt="" v-if="showImgEight">

        <p class="title">我要建议</p>
      </li>
      <!--<li  @mouseover="showItem2" @mouseout="hideItem2">-->
      <!--<img src="../assets/svg/icon-reply.svg"/>-->
      <!--<p class="title">回复</p>-->
      <!--<ul class="tool-hover-show" v-if="this.active2" :style="topHeight">-->
      <!--<li>-->
      <!--<img src="../assets/svg/icon-letter.svg"/>-->
      <!--<p class="title">信件回复</p>-->
      <!--</li>-->
      <!--<li>-->
      <!--<img src="../assets/svg/icon-error.svg"/>-->
      <!--<p class="title">纠错回复</p>-->
      <!--</li>-->
      <!--</ul>-->
      <!--</li>-->
      <!--<li @mouseover="showItem3" @mouseout="hideItem3">-->
      <!--<img src="../assets/svg/icon-mobil.svg"/>-->
      <!--<p class="title">手机端</p>-->
      <!--<ul class="tool-mobil" v-if="this.active3" :style="topHeight">-->
      <!--<li>-->
      <!--<img src="../assets/img/icon-erchat.jpg"/>-->
      <!--<p class="title">扫码登录</p>-->
      <!--</li>-->
      <!--<li>-->
      <!--<img src="../assets/img/icon-erchat.jpg"/>-->
      <!--<p class="title">扫码关注</p>-->
      <!--</li>-->
      <!--<li>-->
      <!--<img src="../assets/img/icon-erchat.jpg"/>-->
      <!--<p class="title">扫码下载</p>-->
      <!--</li>-->
      <!--</ul>-->
      <!--</li>-->
      <!--<li @click="alertQuestion">-->
      <!--<img src="../assets/svg/icon-common-problem.svg"/>-->
      <!--<p  class="title">常见问题</p>-->
      <!--</li>-->
      <li @click="goTop" @mouseenter="changeImg(5)" @mouseleave="hideImg(5)">
        <img src="../assets/svg/u1119.svg" v-if="showImgFive"/>
        <img src="../assets/svg/u748.svg" v-if="showImgSix" alt="">
        <p class="title">返回顶部</p>
      </li>
    </ul>
    <div class="arrow-btn" @click="ctrlBar">
      <Icon type="ios-arrow-forward" v-show='showBar'/>
      <Icon type="ios-arrow-back" v-show="!showBar"/>
    </div>　
  </div>
</template>

<script>

  import {mapGetters} from 'vuex'

  export default {
    name: "ty-quick-info",
    data() {
      return {
        showBar: true,
        showImgTwo: false,
        showImgOne: true,
        showImgFive: true,
        showImgSix: false,
        showImgThree: true,
        showImgThour: false,
        showImgSeven: true,
        showImgEight: false,
        active1: false,
        active2: false,
        active3: false,
        showImgnight: true,
        showImgTen: false,
        topHeight: {
          top: 0,
        },
        css: {
          backgroundColor: ''
        },
        leftWidth: {
          right: 0
        },
        // zIndex:{
        //   zIndex:0
        // },
        timer: ''

      }
    },
    computed: {
      ...mapGetters([
        'storeUserInfo'
      ])
    },
    mounted() {
    },

    methods: {
      ctrlBar() {
        this.showBar = !this.showBar
      },
      ctrlBarByRatio() {
        let { clientHeight, clientWidth } = util.getResolutionRatio();
        if (clientHeight < 768 || clientWidth < 1366) {
          let timer = setTimeout(() => {
            this.ctrlBar()
            clearTimeout(timer);
          }, 3000)
        }
      },
      goTop() {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
      },
      showItem1() {
        // if(this.timer){
        clearTimeout(this.timer);
        this.css.backgroundColor = 'blue';
        this.active1 = true;
        this.topHeight.top = -100 + 'px';
        this.leftWidth.right = 90 + 'px';
        // }

        // this.zIndex.zIndex=10000;
      },
      showChild() {
        this.active1 = true;
      },
      hideItem1() {
        this.timer = setTimeout(() => {
          this.active1 = false;
        }, 1000)

      },
      showItem2() {
        this.active2 = true;
        this.topHeight.top = -50 + 'px';
        this.leftWidth.left = -100 + 'px'
      },
      hideItem2() {
        this.active2 = false;
      },
      showItem3() {
        this.active3 = true;
        this.topHeight.top = -120 + 'px';
      },
      hideItem3() {
        this.active3 = false;
      },
      alertQuestion() {
      },
      alertZhi() {
        window.open(appConfig.customURL)
      },
      //改变图片
      changeImg(num) {
        switch (num) {
          case 1:
            this.showImgOne = false;
            this.showImgTwo = true;
            break;
          case 2:
            this.showImgThree = false;
            this.showImgThour = true;
            break;
          case 3:
            this.showImgnight = false;
            this.showImgTen = true;
            break;
          case 4:
            this.showImgSeven = false;
            this.showImgEight = true;
            break;
          case 5:
            this.showImgFive = false;
            this.showImgSix = true;
            break;
          default:
        }
      },
      hideImg(num) {
        switch (num) {
          case 1:
            this.showImgOne = true;
            this.showImgTwo = false;
            break;
          case 2:
            this.showImgThree = true;
            this.showImgThour = false;
            break;
          case 3:
            this.showImgnight = true;
            this.showImgTen = false;
            break;
          case 4:
            this.showImgSeven = true;
            this.showImgEight = false;
            break;
          case 5:
            this.showImgFive = true;
            this.showImgSix = false;
            break;
          default:

        }
      },
      //跳转路由
      jump(num) {
        localStorage.setItem('prevNum', num)
        // this.prevNum=num;
        if (this.storeUserInfo.isLogin == "0") {
          util.login();
        } else {
          this.$router.push({
            name: 'home-suggestion-suggest-open-list', query: {
              numbers: num
            }
          })
        }
      }
    },
    created() {
      this.ctrlBarByRatio();
    },
    beforeDestroy() {
      // clearTimeout(this.timer)
    }

  }
</script>

<style lang="less" scoped>


</style>





