<template>
  <div class="user-info-box">
    <div class="fl leftInfo">
      <div class="fl pic">
        <div class="photo fl">
          <img src="../assets/img/u242.png" alt="">
        </div>
        <div class="ivu-tooltip-popper ivu-tooltip-light">
          <div class="ivu-tooltip-content" v-if="calevelIdentity">
            <!--<div class="ivu-tooltip-arrow"></div>-->
            <div class="authenticate">{{calevelIdentity}}</div>
          </div>
        </div>
      </div>
      <div class="info">
        <ul class="infoMsg">
          <li style="padding: 3px 0;" :style="{lineHeight:heightLi}">
            <span class="user-name" :title="storeUserInfo.userName">{{storeUserInfo.name}}</span>
            <span style="float: right;font-size: 12px">
               <template  v-if="storeUserInfo.type == 1">(个)</template>
               <template v-if="storeUserInfo.type == 2" >(企)</template>
               <!--<img src="../assets/svg/u11806.svg" alt="" style="width: 16px; height: 16px;margin-right: 8px;margin-top: 4px">-->
               <a @click="jumpRoute">{{identityMsg}}</a>
            </span>
          </li>
          <li style="padding: 3px 0;line-height: 14px;">
            <a style="color: #666666;font-size: 12px;padding-right: 10px" @click="goBack">退出登录</a>
            <a style="color: #666666;font-size: 12px;" @click="goUserMana">用户管理</a>
          </li>
          <li style="padding: 3px 0;">
            <span style="font-size: 12px;color: #999">上次登录时间：</span>
            <span style="font-size: 12px;color: #999">{{userInfo.lastLogTime}}</span>
          </li>
          <li style="padding: 2px 0; line-height: 16px ">
            <template  v-if="storeUserInfo.type == 1">
              <!--<router-link :to="{-->
              <!--name:'home-legal-identiy',-->
              <!--query:{customerNo:storeUserInfo.customerNo,customerName:this.customerName}-->
              <!--}" class="link-href">-->
                <!--<a style="font-size: 12px;color: #3a71c7;">开通法人账号</a>-->
              <!--</router-link>-->
              <!--<a class="link-href" style="font-size: 12px;color: #3a71c7" @click="goOut">切换法人账号</a>-->
            </template>
            <template v-if="storeUserInfo.type == 2" >
              <a style="font-size: 12px;color: #3a71c7;cursor:default" >{{userInfo.companyName}}</a>
            </template>
          </li>
        </ul>
      </div>
    </div>
    <div class="rightEvent">
      <Row class="right-row">
        <Col span="4">
          <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:this.transactNumState}}"  style="display: inline-block">
            <div class="eventIcon">
              <img src="../assets/img/u264.png" alt="" width="36px" height="36px" style="margin-top: 12px">
            </div>
            <!--<p >办理中</p>-->
          </router-link>
          <div style="margin-top: 16px;">
            <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:this.transactNumState}}" style="display: inline-block">
              <a style="margin-top: 8px;color: #666;font-size: 12px;">办理中（{{userInfo.handing}}）</a>
            </router-link>
          </div>
        </Col>
        <Col span="4">
          <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:this.verifyNumState}}"  style="display: inline-block">
            <div class="eventIcon">
              <img src="../assets/img/u274.png" alt="" width="36px" height="36px" style="margin-top: 12px">
            </div>
          </router-link>
          <div style="margin-top: 16px;">
            <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:verifyNumState}}" style="display: inline-block">
              <a style="margin-top: 8px;color: #666;font-size: 12px;">审批中（{{userInfo.checking}}）</a>
            </router-link>
          </div>
        </Col>
        <Col span="4">
          <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:this.supplyNumState}}"  style="display: inline-block">
            <div class="eventIcon">
              <img src="../assets/svg/u254.svg" alt="" width="36px" height="36px" style="margin-top: 12px">
            </div>
            <!--<p style="margin-top: 8px;color:#999999">审核中</p>-->
          </router-link>
          <div style="margin-top: 16px;">
            <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:this.supplyNumState}}">
              <a style="margin-top: 8px;color: #666;font-size: 12px;">补齐补正（{{userInfo.supplyed}}）</a>
            </router-link>
          </div>
        </Col>
        <Col span="4">
          <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:this.payNumState}}"  style="display: inline-block">
            <div class="eventIcon">
              <img src="../assets/img/u284.png" alt="" width="36px" height="36px" style="margin-top: 12px">
            </div>
            <!--<p style="margin-top: 8px;color:#999999">待支付</p>-->
          </router-link>
          <div style="margin-top: 16px;">
            <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:this.payNumState}}">
              <a style="margin-top: 8px;color: #666;font-size: 12px;">等待支付（{{userInfo.needPay}}）</a>
            </router-link>
          </div>
        </Col>
        <Col span="4">
          <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:this.finishNumState}}"  style="display: inline-block">
            <div class="eventIcon">
              <img src="../assets/img/u294.png" alt="" width="36px" height="36px" style="margin-top: 12px">
            </div>
            <!--<p style="margin-top: 8px;color:#999999">已办结</p>-->
          </router-link>
          <div style="margin-top: 16px;">
            <router-link :to="{
            name:'home-member-center-banjian',
            query:{state:this.finishNumState}}">
              <a style="margin-top: 8px;color: #666;font-size: 12px;">已办结（{{userInfo.finishNum}}）</a>
            </router-link>
          </div>
        </Col>
      </Row>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Service from '../service/home-member-center-index-service'

  export default {
    name: 'userInfo',
    props: {
      userData: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    data (){
      return{
        flag:true,
        identity:'0',
        calevelIdentity:'',
        identityMsg:'',
        transactNumState:'',
        verifyNumState:'',
        supplyNumState:'',
        payNumState:'',
        finishNumState:'',
        customerName:'',
        userInfo:{
          handing:'0',
          checking:'0',
          supplyed:'0',
          needPay:'0',
          finishNum:'0',
          lastLogTime:'',
          startTimeStr:'',
          companyName:'',
        }
      }
    },
    computed: {
      heightLi(){
        if(this.storeUserInfo.name&&this.storeUserInfo.name.length>8){
          return 22+'px'
        }
        return 44+'px'
      },
      ...mapGetters([
        'storeUserInfo'
      ])
    },
    methods:{
      goBack(){
        util.logout();
          // window.open(appConfig.logoutUrl, appConfig.logoutTarget);
      },
      // goOut(){
      //   window.open(appConfig.loginUrl(), appConfig.loginTarget);
      // },
      goUserMana(){
        if(this.storeUserInfo.type==='1'){
          this.$router.push({
            name:'home-member-center-personalUser',
            query:{
              isPersonal:true
            }
          })
        }else if(this.storeUserInfo.type==='2'){
          this.$router.push({
            name:'home-member-center-personalUser',
            query:{
              isPersonal:false
            }
          })
        }else if(this.storeUserInfo.type===''){
          this.$router.push({
            name:'home-member-center-personalUser',
            query:{
              isPersonal:true
            }
          })
        }

      },
      jumpRoute(){
        if(this.storeUserInfo.type==='1'){
          this.$router.push({
            name:'home-member-center-identityInfo',
            query:{
              isPersonal:true
            }
          })
        }else  if(this.storeUserInfo.type==='2'){
          this.$router.push({
            name:'home-member-center-identityInfo',
            query:{
              isPersonal:false
            }
          })
        }else if(this.storeUserInfo.type==='') {
          this.$router.push({
            name:'home-member-center-identity',
            query:{
              isPersonal:true
            }
          })
        }

      }
    },
    created() {
      switch(this.storeUserInfo.type){
        case '1':
          this.identityMsg='已实名';
          Service.personalCenter({}).then(data=>{
            this.calevelIdentity=data.qryInfoPerson.caLevel;
            this.transactNumState=data.transactNumState;
            this.verifyNumState=data.verifyNumState;
            this.supplyNumState=data.supplyNumState;
            this.payNumState=data.payNumState;
            this.finishNumState=data.finishNumState;
            this.userInfo.handing=data.transactNum;
            this.userInfo.checking=data.verifyNum;
            console.log(data.verifyNum)
            this.userInfo.supplyed=data.supplyNum;
            this.userInfo.needPay=data.payNum;
            this.userInfo.finishNum=data.finishNum;
            this.userInfo.lastLogTime=data.lastLogTime;
            this.userInfo.startTimeStr=data.startTimeStr;
            this.customerName=data.qryInfoPerson.customerName
          });
          break;
        case '2':
          this.identityMsg='已认证';
          Service.personalCenter({}).then(data=>{
            this.calevelIdentity=data.qryCorporateUnitRspBo.caLevel;
            this.transactNumState=data.transactNumState;
            this.verifyNumState=data.verifyNumState;
            this.supplyNumState=data.supplyNumState;
            this.payNumState=data.payNumState;
            this.finishNumState=data.finishNumState;
            this.userInfo.companyName=data.qryCorporateUnitRspBo.enterpriseName;
            this.userInfo.handing=data.transactNum;
            this.userInfo.checking=data.verifyNum;
            this.userInfo.supplyed=data.supplyNum;
            this.userInfo.needPay=data.payNum;
            this.userInfo.finishNum=data.finishNum;
            this.userInfo.lastLogTime=data.lastLogTime;
            this.userInfo.startTimeStr=data.startTimeStr;
          });
          break;
        case '':
          this.identityMsg='未实名';
          this.flag=false;
          Service.personalCenter({}).then(data=>{
            this.userInfo.lastLogTime=data.lastLogTime;
          });
          break;
      }
    }
  }
</script>



