<template>
  <Card class="ty-steps-h5">
    <Steps v-bind="$props" style="margin-bottom: 20px;">
      <Step v-for="step,index in steps" v-bind="step" :key="index"/>
    </Steps>
  </Card>
</template>

<script>
    export default {
        name: "ty-steps-h5",
        props:{
          current:0,
          steps:{
            type:Array,
            default(){
              return [];
            }
          }
        }
    }
</script>



