  </div>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExampleModalIcon from "./example-modal-icon"
  export default {
    name: "index",
    components: {ExamplesDemo, ExampleModalIcon},
    data() {
      return {
        code: Code
      }
    }
  }
</script>



