<template>
  <div class="previewFileCell">
    <div style="width: 120px;display: inline-block;margin-right: 8px;" v-for="(item,index) in picList">
      <div class="demo-upload-list">
        <img :src="item.enclosureUrl">
        <div class="demo-upload-list-cover">
          <Icon type="ios-eye-outline" class= 'seeIcon' @click.native="handleView(item.enclosureUrl)"></Icon>
          <Icon type="ios-trash-outline" class='delIcon' @click.native="handleRemove(item)" v-if="isDelete"></Icon>
        </div>
      </div>
      <p style="text-align: center;cursor: pointer;" :title="item.enclosureName.length>9? item.enclosureName:''">{{item.enclosureName.length <= 9? item.enclosureName : item.enclosureName.slice(0,9) +'...' }}</p>
      <p style="text-align: center; color: #4a8adc;cursor: pointer" @click="handleDowload(item)">下载</p>
    </div>
    <Row v-for="(item,index) in fileList" :key="index+1">
      <Col span="18">
        <p :title="item.enclosureName.length>46? item.enclosureName:''"
           style="marginTop:'8px';color: rgb(102, 102, 102);text-align: left; cursor: pointer;">
          <img src="../../assets/svg/p0.svg" style="width: 16px;vertical-align: text-bottom"
               v-if="item.enclosureUrl.split('.')[item.enclosureUrl.split('.').length -1].toLowerCase() == 'doc'||item.enclosureUrl.split('.')[item.enclosureUrl.split('.').length -1].toLowerCase() == 'docx'">
          <img src="../../assets/svg/p1.svg" style="width: 16px;vertical-align: text-bottom"
               v-if="item.enclosureUrl.split('.')[item.enclosureUrl.split('.').length -1].toLowerCase() == 'xls'||item.enclosureUrl.split('.')[item.enclosureUrl.split('.').length -1].toLowerCase() == 'xlsx'">
          <img src="../../assets/svg/p2.svg" style="width: 16px;vertical-align: text-bottom"
               v-if="item.enclosureUrl.split('.')[item.enclosureUrl.split('.').length -1].toLowerCase() == 'pdf'">
          {{item.enclosureName.length>46?item.enclosureName.slice(0,46)+'...':item.enclosureName}}
        </p>
      </Col>
      <Col span="6" style="text-align: right">
        <span style="color: #4a8adc; margin-right: 10px;cursor: pointer" @click="handleDowload(item)"> 下载</span>
        <span style="color:#CC0000; margin-right: 10px;cursor: pointer" @click="handleRemove(item)" v-if="isDelete"> 删除</span>
      </Col>
    </Row>
    <div ref="images" v-viewer.static="{navbar: 0,title: funTitle}" v-show="false">
      <img :src="imgUrl" width="500px">
    </div>
  </div>
</template>

<script>
  /**
   *@author       杨波
   *@date         2018/11/21 19:50
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import Service from "../../service/home-member-center-ziliao";
  import Config from "../../utils/config"

  export default {
    name: "ziliaoCell",
    props: {
      fileData: {
        type: Array,
        default() {
          return []
        }
      },
      isDelete: {
        type: Boolean,
        default: true
      }
    },
    components: {},
    data() {
      return {
        picList: [],
        fileList: [],
        imgUrl: '',
      }
    },
    computed: {},
    watch: {},
    methods: {
      handleView(name) {
        this.imgUrl = name;
        const viewer = this.$refs.images.$viewer;
        viewer.show()
      },
      handleRemove(data) {
        Service.delCustDatumOnly({datumId: data.datumId, operSys: '0'}).then(res => {
            let fileLength = data.enclosureUrl.split('.');
            const ext = fileLength[fileLength.length - 1];
            if (ext.toLowerCase() == 'png' || ext.toLowerCase() == 'jpg' || ext.toLowerCase() == 'jpeg') {
              this.picList.splice(this.picList.indexOf(data), 1);
            } else {
              this.fileList.splice(this.fileList.indexOf(data), 1);
            }
            this.fileData.splice(this.fileData.indexOf(data), 1);
            if (this.fileData.length == 0) {
              this.$emit('delOneData')
            }
            util.alert({type: 'success', content: '删除资料成功'})
          }
        )
      },
      handleDowload(data) {
        let name = encodeURI(data.enclosureName);
        let url = encodeURI(data.enclosureUrl);

        window.open(encodeURI(Config.api('file/dowloadFile?fileName=' + url + '&realfileName=' + name)))
      },
      funTitle(a) {
        let arr = a.src.split('/');
        let length = arr.length;
        return decodeURI(arr[length - 1]);
      },
    },
    created() {
      for (let i in this.fileData) {
        let fileLength = this.fileData[i].enclosureUrl.split('.');
        const ext = fileLength[fileLength.length - 1];
        if (ext.toLowerCase() == 'png' || ext.toLowerCase() == 'jpg' || ext.toLowerCase() == 'jpeg') {
          this.picList.push(this.fileData[i])
        } else {
          this.fileList.push(this.fileData[i])
        }
      }
    },
    mounted() {

    }
  }
</script>

<style lang="less">
  .previewFileCell {
    text-align: left;
    padding: 10px 0;
  }

  .demo-upload-list {
    display: inline-block;
    width: 120px;
    height: 120px;
    text-align: center;
    line-height: 120px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
  }

  .demo-upload-list img {
    width: 100%;
    height: 100%;
  }

  .demo-upload-list-cover {
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, .6);
  }

  .demo-upload-list:hover .demo-upload-list-cover {
    display: block;
  }

  .demo-upload-list-cover i {
    color: #999;
    font-size: 24px;
    cursor: pointer;
    margin: 0 2px;
  }

  .demo-upload-list-cover i.seeIcon {
    font-size: 40px;
  }
  .demo-upload-list-cover i.delIcon {
    position: absolute;
    left: 4px;
    top: 4px;
  }

  .demo-upload-list-cover i:first-child:hover,
  .demo-upload-list-cover i:last-child:hover {
    color: #fff;
  }
</style>



