let Code = {};
Code.base = `<template>
  <ty-button-oprator v-bind="demoData"/>
</template>
<script>
  /**
   *@desc
   *@author  谢辉
   *@date    2019.9.3
   */
  export default {
    data() {
      return {
        demoData: {
          buttons: [{label: '下一步', type: 'error', click: this.nextStepClick}, {
            label: '保存',
            type: 'warning',
            click: this.saveClick
          }]
        }
      }
    },
    methods: {
      nextStepClick(e) {
        console.log(e)
      },
      saveClick(e) {
        console.log(e)
      }
    }
  }
</script>
`;
export default Code;



