<template>
  <div class="identity">
    <div v-if="isPersonal== 'true'">
      <Row class="identityRow">
        <Col span="10" class="label">证件类型：</Col>
        <Col span="14" class="content">{{identityList.idTypeDesc}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">证件号码：</Col>
        <Col span="14" class="content">{{identityList.idNumber}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">姓名：</Col>
        <Col span="14" class="content">{{identityList.name}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">性别：</Col>
        <Col span="14" class="content">{{identityList.sexFlag}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">民族：</Col>
        <Col span="14" class="content">{{identityList.nation}}</Col>
      </Row>
      <!--<Row class="identityRow">-->
      <!--<Col span="10" class="label">自然人状态：</Col>-->
      <!--<Col span="12" class="content">{{this.identityList.statusDesc}}</Col>-->
      <!--</Row>-->
      <Row class="identityRow">
        <Col span="10" class="label">出生日期：</Col>
        <Col span="14" class="content">{{identityList.birthday}}</Col>
      </Row>
    </div>
    <div v-else-if="isPersonal=='false'">
      <Row class="identityRow">
        <Col span="10" class="label">企业类型：</Col>
        <Col span="14" class="content">{{identityList.legalTypeDesc}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">统一社会信用代码：</Col>
        <Col span="14" class="content">{{identityList.licenseNo}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">法人名称：</Col>
        <Col span="14" class="content">{{identityList.enterpriseName}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">法定代表人姓名：</Col>
        <Col span="14" class="content">{{identityList.enterpriseCorporate}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">法定代表人身份证号码：</Col>
        <Col span="14" class="content">{{identityList.legalIdNumber}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">法定代表人联系电话：</Col>
        <Col span="14" class="content">{{identityList.telePhone}}</Col>
      </Row>
      <Row class="identityRow">
        <Col span="10" class="label">注册地址：</Col>
        <Col span="14" class="content">{{identityList.areaId}}</Col>
      </Row>
    </div>

  </div>

</template>

<script>
  import Service from '../service/home-member-center-verification'

  /**
   *@author       王婷婷
   *@date         2018/9/19 17:55
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  export default {
    name: "ty-identity",
    props:{
      ob:{
        type:Object
      },

    },
    data(){
      return {
        isPersonal:'',
        identityList:{
          idNumber:'',
          idType:'',
          name:'',
          sexFlag:'',
          nation:'',
          birthday:'',
          status:'',
          idTypeDesc:'',
          statusDesc:'',
          enterpriseCorporate:'',
          licenseNo:'',
          legalIdNumber:'',
          telePhone:'',
          legalTypeDesc:'',
          areaId:'',
          enterpriseName:''
        }
      }
    },
    methods: {
      getPersonIQuery() {
        Service.getPersonIQuery({}).then(data => {
          //自然人
          this.identityList.idNumber=data.qryInfoPerson.idNumber;
          this.identityList.idTypeDesc=data.idTypeDesc;
          this.identityList.name=data.qryInfoPerson.customerName;
          if(data.qryInfoPerson.sexFlag=='2'){
            this.identityList.sexFlag='女'
          }else if(data.qryInfoPerson.sexFlag=='1') {
            this.identityList.sexFlag='男'
          }
          this.identityList.nation=data.qryInfoPerson.nation;
          if(data.qryInfoPerson.birthday!==null){
            const time=data.qryInfoPerson.birthday;
            this.identityList.birthday=time.split(" ")[0];
          }else {
            this.identityList.birthday=data.qryInfoPerson.birthday;
          }
          this.identityList.statusDesc=data.statusDesc;
          // 法人
          this.identityList.enterpriseCorporate=data.qryCorporateUnitRspBo.enterpriseCorporate;
          this.identityList.licenseNo=data.qryCorporateUnitRspBo.licenseNo;
          this.identityList.legalIdNumber=data.qryCorporateUnitRspBo.legalIdNumber;
          this.identityList.telePhone=data.qryCorporateUnitRspBo.telePhone;
          this.identityList.legalTypeDesc=data.legalTypeDesc;
          this.identityList.areaId=data.qryCorporateUnitRspBo.areaId;
          this.identityList.enterpriseName=data.qryCorporateUnitRspBo.enterpriseName;
        })
      }
    },
    created(){
      this.isPersonal = this.$route.query.isPersonal.toString();
      this.getPersonIQuery();
    }
  }
</script>

<style scoped>

</style>



