<template>
  <div class="licenceBox" v-bind="realModel" v-if="realState">
    <div class="picBox fl">
      <img src="../assets/img/u4329.svg">
    </div>
    <div class="licenceContent fl">
      <p>{{title}}</p>
      <!--<p class="validTime">证照有效期：{{realModel.expiryDate}}</p>-->
      <!--<p class="detail">{{detail}}</p>-->
      <p v-if="date" class="validTime">{{date}}</p>
      <p class="detail">{{text}}</p>
      <p v-if="isYiBao && yibaohao">电子医保号：{{yibaohao}}</p>
      <Button class="bt1" style="float: left;" @click="downLoad(fullModel.fileUrlZz)">{{btnName}}</Button>
      <!--<Button class="send" id="suggestUploadId" @click="upload(fullModel.fileUrlZz)">{{btnName1}}</Button>-->
      <template v-if="btnShow">
        <Uploads style="margin: 9px 0 0 10px;float: left;" uploadContent="上传合同" uploadId="suggestUploadId" picNum=1
                 isContainService="2" upType="pdf" :param="params"></Uploads>
      </template>
    </div>
  </div>
</template>
<script>
  import Service from "../service/single-index-service.js"
  import Config from "../utils/config.js"

  export default {
    name: "ty-pap-download-scan",
    props: {
      title: {
        type: String
      },
      btnName: {
        type: String
      },
      btnName1: {
        type: String,
        default: "上传已签生成合同"
      },
      btnShow: {
        type: Boolean,
        default: false
      },
      detailWarn: {
        type: String
      },
      detail: {
        type: String
      },
      text: {
        type: String
      },
      date: {
        type: String
      },
      //note 全量绑定数据传递
      fullModel: {
        type: [Object, Array],
      },
      //note 绑定数据参数,默认$root,绑定fullModel,可自行设定绑定参数,
      //比如设置 second则绑定fullModel.second对象
      model: {
        type: [String, Object],
        default: '$root',
      },
      isYiBao: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        modal1: false,
        downUrl: this.url,
        formData: {},
        defaultList: [],
        params: {
          attrId: "-1",
          type: "1",
          serviceCode: this.fullModel.serviceCode,
          projId: this.fullModel.projId,
          operNo: this.$store.state.common.userInfo.userId,
          operSys: "0",
          enclosureEnumType: "1"
        },
        yibaohao: null
      }
    },
    computed: {
      realState() {
        if (this.fullModel.state != 5 || this.fullModel.fileUrlZz == undefined) {
          return false
        } else {
          return true
        }
      },
      //note 最终真是绑定对象获取
      realModel() {
        if (this.fullModel === undefined) {
          return {};
        }
        if (typeof(this.model) === 'string') {
          if (this.model === '$root') {
            return this.fullModel;
          }
          let params = this.model.split('.');
          let result = this.fullModel;
          while (params.length > 0) {
            result = result[params.shift()];
          }
          return result;
        } else if (typeof(this.model) === 'object') {
          return this.model;
        } else if (typeof(this.model) === 'array') {
          return this.model;
        }
        return {};
      }
    },
    methods: {
      downLoad(url) {
        console.log(url);
        if (url instanceof Array) {
          for (let item of url) {
            let url = encodeURI(item.fileUrl);
            let attrName = encodeURI(item.attachmentName);
            window.open(encodeURI(Config.api('file/dowloadFile?fileName=' + url + '&realfileName=' + attrName)))
          }
        }
      },
      upload(url) {
        console.log(url);
      }
    },
    created() {
      if (this.isYiBao) {
        Service.queryHealthNo({applyNo: this.fullModel.projId}).then((res) => {
          if (res && res.length) {
            this.yibaohao = res[0].acceptValue
          }
        })
      }
    }
  }
</script>



