<template>
  <div>
    <TableFixed v-bind="fixedData" style="width: 1200px"></TableFixed>
  </div>
</template>
<script>
    /**
     *@author       王婷婷
     *@date         2018/9/17 14:38
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    import TableFixed from '../../../components/table-fixed'
    export default {
      name: "example-table-fixed",
      components: {TableFixed},
      data(){
        return {
          fixedData: {
            columns: [
              {
                title: '材料名称',
                key: 'materialName',
                align:'center',
                fixed: 'left',
                width:200,
                render: (h, params) => {
                  if(params.row.type=='fee'){
                    return h('div', [
                      h('P',params.row.materialName),
                      // h('TyContentElementLookupFee')
                    ]);
                  }else if(params.row.type=='downLoad'){
                    return h('div', [
                      h('P',params.row.materialName),
                      h('Tag', {
                        props: {
                          color:'primary',
                        }
                      }, '下载申请表格'),
                    ]);
                  }

                }
              },
              {
                title: '材料类型',
                key: 'materialType',
                align:'center',
                width:200
              },
              {
                title: '来源渠道',
                key: 'sourceChannel',
                align:'center',
                width:200,
                render:(h,params)=>{
                  return h('div', [
                    h('P',params.row.sourceChannel),
                    h('TyContentElementLookupMap')
                  ]);
               }
              },
              {
                title: '纸质材料份数和规格',
                key: 'materialPaper',
                align:'center',
                width:200
              },
              {
                title: '填报须知',
                key: 'notification',
                align:'center',
                width:200
              },
              {
                title: '受理标准',
                key: 'admissibilityStandard',
                align:'center',
                width:200
              },
              {
                title: '是否需电子材料',
                key: 'isEleMaterials',
                align:'center',
                width:200
              },
              {
                title: '备注',
                key: 'remark',
                align:'center',
                width:200
              },
            ],
            data: [
              {
                materialName: '已签字盖章的申请表',
                materialType: 'bmp、gif、png、jpg、jpeg',
                sourceChannel: '申请单位名称',
                materialPaper: '1份（A4）',
                notification: '申请材料填写信息时需注意内容',
                admissibilityStandard:'提示上传申请材料审批人员审核标准',
                isEleMaterials:'是',
                remark:'备注信息',
                type:'downLoad'
              },
              {
                materialName: '企业营业执照',
                materialType: 'bmp、gif、png、jpg、jpeg',
                sourceChannel: '申请单位名称',
                materialPaper: '1份（A4）',
                notification: '申请材料填写信息时需注意内容',
                admissibilityStandard:'提示上传申请材料审批人员审核标准',
                isEleMaterials:'是',
                remark:'备注信息',
                type:'fee'
              },
            ]
          },
        }
      }
    }
</script>

<style scoped>

</style>



