<template>
  <div class="guideInfo clearfix" v-bind="$props">
    <div class="guideContent fl">
      <p><<{{title}}>>办事指南</p>
      <Button  v-for="(item,index) in btnList" :key="index" :type="item.type" ghost @click="transferClick(item.value)">{{item.label}}</Button>
    </div>
    <div class="ewmBox fr">
      <img :src="qrCode">
    </div>
  </div>
</template>
<script>
    export default {
        name: "guideInfo",
        props:{
            obj:{
              type:Object
            },
            qrCode:{
              type:String
            },
            title:{
              type:String
            },
            btnList:{
              type:Array
            }

        },
        data(){
          return {
            formData:{

            }
          }
        },
        methods:{
          transferClick(data){
            if(data===1){
              this.$router.push({
                name: 'example-extend-table'
              })
            }else if(data==2){
              this.$router.push({
                name: 'example-enclosure-info'
              })
            }else if(data===3){
              this.$router.push({
                name: 'example-button-oprator'
              })
            }


          }


        }
    }
</script>



