<template>
  <div class="payment-way" v-bind="$props" >
    <h3 style="font-size: 18px">{{chosePayWay}}</h3>
    <ul class="fix paymentStyle">
      <li class="fl" :class="(index===alipayList.length-1)?'bank':'alipay'" v-for="(item,index) in alipayList" :key="index">
        <a class="pay">
          <!--<img class="pay-img" src="../assets/img/alipay-money.png"/>-->
          <img  class="pay-img" :src="item.imgUrl"/>
          <span class="pay-span" v-if="index!==alipayList.length-1">{{item.title}}</span>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
      name: "ty-payment-way",
      props:{
        chosePayWay:{
          type:String
        },
        showPay:{
          type:Boolean,
          default:true
        },
        alipayList:{
          type:Array,
          default(){
            return []
          }
        }
      },
      data(){
        return {

        }
      },
      methods:{},
      created(){}
    }
</script>



