<template>
  <examples-demo title="附件信息组件 info-attachment" desc="" :code="code.infoAttachment"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-info-attachment slot="demo" />
  </examples-demo>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/9/10 14:21
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description  附件信息
     */
    import Code from './code';
    import ExamplesDemo from "../../components/demo";
    import ExamplePageTemplateInfoAttachment from "./example-page-template-info-attachment";
    export default {
        name: "example-page-template-info-attachment-demo",
        components: {ExamplePageTemplateInfoAttachment, ExamplesDemo},
        data() {
          return {
            code: Code,
            columns:[
              {
                title: '属性',
                key: 'prop'
              },
              {
                title: '说明',
                key: 'desc'
              },
              {
                title: '类型',
                key: 'type'
              },
              {
                title: '默认值',
                key: 'default'
              }
            ],
            data:[
              {
                prop: 'fullModel',
                desc: '全局绑定对象,',
                type: '*',
                default: '空'
              },
              {
                prop: 'model',
                desc: '无fullModel情况下,给model赋值对象直接绑定model,' +
                '有fullModel情况下,默认$root取fullModel根对象绑定,如果去内部需要设置model,' +
                '例如"a.b.c",就会绑定fullModel.a.b.c',
                type: 'String,Object',
                default: '$root'
              },
              {
                prop: 'headerTitle',
                desc: '标题名称',
                type: 'String',
                default: '空'
              },
              {
                prop: 'showHeader',
                desc: '是否显示标题名称,',
                type: 'Boolean',
                default: 'false'
              }
            ]
          }
        }
    }
</script>

<style scoped>

</style>



