<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
  /**
   *@desc     单选
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-form-radio",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '单选',
            fullModel:{
              normalRadio: 'female',
              withBalloon:1,
              celeCompName:'气球'
            }
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //双向绑定数据对象
                    // model: {
                    //
                    // },
                    //规则
                    rules: {

                    },
                    //Form扩展配置
                    extend: {
                      labelWidth: 300,
                      span: 15,
                    },
                    items:
                      [
                        {
                          type: 'radio',
                          label: '单选 type:radio',
                          prop: 'normalRadio',
                          extend: {},
                          items: [
                            {
                              label: '男',
                              value: 'male',
                              extend: {
                                size: 'large',
                                disabled: true,
                              }
                            },
                            {
                              label: '女',
                              value: 'female'
                            }
                          ]
                        },
                        {
                          type: "radio",
                          label: "是否施放新型空气气球",
                          prop: "withBalloon",
                          items: [
                            {
                              label: "是",
                              value: 1
                            },
                            {
                              label: "否",
                              value: 0
                            }
                          ],
                          extend: {
                            itemWidth: 83,
                            eventType: "checkShowHide"
                          }
                        },
                        {
                          type: "input",
                          label: "庆典公司名称",
                          prop: "celeCompName",
                          extend: {
                            placeholder: "请输入庆典公司名称",
                            itemWidth: 320,
                            showFlag: [
                              {
                                key: "withBalloon",
                                value: 1
                              }
                            ]
                          }
                        }
                      ]
                  }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



