<template>
  <div class="ty-steps">
    <Steps v-bind="$props" style="margin-bottom: 20px;">
      <Step v-for="step,index in steps" v-bind="step" :key="index"/>
    </Steps>
  </div>
</template>

<script>
    export default {
        name: "ty-steps",
        props:{
          current:0,
          steps:{
            type:Array,
            default(){
              return [];
            }
          }
        }
    }
</script>



