<template>
      <div class="handleGuideBaseInfo">
        <template v-if="flag===1">
          <Row class="row-border">
            <Col v-for="(item,index) in baseInfoData.data" :key="index" :span="item.span?item.span:24/baseInfoData.col">
              <div :class="item.span==24?'cell-item2':'cell-item1'">
                <span>{{item.title}}</span>
                <p>
                  <template v-if="item.prop" >
                      {{baseData[item.prop]}}
                  </template>
                  <template v-else >
                      {{item.content}}
                  </template>
                  <template v-if="item.extend">
                    <ty-content-element-lookup-map v-if="item.extend.type==='lookup-map'"/>
                    <ty-content-element-lookup-fee v-if="item.extend.type==='lookup-fee'"/>
                    <ty-content-element-lookup-sample :imgList="item.extend.demoImg" v-if="item.extend.type==='lookup-sample'"/>
                  </template>
                </p>
              </div>
            </Col>
          </Row>
        </template>
        <template v-else>
          <div v-if="baseData">
            <p v-if="!(baseData.length>0)">无</p>
          </div>
          <Row v-for="(content,index) in baseData"  :key="index" >
            <Col v-for="(item,index) in baseInfoData.data" :key="index" :span="item.span?item.span:24/baseInfoData.col">
              <div :class="item.span==24?'cell-item2':'cell-item1'">
                <span>{{item.title}}</span>
                <p>
                  <template v-if="item.prop">
                    {{content[item.prop]}}
                  </template>
                  <template v-else>
                    {{item.content}}
                  </template>
                  <template v-if="item.extend">
                    <ty-content-element-lookup-map v-if="item.extend.type==='lookup-map'"/>
                    <ty-content-element-lookup-fee v-if="item.extend.type==='lookup-fee'"/>
                    <ty-content-element-lookup-sample :imgList="item.extend.demoImg" v-if="item.extend.type==='lookup-sample'"/>
                  </template>
                </p>
              </div>
            </Col>
          </Row>
        </template>
      </div>
</template>
<script>
    /**
     *@author       刘文军
     *@date         2018/9/14 10:05
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    import TyContentElementLookupMap from "./content-box/content-elements/content-element-lookup/content-element-lookup-map";
    import TyContentElementLookupFee from "./content-box/content-elements/content-element-lookup/content-element-lookup-fee";
    import TyContentElementLookupSample from "./content-box/content-elements/content-element-lookup/content-element-lookup-sample";
    export default {
        name: "hanlde-guide-base-info",
        components:{
          TyContentElementLookupMap,
          TyContentElementLookupFee,
          TyContentElementLookupSample
        },
        props:{
          flag:{
            type:Number
          },
          baseData:{
            type:[Object, Array]
          },
          baseInfoData:{
            type:Object,
            default(){
              return {}
            }
          }
        },
      created(){

      }
    }
</script>




