                    "feeUnit": "元/平方米(年)"
                  },
                  {
                    "label": "电子显示屏广告",
                    "value": 2,
                    "feeType": "电子显示屏广告",
                    "feeExpression": "%s*%s|feeScale,amount",
                    "feeScale": 7000,
                    "feeUnit": "元/年"
                  }
                ],
                "tipAdvData": {
                  "amount": 0,
