                          },
                          {
                            title: '步骤2'
                          },
                          {
                            title: '步骤1'
                          },
                          {
                            title: '步骤2'
                          }
                        ]
                    }
                },
                {
                  type: 'title',
                  data: {
                    title: '表单组件 type:form'
                  }
                },
                {
                  type: 'form',
                  data: {
                    //双向绑定数据对象
