                      normalLabel: '文本框',
                      normalInput: '输入框',
                      normalRadio: 'male',
                      normalDate:[],
                      startDate:'',
                      endDate:'',
                      normalSelect:''
                    },
                    //规则
                    rules: {
                      normalInput: [
                        {required: true, message: 'Please fill in the user name', trigger: 'blur'}
                      ],
                      // normalDate: [
                      //   {required: true, message: 'Please fill in the password.', trigger: 'blur'},
                      //   {
                      //     type: 'string',
                      //     min: 6,
                      //     message: 'The password length cannot be less than 6 bits',
                      //     trigger: 'blur'
                      //   }
                      // ]
                    },
                    //Form扩展配置
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'label',
                          label: '普通文本框 type:label',
                          prop: 'normalLabel',
