<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     广告-提示
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-tip-adv",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            fullModel:{
              "templateCache": {
              "validators": [],
                "advList": [
                  {
                    "label": "楼体(立面)广告",
                    "value": 1,
                    "feeType": "楼体(立面)广告",
                    "feeExpression": "%s*%s*%s*%s|width,length,feeScale,amount",
                    "feeScale": 110,
                    "feeUnit": "元/平方米(年)"
                  },
                  {
                    "label": "电子显示屏广告",
                    "value": 2,
                    "feeType": "电子显示屏广告",
                    "feeExpression": "%s*%s|feeScale,amount",
                    "feeScale": 7000,
                    "feeUnit": "元/年"
                  }
                ],
              "tipAdvData": {
                "amount": 0,
                "width": 0,
                "height": 0,
                "feeExpression": "",
                "feeType": "",
                "feeScale": 0,
                "feeUnit": "元/年"
              }
            }
            }
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  "type": "tip-adv",
                  "data": {
                    "model": "templateCache.tipAdvData",
                    "eventType": "advTipUpdate",
                    "title": "温馨提示"
                  }
                }
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



