<template>
    <div class="essentialInfo" v-if="showStatus">
        <div class="cell" v-for="(item,index) in infoData" :key="index">
            <div class="cell-title" v-if="showTitle">
              <p>
                <Icon type="md-document" />
                <span>{{item.title}}</span>
              </p>
            </div>
            <div class="cell-content" >
                <Row>
                  <Col v-for="(item1,index1) in item.data" :key="index1" span="8">
                    <span :class="{ active: item1.isModify===true }">{{item1.title}}：</span>
                    <span :class="{ active: item1.isModify===true }">{{getPropVal(item1.prop,item1.label,item1.itemProp)}}</span>
                  </Col>
                </Row>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/12/10 11:33
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    import {showStatus} from "./content-box/mixin-showStatus";
    export default {
        name: "essentialInfo",
        mixins: [showStatus],
        props:{
          infoData:{
            type:Array
          },
          isShow:{
            type:String
          },
          showTitle:{
            type:Boolean,
            default:true
          }
        },
        data(){
          return {

          }
        },
        computed:{

        },
        methods:{
          getPropVal(prop,label,itemProp){
            if(prop.indexOf(".")!==-1){
              //存在
              let props=prop.split(".");
              if(label){
                let val=this.handleType(props,label,itemProp);
                return val;
              }else{
                return this.realModel[props[0]][props[1]];
              }
            }else{
              if(label){
                let val=this.handleType(prop,label,itemProp);
                return val;
              }else{
                return this.realModel[prop];
              }
            }
          },
          handleType(prop,label,itemProp){
            switch (label){
              case "select":
                let data =itemProp.split('.'),flag,pp=data[1];
                if(prop instanceof  Array){
                  if (this.realModel.templateCache[pp]) {
                    this.realModel.templateCache[pp].forEach(item => {
                      if (item.value == this.realModel[prop[0]][prop[1]]) {
                        flag = item.label;
                      }
                    })
                  }
                }else{
                  if (this.realModel.templateCache[pp]) {
                    this.realModel.templateCache[pp].forEach(item => {
                      if (item.value == this.realModel[prop]) {
                        flag = item.label;
                      }
                    })
                  }
                }
                return flag;
                break;
              default:
                break
            }
          }
        },
        created(){

        }
    }
</script>

<style scoped lang="less">
  .essentialInfo{
    background-color: #f0f9ff99;
    margin:5px 0 15px 0 ;
    .cell{
      .cell-title{
          height: 40px;
          p{
            padding: 15px 0 0 23px;
            i{
              font-size: 18px;
              color: #4A89DC;
              display: inline-block;
              float: left;
              margin-top: 1px;
            }
            span{
              font-weight: 400;
              font-style: normal;
              font-size: 16px;
              color: #4A89DC;
            }
          }
      }
      .cell-content{
        .ivu-row{
            padding:20px 15px 10px 55px;
            .ivu-col-span-8{
              margin-bottom: 20px;
            }
            span{
              font-weight: 400;
              font-style: normal;
              font-size: 14px;
              color: #666666;
            }
            span.active{
              color: #CC0000;
            }
        }
      }
    }
  }
</style>



