<template>
  <div>
    <Button @click="click">改变选中值</Button>
    <Input v-model="casedata.value[2]"/>
    <Cascader ref="cascader" :data="casedata.data" v-model="casedata.value" @on-change="onChange"></Cascader>
  </div>
</template>
<script>
  export default {
    model: {
      prop: 'casedata',
      event: 'change'
    },
    props: {
      casedata: {
        type: Object,
        default() {
          return {
            data: [],
            value: []
          };
        }
      }
    },
    watch: {
      casedata() {
        this.$nextTick(() => {
          this.$refs.cascader.updateSelected()
        })
      }
    },
    methods: {
      click() {
        this.$emit(
          'change', {
            value: ['jiangsu', 'suzhou', 'shizilin'],
            data: [
              {
                value: 'beijing',
                label: '北京',
                children: [
                  {
                    value: 'gugong',
                    label: '故宫'
                  },
                  {
                    value: 'tiantan',
                    label: '天坛'
                  },
                  {
                    value: 'wangfujing',
                    label: '王府井'
                  }
                ]
              }, {
                value: 'jiangsu',
                label: '江苏',
                children: [
                  {
                    value: 'nanjing',
                    label: '南京',
                    children: [
                      {
                        value: 'fuzimiao',
                        label: '夫子庙',
                      }
                    ]
                  },
                  {
                    value: 'suzhou',
                    label: '苏州',
                    children: [
                      {
                        value: 'zhuozhengyuan',
                        label: '拙政园',
                      },
                      {
                        value: 'shizilin',
                        label: '狮子林',
                      }
                    ]
                  }
                ],
              }]
          }
        );
      },
      onChange(value) {
        this.$emit('change', {
          data: this.casedata.data,
          value: value,
        })
      }
    }
  }
</script>



