          <div class="left-content">按主题:</div>
        </Col>
        <Col span="19" align="left" v-if="moreList" style="padding-left:18px;">
          <ul class="list-data" >
            <li v-for="(item,index) in mainListDataList.slice(0,6)" :key="index" @click="onSelect(1,index,item.classifyCode)">
              <a>{{item.classifyName}}</a>
            </li>
          </ul>
          <ul class="listDataMore" v-if="moreMainUl">
            <li v-for="(item,index) in mainListDataList.slice(6)" :key="index" @click="onSelect(1,index+6,item.classifyCode)"><a>{{item.classifyName}}</a>
            </li>
          </ul>
        </Col>
        <Col v-if="showMoreXuanZe" span="20"  style="padding-left:18px;margin-left: 20px;">
          <div >
            <CheckboxGroup v-model="data">
              <Checkbox v-for="(item,index) in mainListDataList" :key="index" :label="item.classifyCode+','+item.classifyName"
                        style="width:8.6%;" @on-change="showMoreName(index)">{{item.classifyName}}
              </Checkbox>
            </CheckboxGroup>
            <div class="more-btn" style="text-align: center">
              <Button type="primary"
                      style="margin-right:8px;" :disabled="data.length==0" @click="checkAllGroupChange(1)">确定
              </Button>
              <Button @click="cancle(1)">取消</Button>
            </div>
          </div>
        </Col>
        <Col span="1" v-if="showTagOne" style="margin-left: 10px;margin-right: 8px;">
          <div class="showall-btn" @click="showMore(1)" v-show="tagMoreShowOne">
            <Button size="small" type="primary">
              更多
              <Icon type="ios-arrow-down" color="darkgray"/>
            </Button>
          </div>
          <div class="showall-btn" @click="hideMore(1)" v-show="tagMoreHideOne">
            <Button size="small" type="primary" style="border:1px solid gray">
              收起
              <Icon type="ios-arrow-up" color="darkgray"/>
            </Button>
          </div>
        </Col>
        <Col span="1" style="margin-left:12px;">
          <div class="showall-btn"  @click="moreShowSelect(1)">
          <Button size="small">
            {{showMoreXuanZe==true?"收起":"多选"}}
            <Icon type="md-add"/>
          </Button>
          </div>
        </Col>
      </Row>
      <Row class="list-row" :class="[deparmentListFlag==true ? 'border2' : 'border1']"
           style="borderTop:1px dashed #e4e4e4" v-if="deparmentLiFlag&&tempHide">
        <Col span="2" align="center" class="list-name" :class="[deparmentListFlag == true? 'backGroundOne' : 'backGroundTwo']" v-if="moreDepartmentList">
          <div class="left-content">按部门:</div>
        </Col>
        <Col span="19" align="left" style="padding-left:18px;" v-if="deparmentListFlag">
          <ul class="list-data" >
            <li v-for="(item,index) in departmenList.slice(0, 6)" :key="index"
                @click="onSelect(5,index,item.orgId)"><a>{{item.orgName}}</a>
            </li>
          </ul>
          <ul class="listDataMore" v-if="moreUl">
            <li v-for="(item,index) in departmenList.slice(6)" :key="index"
                @click="onSelect(5,index+6,item.orgId)"><a>{{item.orgName}}</a>
            </li>
          </ul>
        </Col>
        <Col v-if="showMoreXuanZeDepartment" span="20"  style="padding-left:18px;margin-left: 20px;">
          <div>
            <CheckboxGroup v-model="departmentData">
              <Checkbox v-for="(item,index) in departmenList" :key="index"
                        :label="item.orgId+','+item.orgName"
                        style="width:18.66%;" @on-change="showMoreName(index)">{{item.orgName}}
              </Checkbox>
            </CheckboxGroup>
            <div class="more-btn" style="text-align: center">
              <Button type="primary"
                      style="margin-right:8px;" :disabled="departmentData.length==0" @click="checkAllGroupChange(2)">确定
              </Button>
              <Button @click="cancle(2)">取消</Button>
            </div>
          </div>
        </Col>
        <Col span="1"  v-if="showTagTwo" style="margin-left: 10px;margin-right:8px;">
          <div class="showall-btn" @click="showMore(2)" v-show="tagMoreShowTwo">
            <Button size="small" type="primary">
              更多
              <Icon type="ios-arrow-down" color="darkgray"/>
            </Button>
          </div>
          <div class="showall-btn" @click="hideMore(2)" v-show="tagMoreHideTwo">
            <Button size="small" type="primary">
              收起
              <Icon type="ios-arrow-up" color="darkgray"/>
            </Button>

          </div>
        </Col>
        <Col span="1" style="margin-left:12px;">
          <div class="showall-btn" @click="moreShowSelect(2)">
          <Button size="small">
           {{showMoreXuanZeDepartment==true?"收起":"多选"}}
            <Icon type="md-add"/>
          </Button>
          </div>
        </Col>
      </Row>
      <div v-if="showMoreNum">
      <Row class="list-row border2" style="border-top:1px dashed #e4e4e4" v-if="eventListFlag&&tempHide">
        <Col span="2" align="center" class="list-name" style="background:#f6f6f6">
          <div class="left-content">事件性质:</div>
        </Col>
        <Col span="22" align="left" style="padding-left:18px;">
          <ul class="list-data">
            <li v-for="(item,index) in eventList" :key="index" @click="onSelect(4,index,item.classifyCode)"><a>{{item.classifyName}}</a>
            </li>
          </ul>
        </Col>
      </Row>
      </div>
      <!--<div v-if="showMoreNum">-->
        <!--<Row class="list-row border2" style="border-top:1px dashed #e4e4e4" v-if="administrativeListFlag">-->
          <!--<Col span="3" align="center" class="list-name" style="background:#f6f6f6">-->
            <!--<div class="left-content">行政分类:</div>-->
          <!--</Col>-->
          <!--<Col span="18" align="left" style="padding-left:18px;">-->
            <!--<ul class="list-data">-->
              <!--<li v-for="(item,index) in administrativeClassifiyData" :key="index"-->
                  <!--@click="onSelect(3,index,item.createOperId)">-->
                <!--<a>{{item.classifyName}}</a></li>-->
            <!--</ul>-->
          <!--</Col>-->
        <!--</Row>-->
        <!--<Row class="list-row" style="borderTop:1px dashed #e4e4e4" v-if="serviceFlag">-->
          <!--<Col span="3" align="center" class="list-name" style="background:#f6f6f6">-->
            <!--<div class="left-content">服务形式:</div>-->
          <!--</Col>-->
          <!--<Col span="18" align="left" style="padding-left:18px;">-->
            <!--<ul class="list-data">-->
              <!--<li v-for="(item,index) in mainServiceList.slice(0,2)" :key="index" @click="onSelect(2,index,item.createOperId)">-->
                <!--<a>{{item.classifyName}}</a></li>-->
            <!--</ul>-->
          <!--</Col>-->
        <!--</Row>-->
      <!--</div>-->
    </div>
    <div class="showall-btntwo" v-show="tag===false&&tempHide&&isShowClassify">
      <Button size="small" type="primary" @click="down">
        更多选项
        <Icon type="ios-arrow-down" color="darkgray"/>
      </Button>
    </div>
    <div class="show-alltwo" v-show="tag===true&&tempHide&&isShowClassify">
      <Button size="small" type="primary" @click="up">
        收起
        <Icon type="ios-arrow-up" color="darkgray"/>
      </Button>
    </div>
  </div>
</template>

<script>
  import Service from '../../service/home-procceeds-service'
  // import InstanceService from '../../service/'
  import {mutations} from "vuex"
  /**
   *@author       张利萍
   *@date         2018/9/14 10:58
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */

  function getInitData () {
    return {
      mainListDatas: [],
      departmentData: [],
      //note 选择更多的数组
      moreDataList: [],
      numberBother: [],
      //note 主体的选择数据
      checkAllGroupMainData: [],
      //note 显示更多控制
      tagMoreShowOne: true,
      tagMoreHideOne: false,
      tagMoreShowTwo: true,
      tagMoreHideTwo: false,
      //note 服务分类的数组
      serveNameArr: [
        {
          id: 1,
          serveName: "自然人"
        },
        {
          id: 2,
          serveName: "法人"
        }
      ],
      showTagOne: true,
      showTagTwo: true,
      // note 服务形式的数组
      mainServiceList: [],
      serviceDataFlag: false,
      //note 控制服务形式的显示与隐藏
      serviceDataValue: '',
      serviceFlag: true,
      //note 部门分类
      departmenNameList: [],
      departentListDataValue: '',
      departmenList: [],
      moreDepartmentList: true,
      showMoreXuanZeDepartment: false,
      departmentFlag: false,
      //note  控制更多的选择
      showMoreXuanZe: false,
      moreList: true,
      // note 选择是部门
      departmendata: [],
      departListValue: [],
      deparmentListFlag: true,
      mainListValue: [],
      // note 部门显示更多
      moreUl: false,
      // note 行政分类
      administrativeClassName: '',
      administrativeFlag: false,
      administrativeListFlag: true,
      administrativeClassifiyData: [],
      showMoreXuanZeDepartment: false,
      // note 事件性质
      eventList: [],
      eventListDatavalue: '',
      eventDataFlag: false,
      eventListFlag: true,
      //note 显示更多
      showTagMoreDataOne: false,
      showMoreNum: false,
      serveNameCode: '01',
      baseData: [],
      serviceMainList: [],
      mainListDataList: [],
      classifyData: [],
      tag: false,
      data: [],
      // note 服务主题
      flag: false,
      flagSeve: true,
      moreMainUl: false,
      // note 控制主题的显示隐藏
      mainFlag: false,
      mainLiFlag: true,
      deparmentLiFlag: true,
      servieName: '',
      mainListDataValue: '',
      Servicetype: '',
      //note 传给父组件的值
      parentArrObj: {
        // note 行业主题的id集合
        themeList: [],
        // note 服务对象id
        serviceObject: "",
        // note 部门id
        deptList: [],
        // note 服务形式的id集合
        serviceTypeList: [],
        // note 行政分类的id集合
        admList: [],
        // note 事项性质id集合
        propertyList: [],
        // note 兄弟页面传过来的code码
        areaCode:""
      }
    }
  }
  export default {
    name: "ty-classify",
    // showMore:false,
    props: {
      selectItem: {
        default() {
          return {}
        }
      },
      serviceMain: {
        type: Array,
      }
    },

    data() {
      return {
        // note 临时变量，标示是否显示隐藏
        tempHide: false,
        areaCodes:'',
        areaCodeBother:"",
        ...getInitData()
      }
    },
    computed: {
      showTagMore() {
        if (this.numberBother.length > 7) {
          return false
        } else {
          return true
        }
      },
      dddd(){
        return this.$store.state.common.deparmentLiFlag;
      },
      isShowClassify() {
        return this.flagSeve || this.mainLiFlag || this.deparmentLiFlag || this.eventListFlag;
      }
    },
    //note 监听传过来的数据
    watch: {
      serviceMain(newVal, oldVal) {
        this.serviceMainList = newVal
      },
    },
    methods: {
      //note 控制显示更多的按钮
      showMore(num) {
        if (num == 1) {
          this.tagMoreShowOne = false;
          this.tagMoreHideOne = true;
          this.showTagMoreDataOne = true;
          this.moreMainUl = true;
          if(this.moreMainUl==true){
            // this.onSelect(1,index,item.createOperId);
          }
        } else if (num == 2) {
          this.tagMoreShowTwo = false;
          this.tagMoreHideTwo = true;
          this.showTagMoreDataTwo = true;
          this.moreUl = true;
          if(this.moreUl==true){
           // this.onSelect(num, index, id)
          }
        }
      },
      //note 收起显示更多
      hideMore(num) {
        if (num == 1) {
          this.tagMoreShowOne = true;
          this.tagMoreHideOne = false;
          this.showTagMoreDataOne = false;
          this.moreMainUl = false;
        } else if (num == 2) {
          this.tagMoreShowTwo = true;
          this.tagMoreHideTwo = false;
          this.showTagMoreDataTwo = false;
          this.moreUl = false;
        }
      },

      //note 多选确认按钮
      checkAllGroupChange(num) {
        if (num == 1) {
          this.mainListDatas = [];
          this.mainListValue = [];
          this.data.forEach((value) => {
            this.mainListDatas.push(value.split(',')[1])
            this.mainListValue.push(value.split(',')[0])
          });
          this.mainListDataValue = this.mainListDatas.join(',');
          this.parentArrObj.themeList = this.mainListValue;
          this.mainFlag = true;
          this.mainLiFlag = false;
          this.showMoreXuanZe = false;
          this.moreList = true;
          this.$emit('update', this.parentArrObj);
          this.data = [];
        } else if (num == 2) {
          this.departmenNameList = [];
          this.departListValue = [];
          this.departmentData.forEach((value) => {
            this.departmenNameList.push(value.split(',')[1]);
            this.departListValue.push(value.split(',')[0])
          });
          this.departentListDataValue = this.departmenNameList.join(',')
          this.departmentFlag = true;
          this.deparmentLiFlag = false;
          this.showMoreXuanZeDepartment = false;
          this.deparmentListFlag = true;
          this.parentArrObj.deptList = this.mainListValue;
          this.departmentData = [];
        }
      },
      // note 取消
      cancle(num) {
        if (num == 1) {
          this.mainFlag = false;
          this.mainLiFlag = true;
          this.showMoreXuanZe = false;
          this.moreList = true;
          this.showTagOne=true;
        } else if (num == 2) {
          this.departmentFlag = false;
          this.deparmentLiFlag = true;
          this.showMoreXuanZeDepartment = false;
          this.deparmentListFlag = true;
          this.moreUl=false;
          this.showTagTwo=true;
        }
      },
      //note 自然人法人切换
      onService(val) {
        this.flag = true;
        this.flagSeve = false;
        if (val == 1) {
          this.servieName = '自然人';
          // this.getClassifyData(1,'dept',this.areaCodeBother);
          this.getClassifyData(1, 'all');
          this.getClassifyData(1, 'theme');
          if(this.areaCodeBother){
            this.getClassDepart(this.areaCodeBother);
          }else{
            this.getClassDepart(610100000000);
          }
          this.parentArrObj.serviceObject = 1;
          this.$emit('update', this.parentArrObj);
          Bus.$emit("transmit", 1)
        } else {
          this.servieName = '法人';
          this.parentArrObj.serviceObject = 2;
          // this.getClassifyData(2,'dept',this.areaCodeBother);
          // this.getClassifyData(2, 'all');
          this.getClassifyData(2,'theme');
          this.getClassifyData(2, 'all');
         if(this.areaCodeBother){
           this.getClassDepart(this.areaCodeBother);
         }else{
           this.getClassDepart(610100000000);
         }
          this.parentArrObj.serviceObject = 2;
          this.$emit('update', this.parentArrObj);
          Bus.$emit("transmit", 2);
        }
      },
      // note 关闭标签发请求
      closeService(num) {
        // debugger
        switch (num) {
          case 1:
            this.flag = false;
            this.flagSeve = true;
            this.parentArrObj.serviceObject = "";
            this.$emit('update', this.parentArrObj);
            break;
          case 2:
            this.mainFlag = false;
            this.mainLiFlag = true;
            this.parentArrObj.themeList = [];
            this.showTagOne=true;
            this.$emit('update', this.parentArrObj);
            break;
          case 3:
            this.serviceDataFlag = false;
            this.serviceFlag = true;
            this.parentArrObj.serviceTypeList = [];
            this.$emit('update', this.parentArrObj);
            break;
          case 4:
            this.administrativeListFlag = true;
            this.administrativeFlag = false;
            this.parentArrObj.admList = [];
            this.$emit('update', this.parentArrObj);
            break;
          case 5:
            this.eventListFlag = true;
            this.eventDataFlag = false;
            this.parentArrObj.propertyList = [];
            this.$emit('update', this.parentArrObj);
            break;
          case 6:
            this.deparmentLiFlag = true;
            this.departmentFlag = false;
            this.parentArrObj.deptList = [];
            this.showTagTwo=true;
            this.$emit('update', this.parentArrObj);
            break;
          case 7:
            let resetData = getInitData();
            resetData.parentArrObj.areaCode = this.parentArrObj.areaCode;
            Object.assign(this, resetData);
            this.$emit('update', this.parentArrObj);
            // console.log(this);
            // debugger;
          default:
        }
        // debugger
        if(this.servieName=='法人'){
          this.getClassifyData(2, 'all');
          this.getClassifyData(2, 'theme');
        }else if(this.servieName){
          this.getClassifyData(1, 'all');
          this.getClassifyData(1, 'theme');
        }
        this.getClassifyData(1, 'theme');
        this.getClassifyData(-1, 'all');
        // this.getClassifyData(1,'dept',this.areaCodeBother);
        if(this.areaCodeBother){
          this.getClassDepart(this.areaCodeBother);
        }else{
          this.getClassDepart(610100000000);
        }
      },
      // note 选择上面的标签
      onSelect(num, index, id) {
        console.log(num,index,id)
        switch (num) {
          case 1:
            this.mainFlag = true;
            this.mainLiFlag = false;
            this.parentArrObj.themeList = [id];
            this.$emit('update', this.parentArrObj);
            this.mainListDataValue = this.mainListDataList[index].classifyName;
            break;
          case 2:
            this.serviceDataFlag = true;
            this.serviceFlag = false;
            this.serviceDataValue = this.mainServiceList[index].classifyName;
            this.parentArrObj.serviceTypeList = [id];
            this.$emit('update', this.parentArrObj);
            break;
          case 3:
            this.administrativeListFlag = false;
            this.administrativeFlag = true;
            this.administrativeClassName = this.administrativeClassifiyData[index].classifyName;
            this.parentArrObj.admList = [id];
            this.$emit('update', this.parentArrObj);
            break;
          case 4:
            this.eventListFlag = false;
            this.eventDataFlag = true;
            this.eventListDatavalue = this.eventList[index].classifyName;
            this.parentArrObj.propertyList = [id];
            this.$emit('update', this.parentArrObj);
            break;
          case 5:
            this.deparmentLiFlag = false;
            this.departmentFlag = true;
            this.departentListDataValue = this.departmenList[index].orgName;
            this.parentArrObj.deptList = [id + ""];
            this.$emit('update', this.parentArrObj);
            break;
          default:
        }
      },
      // note 控制更多的选择
      moreShowSelect(num) {
        if (num == 1) {
          this.showMoreXuanZe = !this.showMoreXuanZe;
          this.moreList = !this.moreList;
          this.showTagOne = !this.showTagOne;
          this.moreMainUl = false;
        } else if (num == 2) {
          this.showMoreXuanZeDepartment = !this.showMoreXuanZeDepartment;
          this.deparmentListFlag = !this.deparmentListFlag;
          this.showTagTwo = !this.showTagTwo
        }

      },
      showMoreName(index) {
        this.moreDataList = index;
      },
      //note 展开和收起全部按钮
      up() {
        this.showMoreNum = false;
        this.tag = false;
      },
      // note 收起更多的按钮
      down() {
        this.showMoreNum = true;
        this.tag = true;
      },
      //note 获取查询条件数据接口
      getClassifyData(val,type,code) {
        let params = {
          classifyType: type,
          areaCode:code,
          headers:{'Show-Loading':false}
        };
        if (val > -1) {
          params.serviceObject = val
        }
        if(type!='all'){
          this.mainListDataList=[];
          Service.getServiceClassLiist(params).then(data => {
            if (data) {
              this.mainListDataList=data.themeList;
              this.mainServiceList=data.serviceTypeList;
              this.administrativeClassifiyData=data.admList;
              this.eventList=data.itemPropertyList;
            }
          }).catch(err => {
          });
        }else{
          this.mainListDataList=[];
          Service.getServiceClassLiist(params).then(data => {
            if (data) {
              this.mainServiceList=data.serviceTypeList;
              this.administrativeClassifiyData=data.admList;
              this.eventList=data.itemPropertyList;
            }
          }).catch(err => {
          });
        }
      },
      // note 请求部门的数据
      getClassDepart(code){
        let params={
          regionCode:code,
          headers:{'Show-Loading':false}
        };
        Service.getServiceDepartLiist(params).then((res)=>{
          // this.departmenList=[]
          if(res){
            this.departmenList=res;
          }
        })
      }
    },
    mounted() {
      this.$emit('update', this.parentArrObj = {
        // note 行业主题的id集合
        themeList: [],
        // note 服务对象id
        serviceObject: "",
        // note 部门id
        deptList: [],
        // note 服务形式的id集合
        serviceTypeList: [],
        // note 行政分类的id集合
        admList: [],
        // note 事项性质id集合
        propertyList: [],
        //note 地址code
        areaCode: localStorage.getItem('areaCoded') || 610100000000,
        tagList:[],
      },)
    },
    created() {
      // note  其他组件传来的区域编码(地址选择组件)
      Bus.$on("depart",  (num,name)=> {
        this.areaCodeBother = num;
        this.parentArrObj.areaCode=num;
        this.$emit('update',this.parentArrObj, false);
        this.getClassifyData(1,'theme');
        this.getClassifyData(-1, 'all');
        // this.getClassDepart(610100000000);
        this.getClassDepart(this.areaCodeBother);
      });

      Bus.$on('closeService', this.closeService);

        this.getClassifyData(1,'theme');
        this.getClassifyData(-1, 'all');
        this.areaCodes=localStorage.getItem('areaCoded');
        if(this.areaCodes){
          this.getClassDepart(this.areaCodes);
          this.parentArrObj.areaCode=this.areaCodes
          // debugger
        }else{
          this.getClassDepart(610100000000);
          this.parentArrObj.areaCode=610100000000
        }
      // this.getClassDepart(this.areaCodeBother);

    },
    // note 取消监听事件
    beforeDestroy(){
      Bus.$off('depart');
    }
  }
</script>
<style scoped>
</style>



