<template>
    <examples-demo title="步骤组件 step" desc="" :code="code.step"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-step slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateStep from "./example-page-template-step";

  export default {
    name: "example-page-template-all-demo",
    components: {ExamplePageTemplateStep, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns: [
          {
            title: '属性',
            key: 'prop'
          },
          {
            title: '说明',
            key: 'desc'
          },
          {
            title: '类型',
            key: 'type'
          },
          {
            title: '默认值',
            key: 'default'
          }
        ],
        data: [
          {
            prop: 'current',
            desc: '当前步骤,',
            type: 'Number',
            default: '空'
          },
          {
            prop: 'steps',
            desc: '所有的步骤数据',
            type: 'Array',
            default: '空'
          },
          {
            prop: 'title',
            desc: '步骤名称',
            type: 'String',
            default: '空'
          }
        ]
      }
    }
  }
</script>



