export default class InstanceService {
  //note 图片验证码
  static async getContrastCode(data){
    return ServiceRequest.post('captcha/contrastCode',data);
  }
  //note 工单提交
  static async getFeedbackSubmit(data){
    return ServiceRequest.post('feedback/submitFeedback',data);
  }
  //note 查询工单详情
  static async getFeedbackDtail(data){
    return ServiceRequest.post('feedback/feedbackOrderDetail',data);
  }
  //note 查询公开列表详情
  static async getOpenListDtail(data){
    return ServiceRequest.post('feedback/feedbackOrderOpenDetail',data);
  }
  //note 区域接口
  static async getFeedbackDepartment(data){
    return ServiceRequest.post('address/users/signup/selectRegNameList',data);
  }
  // note 获取部门的接口
  static async  getFeedbackDepartLiist(data){
    return ServiceRequest.post('item/getOrgIdByAregionCode',data)
  }
  //note 获取事项列表服务
  static async getItemQryMatterPage(data){
    return ServiceRequest.post('item/users/signup/qrySubItemPage',data);
  }
  static async getFeedbackDepartmentBuMen(data){
    return ServiceRequest.post('item/qryDeptByRegionCode')
  }
  //note 个人用户管理查询
  static async getPersonIQuery(data) {
    return ServiceRequest.post(`user/qryUserInfoManager`, data)
  }
}



