export default class InstancesService  {

  //note SL0301添加证照信息服务
  static async addApplicationDoc(data) {
    return ServiceRequest.post(`certificate/addApplicationDoc`, data)
    // return ServiceRequest.post('getTemplateData', data, true);
  };
  //note 能力平台 外部系统药监局 资格校验接口
  static async qualificationCheck(data) {
    return ServiceRequest.post(`ability/selectQualificatoryCheck`, data)
    // return ServiceRequest.post('getTemplateData', data, true);
  };
  //note 能力平台 外部系统人社局 身份校验接口
  static async identityCheck(data) {
    return ServiceRequest.post(`ability/identityCheck`, data)
  };
  //note 获取模板数据测试用
  static async getTemplateData(data) {
    return ServiceRequest.get(`${location.origin}/zdpyc/portal/static/data/procceeds/${data.id}-${data.step}.json`);
    // return ServiceRequest.post('getTemplateData', data, true);
  };

  //note 获取页面数据
  static async getPageData(data) {
    return ServiceRequest.post(`certificate/getCertificateInfo`, data)
  }
  //note 获取支付接口数据
  static async getPaymentUrl(data) {
    return ServiceRequest.post(`payCenter/cashierUrlEncrypt`, data)
  }

  //note 保存
  static async save(data) {
    return ServiceRequest.post('certificate/addCertificateInfo', data);
  };


  //note 提交
  static async submit(data) {
    return ServiceRequest.post('certificate/acceptFinish', data, false);
  };

  //note 补正补齐情况下的提交
  static async SacSubmit(data) {
    return ServiceRequest.post('certificate/correctionToldFinish', data, false);
  };
//note 获取上传地址和accessKeyId, accessKeySecret
  static async getUploadSecret() {
    return ServiceRequest.get('file/toUpload');
  };
  //note 上传附件
  static async uploadAttach(data) {
    return ServiceRequest.post('fileAttrInfo/addAttrInfo', data);
  };
  //note 删除附件
  static async deleteAttach(data) {
    return ServiceRequest.post('fileAttrInfo/delAttrInfo', data);
  };
  //note 办理指南获取页面数据
  static async getGuideData(data) {
    return ServiceRequest.post(`item/qryItemDetailById`, data)
  }

  //note 办理指南获取申请材料数据

  static async getApplyData(data) {
    return ServiceRequest.post(`item/qryItemMaterialsList`, data)
  }
  //note 获取收费信息数据
  static async getChargeData(data) {
    return ServiceRequest.post(`item/qryItemCharge`, data)
  }

  //note 查询收藏信息
  static async getCollectData(data) {
    return ServiceRequest.post(`collection/getListCustCollectionByParm`, data)
  }
  //note 新增收藏
  static async addCollectData(data) {
    return ServiceRequest.post(`collection/insertCustCollection`, data)
  }
  //note 删除收藏
  static async delCollectData(data) {
    return ServiceRequest.post(`collection/deleteCustCollectionByPrimaryKey`, data)
  }
  //note 事项分类列表数据
  static async getClassifyData(data) {
    return ServiceRequest.post(`matterCenter/qrySxflList`, data)
  }
  //note 事项列表查询
  static async getListData(data){
    return ServiceRequest.post('matterCenter/qrySxList',data);
  }
  // note matter-list列表查询
  static async getMatterList(url,data){
    return ServiceRequest.post(url,data);
  }
  // note  获取事项列表
  static async getListDataAll(data){
    return ServiceRequest.post('item/qryMatterPage',data)
  }
  // note 根据服务对象分类
  static async  getServiceClassLiist(data){
    // return ServiceRequest.post('item/qryTheme',data)
    return ServiceRequest.post('item/qryItemClassify',data)
    // return ServiceRequest.post('item/qryThemeDefinition',data)
  }
  // note 根据区域写部门
  static async  getServiceDepartLiist(data){
    // return ServiceRequest.post('item/qryTheme',data)
    return ServiceRequest.post('item/getOrgIdByAregionCode',data)
    // return ServiceRequest.post('item/qryThemeDefinition',data)
  }
  /*static async getCollectedLiat(data){
    return ServiceRequest.post('collection/getListCustCollectionByParm',data)
  }*/
//  note 儿童父母双方是否参保
  static async  getInsuredConfirm(data){
    return ServiceRequest.post('ability/insuredConfirm',data)
  }
  //  note 处理二手房资格审查验证
  static async  queryAllowData(data){
    return ServiceRequest.post('ability/restrictedSale',data)
  }
  //  note 处理二手房资格审查下一步添加入参
  static async saveOtherData(data) {
    return ServiceRequest.post('ability/buyHousequalificationsField', data);
  };

  //  note 居民参保户籍地址接口
  static async CbAddress(data) {
    return ServiceRequest.post('abilitytwo/residenceBookletQuery', data);
  };
  static async WDAddress(data) {
    return ServiceRequest.post('abilitytwo/cialSecurity', data);
  };
  static async getCountyInfo(data) {
    return ServiceRequest.post('abilitytwo/getCountyInfo', data);
  };
  // note 异地就医网上备案,异地就医网上修改 页面下发接口取个人信息
  static async elsewhereSeekInfoQuery(data) {
    return ServiceRequest.post('ability/elsewhereSeekInfo', data);
  };
  // note 失业保险技能提升补贴申请 页面下发接口取个人信息
  static async companyInfoAndPersonInfo(data) {
    return ServiceRequest.post('ability/companyInfoAndPersonInfo', data);
  };
  // note 失业保险待遇核准支付  正常领取失业保险金场景 页面下发接口取个人信息
  static async companyInfoAndPersonInfo1(data) {
    return ServiceRequest.post('ability/companyInfoAndPersonInfo1', data);
  };
}




