<template>
  <div class="tableTitle"  >
    <div class="title">
      <Row>
        <Col span="4" style="padding-left: 25px;font-size: 16px;">
          <span class="verticalLine"></span>{{tableTitle}}
        </Col>
        <Col span="2" offset="18" style="text-align: right;color: #4A8ADC;font-size: 14px"><span @click="more">更多 >></span></Col>
      </Row>
    </div>
    <Table stripe v-bind="$props" style="margin-bottom: 16px"></Table>
  </div>
</template>
<script>
  export default {
    name: 'tableTitle',
    props: {
      height: {
        type: [Number, String]
      },
      tableTitle: {
        type: String
      },
      more: {
        type: Function
      },
      columns: {
        type: Array,
        default:[]
      },
      data: {
        type: Array,
        default:[]
      }
    },
    data() {
     return {
     }
    }
  }
</script>



