<template>
    <div class="remote-select">
      <Select  style="width:200px" v-model="model1" :label-in-value="flag" @on-change="changeData">
        <Option v-for="item in list" :value="item.value" :key="item.value">{{ item.label }}</Option>
      </Select>
    </div>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/12/21 16:05
     *@Copyright    天源迪科信息技术股份有限公司  远程搜索
     *@Description
     */
    import {realModel} from './content-box/mixin-page-template';
    import Service from "../service/component-service"
    export default {
        name: "remoteSelect",
        mixins:[realModel],
        props:{
          remoteData:{
            type:Object
          },
          prop:{
            type:String
          },
          title:{
            type:String
          },
          idx:{
            type:Number
          }
        },
        watch:{

        },
        data(){
          return {
            flag:true,
            list: [],
            model1:""
          }
        },
        methods:{
          //note 改变下拉框数据的值 赋值fullModel
          changeData(data){
            let id=this.idx,prop=this.prop,name=this.title,name1=`${this.title}Desc`;
            if(this.realModel[prop] instanceof Array){
              this.realModel[prop][id][name]=data.value;
              this.realModel[prop][id][name1]=data.label;
            }else{
              this.realModel[prop]=data.value;
            }
          },
          //note 获取数据
          query(url,params,label1,value1,type){
            Service.qrySelectData(url,params).then(res=>{
              if (res instanceof Array) {
                if (res && res.length > 0) {
                  res.forEach(item => {
                    this.selectData.push({
                      label: label1 && item[label1] ? item[label1] : item.label,
                      value: value1 && item[value1] ? item[value1] : item.value
                    })
                  })
                }
              }else{
                for (let i in res) {
                  if(type=='else'){
                    this.list.push({label: i, value:res[i]});
                  }else{
                    this.list.push({label: res[i], value:i});
                  }
                }
              }
            })
          },
          getList(data){
            if(data instanceof Object){
              this.query(data.url,data.params,data.label,data.value,data.type);
            }
          }
        },
        mounted(){
          this.model1=this.realModel[this.prop][this.idx][this.title]
        },
        created(){
          if(this.remoteData){
            this.getList(this.remoteData);
          }
        }
    }
</script>

<style scoped>

</style>



