<template>
  <Row type="flex" :justify="extend.justify||'center'" v-if="showStatus">
    <Col :span="extend.span||12">
    <Form ref="formValidate" class="ty-content-element-form" :model="realModel" :label-width="extend.labelWidth||205"
          :rules="rules"
          v-bind="extend">
      <FormItem v-for="item,key in items"
                :key="key"
                :ref="`item${key}`"
                v-bind="item"
                :rules="checkRules(item.rules||rules[item.prop])"
                :label="extend.labelWidth === 0 ? '' : item.label+':'"
                :style="formItemStyle(item)">
        <ty-content-element-form-item @update-parent="updateUI" v-bind="item" :model="realModel"
                                      @display-change="(value)=>{displayChange(`item${key}`,value)}"/>
      </FormItem>
    </Form>
    </Col>
    <div style="display: none">{{act}}</div>
  </Row>
</template>

<script>
  import TyContentElementFormItem from "./content-element-form-item";
  import {showStatus} from "../../mixin-showStatus";
  import validators from '../../../../utils/validators'

  /**
   *@desc     自动化页面组件--表单类
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    mixins: [showStatus],
    name: "ty-content-element-form",
    components: {TyContentElementFormItem},
    props: {
      rules: {
        default() {
          return {}
        }
      },
      validator: {
        type: Boolean
      },
      extend: {
        default() {
          return {};
        }
      },
      prop: {
        type: [String, Array]
      },
      items: {
        default() {
          return [
            {
              type: 'input',
              label: '输入框',
              prop: 'data1',
            },
            {
              type: 'label',
              label: '输入框',
              value: '11111'
            }
          ]
        }
      }
    },
    data() {
      return {
        flag: '',
        act: false
      }
    },
    methods: {
      //单个formItem校验
      formItemValidate(prop) {
        this.$refs['formValidate'].validateField(prop);
      },
      updateUI() {
        this.act = !this.act;
      },
      displayChange(ref, value) {
        this.$refs[ref][0].$el.hidden = !value;
      },
      formValidator() {
        if (!this.$refs['formValidate']) {
          return true
        } else {
          this.$refs['formValidate'].validate((valid) => {
            if (valid) {
              this.flag = true;
            } else {
              this.flag = false
            }
          })
          return this.flag
        }
      },
      //note 检测校验规则类型
      checkRules(rules) {
        if (rules instanceof Array) {
          let result = []
          rules.forEach(item => {
            result.push(this.checkRulesObject(item));
          });
          return result;
        } else if (rules instanceof Object) {
          return this.checkRulesObject(rules);
        } else {
          return rules;
        }
      },
      checkRulesObject(obj) {
        if (!obj) {
          return obj;
        }
        switch (obj.type) {
          case 'mobile':
          case 'phone':
          case 'mobilePhone':
          case 'daterange':
          case 'number':
          case 'number5':
          case 'validDate':
          case 'identification':
          case 'identification2':
          case 'checkIdcardList':
          case 'checkAddress':
          case 'area':
          case 'area2':
          case 'faxx':
          case 'number1':
          case 'workerNum':
          case 'noMoreDay':
          case 'stamp':
          case 'userName':
          case 'notNumber':
          case 'number2':
          case 'socialCode':
          case 'sevenNumber':
          case 'insuranceCardNum':
          case 'bankCard':
          case 'notMoreWords':
          case 'dateTimeDuration':
          case 'dateTimeRequired':
          case 'dateTimeOverlap':
          case 'dateRangeRequired':
          case 'dataDurationInNight':
          case 'vehicleLicense':
          case 'number3':
          case 'number4':
          case 'mobilePhone1':
          case 'numAndLetter':
          case 'realEstateUnit':
            return {
              ...obj, ...{
                required: obj.required,
                validator: validators[obj.type].validator,
                trigger: obj.trigger || 'change',
                message: obj.message
              }
            };
          case 'linkage':
          case 'dataTimeExistInDataRange':
          case 'estateRules':
          case 'endTimeStr':
          case 'thanSome':
            return {
              ...obj, ...{
                required: obj.required, validator: (rule, value, callback) => {
                  validators[obj.type].validator(rule, value, callback, this.realModel)
                }, trigger: obj.trigger || 'change', message: obj.message
              }
            };
          case 'dataRangeIncludeDataTime':
            return {
              ...obj, ...{
                required: obj.required, validator: (rule, value, callback) => {
                  validators[obj.type].validator(rule, value, callback, this.realModel, this.formItemValidate.bind(this))
                }, trigger: obj.trigger || 'change', message: obj.message
              }
            };
          case 'custom':
            return {
              ...obj, ...{
                required: obj.required, validator: (rule, value, callback) => {
                  // console.log('校验类型:'+obj.type,'validators:'+validators[obj.type]);
                  validators[obj.type].validator(rule, value, callback, {modal: this.realModel})
                }, trigger: obj.trigger || 'change', message: obj.message
              }
            };
          default:
            return obj;
        }
      },
      formItemStyle(item) {
        if (item.extend && item.extend.itemWidth) {
          const itemWidth = (this.extend.labelWidth || 205);
          return {width: (item.extend.itemWidth + itemWidth) + 'px'}
        }
        return {}
      }
    },
    mounted() {
      Bus.$on('valiteFormItem', this.formItemValidate);
    },
    created() {
      if (this.validator === true) {
        this.fullModel.templateCache.validators.push(this);
      }
    }
  }
</script>

<style lang="less">
  /*.ty-content-element-form{*/
  /*.ivu-form-item{*/
  /*margin-bottom:0;*/
  /*}*/
  /*}*/
</style>



