let Code = {};
Code.base=`<template>
  <attachment v-bind="obj"></attachment>
</template>
<script>
  import CommonService from "../../../service/common-service"
  import attachment from "../../../components/attachment";
  export default {
    name:'example-enclosure-info',
    components:{
      attachment
    },
    data(){
      return {
        obj:{
          headerTitle:"申请附件信息",
          showHeader:true,
          attach:[
            {title:'企业营业执照',downLoadUrl:'1',imgUrl:'img1'},
            {title:'房屋租赁合同',downLoadUrl:'2',imgUrl:'img2'},
            {title:'消防意见书',downLoadUrl:'3',imgUrl:'img3'},
            {title:'广告未设置前整体原景彩图',downLoadUrl:'4',imgUrl:'img4'},
            {title:'广告未设置后整体彩图',downLoadUrl:'5',imgUrl:'img5'}
          ],
          btn:[
            {
              name:'查看附件',
              flag:'see'//查看
            },
            {
              name:'下载附件',
              flag:'download'//下载
            }
          ]
        }
      }
    },
    methods: {

    }
  }
</script>

`;
export default Code;



