export default class HomeMemberCenterAddressAddService {

  //note 获取所有地址信息
  static async getAllAddressData(data){
    return ServiceRequest.post('address/qryAddressInfoByCustomerId',data);
  }
  //note 获取当前地址信息
  static async getAddressData(data){
    return ServiceRequest.post('address/qryAddressDetail',data);
  }
  //note 新增地址信息
  static async addressAdd(data){
    return ServiceRequest.post('address/addAddressInfo',data);
  }
  //note 删除地址信息
    static async addressDel(data){
      return ServiceRequest.post('address/deleteAddressByAddressId',data);
    }
  //note 修改地址信息
  static async addressEdit(data){
    return ServiceRequest.post('address/updateAddressByAddressId',data);
  }
  //note 设置默认地址信息
  static async updateDefaultAddress(data){
    return ServiceRequest.post('address/updateDefaultAddress',data);
  }
  //note 级联选择
  static async regionCode(data){
    return ServiceRequest.post('address/qryStandardAddr',data);
  }
  //note 邮寄证照
  static async sendPostCard(data){
    return ServiceRequest.post('express/addExpress',data);
  }
  //note 判断是否发送
  static async isSendMail(data){
    return ServiceRequest.post('express/qryAcceptPostInfo',data);
  }

}



