<template>
  <div class="ty-component-element-title" v-if="showStatus">
    <div class="title"  style="position:relative">
      <template v-if="anchorLink">
        <a :href="anchorLink" :class="{'incre-dis': location}" :id="anchor">
        </a>
        <span :id="`${anchor}span`">{{title}}</span>
      </template>
      <template v-else>
        {{title}}
      </template>
    </div>
    <Divider v-if="divider" dashed></Divider>
  </div>
</template>

<script>
  /**
   *@desc
   *@author
   *@date
   */
  export default {
    name: "ty-content-element-title",
    props: {
      //note 标题
      title: {
        type: String,
        default: ''
      },
      //note 是否使用锚点
      anchor: {
        type: String,
        default: ''
      },
      //note 是否有分割线
      divider:{
        default:true
      },
      //note 是否更改锚点位置
      location:{
        default: false
      },
      fullModelData:{
        type:Object,
        default() {
          return {}
        }
      },
      isShow:{
        type:String
      },
      showFlag:{
        type:[Object,Array]
      },
      showFlagType:{
        type:String
      }
    },
    data(){
      return{

      }
    },
     computed: {
       showStatus(){
         if(this.isShow){
           if(this.fullModelData[this.isShow]===true){
             return true
           }else{
             return false
           }
         }else if(this.showFlag instanceof Array){
           let current=this.checkShowFlagArray(this.showFlag,this.showFlagType);
           return current
         }else if(this.showFlag){
           if(this.showFlag.showType&&this.showFlag.showType==='either'){
             let v1=this.handleVal('either');
             return v1;
           }else{
             let v2=this.handleVal('');
             return v2;
           }
         }
         return true
       },
      //note 获取锚点链接
      anchorLink() {
        if (this.anchor && this.anchor !== '') {
          return `#${this.anchor}`
        }
        return false
      }
    },
    methods:{
      handleVal(sign){
        let val=this.showFlag.value,val1=this.fullModelData[this.showFlag.key];
        switch (sign){
          case "either":
            let flag1=true;
            if(val.indexOf(",")!==-1){
              let splitVal=val.split(",");
              splitVal.forEach(item=>{
                if(val1==item){
                  flag1= false
                }
              })
              return flag1;
            }else{
              if(val==val1){
                return false
              }
              return true
            }
            break;
          default:
            let flag2=false;
            if(val.indexOf(",")!==-1){
              let splitVal=val.split(",");
              splitVal.forEach(item=>{
                if(val1==item){
                  flag2= true
                }
              })
              return flag2;
            }else{
              if(val==val1){
                return true
              }
              return false
            }
            break;
        }
      },
      checkShowFlagArray(array,type) {
        let result;
        if (type === 'either') {
          result = false;
          array.forEach(item => {
            if (this.fullModel[item.key] == item.value) {
              result = true;
            }
          })
        } else {
          result = true;
          array.forEach(item => {
            if (this.fullModel[item.key] != item.value) {
              result = false;
            }
          })
        }
        return result;
      }
    },
    created(){

    }
  }
</script>



