<template>
  <div class="lookup-query">
    <Button @click="query(type)" type="info">{{btnName}}</Button>
    <Modal
      v-model="modal1"
      title="文件查询"
      ok-text="确认"
      ok-cancel="取消"
      width="640px"
      @on-ok="ok"
      @on-cancel="cancel">
      <Table border :columns="columns1" :data="data1"></Table>
    </Modal>
    <Modal
      v-model="flag"
      title="车辆信息"
      ok-text="确认"
      ok-cancel="取消"
      width="1240px"
      @on-ok="ok"
      @on-cancel="cancel">
      <ty-title class="title" style="margin-top: -22px !important;" :title="'车辆行驶证信息'"></ty-title>
      <Table border :columns="columns2" :data="data2"></Table>
      <ty-title class="title" :title="'车辆产权信息（机动车登记证）'"></ty-title>
      <Table border :columns="columns3" :data="data3"></Table>
    </Modal>
    <Modal
      v-model="modalInvoice"
      title="发票查询"
      ok-text="确认"
      ok-cancel="取消"
      width="640px"
      @on-ok="ok"
      @on-cancel="cancel">
      <Table border :columns="columns4" :data="data4"></Table>
    </Modal>
  </div>
</template>

<script>
  /**
   *@author       刘文军
   *@date         2018/10/18 15:28
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import TyTitle from '../content-element-title'
  import {realModel} from "../../mixin-page-template";
  import SingleIndexService from '../../../../service/single-index-service'
  import Bus from "../../../../utils/bus";

  export default {
    //sign就是prop的值
    mixins: [realModel],
    name: "content-element-lookup-query",
    props: {
      btnName: {
        type: String,
        default: "查询"
      },
      sign: {
        type: String
      },
      modelData: {
        type: Object
      },
      type: {
        type: String,
        default: '1'
      },
      params: {
        type: Array,
      },
      show: {
        type: String
      },
      extend: {
        type: Object
      },
      bindString: {
        type: String
      }
    },
    components: {
      TyTitle
    },
    data() {
      return {
        modal1: false,
        columns1: [
          {
            title: '选择',
            width: 60,
            render: (h, {row, column}) => {
              return h('div', {}, [
                h('Radio', {
                  on: {
                    'on-change': () => {
                      this.modelData['isSuperOrg'] = Number(row.isNeedSupervision);
                    }
                  }
                })
              ])
            }
          },
          {
            title: '环评批复文号',
            key: 'acceptKey'
          },
          {
            title: '是否需要监理机构',
            key: 'acceptValue',
            render: (h, {row, column}) => {
              if (row.acceptValue == '0') {
                return h('span', '不需要')
              } else if (row.acceptValue == '1') {
                return h('span', '需要')
              }
            }
          }
        ],
        data1: [],
        columns2: [
          {
            title: '车辆号码',
            key: ''
          },
          {
            title: '业户名称',
            key: ''
          },
          {
            title: '业户地址',
            key: ''
          },
          {
            title: '车主',
            key: ''
          },
          {
            title: '车辆类型',
            key: ''
          },
          {
            title: '核定载质量',
            key: ''
          },
          {
            title: '总质量',
            key: ''
          },
          {
            title: '整备质量',
            key: ''
          },
          {
            title: '注册登记日期',
            key: ''
          },
          {
            title: '品牌型号',
            key: ''
          },
          {
            title: '车辆颜色',
            key: ''
          },
        ],
        data2: [],
        columns3: [
          {
            title: '车辆号码',
            key: ''
          },
          {
            title: '车轴数',
            key: ''
          },
          {
            title: '准牵引总质量',
            key: ''
          },
          {
            title: '发动机号',
            key: ''
          },
          {
            title: '车辆颜色',
            key: ''
          },
          {
            title: '车辆登记日期',
            key: ''
          },
          {
            title: '出厂日期',
            key: ''
          },
          {
            title: '燃料类型',
            key: ''
          },
          {
            title: '车架号',
            key: ''
          },
          {
            title: '车长',
            key: ''
          },
          {
            title: '车宽',
            key: ''
          },
          {
            title: '车高',
            key: ''
          }
        ],
        data3: [],
        flag: false,
        data4:[],
        columns4:[
          {
            title: '选择',
            width: 60,
            render: (h, {row, column}) => {
              return h('div', {}, [
                h('Radio', {
                  on: {
                    'on-change': () => {
                      this.modelData['serialNum'] = row.serialNum;
                    }
                  }
                })
              ])
            }
          },
          {
            title: '流水号',
            key: 'serialNum'
          }
        ],
        modalInvoice:false
      }
    },
    methods: {
      ok() {

      },
      cancel() {

      },
      query(type) {
        switch (type) {
          //环评批复文号
          case '1':
            SingleIndexService.Query({
              applyName: this.modelData.applyName, serviceName: this.modelData.serviceName,
              eiaRelayNumber: this.modelData[this.sign]
            }).then(res => {
              if(res.length>0){
                this.data1 = res;
                this.modal1 = true;
              }else{
                  util.alert({
                    type: 'info',
                    content: "系统中无该项目相关信息，请手动填写项目信息!"
                  })
              }
            })
            break;
          //身份证号
          case '2':
            let reg = /(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
            if (reg.test(this.realModel[this.sign])) {
              SingleIndexService.QueryIdCard({sfzmhm: this.realModel[this.sign]}).then(res => {
                console.log(JSON.parse(res))
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');
                if (this.params && this.params.length > 0) {
                  this.params.forEach(item => {
                    this.realModel[item] = JSON.parse(res).datas[item];
                  })
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //车牌号
          case '3':
            if (this.realModel[this.sign] !== '') {
              // this.flag = true;
              SingleIndexService.QueryCarId({cardNumber: this.realModel[this.sign]}).then(res => {
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');
                console.log(this.params, this.realModel[this.show])
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //网签合同号
          case '4':
            if (this.realModel[this.sign] !== '') {
              SingleIndexService.queryContract({HTBH: this.realModel[this.sign]}).then(res => {
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');
                if (this.params && this.params.length > 0) {
                  this.params.forEach(item => {
                    this.realModel[item] = res[item];
                  })
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //申请号
          case '5':
            if (this.realModel[this.sign] !== '') {
              SingleIndexService.QueryApplicationNumber({groupNo: this.realModel[this.sign]}).then(res => {
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');

                if (this.params && this.params.length > 0) {
                  this.params.forEach(item => {
                    this.realModel[item] = JSON.parse(res.formInfos)[item];
                  })
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //请求号查询
          case '6':
            if (this.realModel[this.sign] !== '') {
              console.log(this.realModel);
              console.log(this.sign);
              console.log(this.realModel[this.sign]);
              SingleIndexService.QueryScals({groupNo: this.realModel[this.sign]}).then(res => {
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');
                let sellData;
                if (res.formInfos) {
                  sellData = JSON.parse(res.formInfos);
                } else {
                  return
                }
                if (this.params && this.params.length > 0) {
                  this.params.forEach(item => {
                    if (this.bindString) {
                      this.realModel[this.bindString][item] = sellData[item];
                    } else {
                      this.realModel[item] = sellData[item];
                    }
                  })
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //房屋权属证号
          case '7':
            if (this.realModel[this.sign] !== '') {
              this.realModel[this.show] = true;
              Bus.$emit('checkShowHide');
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //监管协议号
          case '8':
            if (this.realModel[this.sign] !== '') {
              this.realModel[this.show] = true;
              Bus.$emit('checkShowHide');
              SingleIndexService.queryAgreementNumber({jgxyRowString:"[{'ZJJGXYH':'"+(this.realModel[this.sign]+"")+"'}]"}).then(res => {
                console.log(res.json);
                let agreement = JSON.parse(res.json);
                agreement = agreement[0];
                if (this.params && this.params.length > 0) {
                  this.params.forEach(item => {
                    this.realModel[item] = agreement[item];
                  })
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //小餐饮查询页面信息
          case '9':
            if (this.realModel[this.sign] !== '') {
              this.realModel[this.show] = true;
              Bus.$emit('checkShowHide');
              console.log(this.sign, this.realModel)
              SingleIndexService.QueryXiaocanyin({
                DZ_search: this.realModel.applyName,
                XKZBH_search: this.realModel.XKZBH,
                QYLX: this.realModel.QYLX,
                ZT: 1
              }).then(res => {
                console.log(this.realModel)
                let areaArray = [{
                  "label": "单纯火锅",
                  "value": "单纯火锅"
                },
                  {
                    "label": "单纯烧烤",
                    "value": "单纯烧烤"
                  },
                  {
                    "label": "全部使用半成品加工",
                    "value": "全部使用半成品加工"
                  },
                  {
                    "label": "中餐类制售",
                    "value": "中餐类制售"
                  },
                  {
                    "label": "西餐类制售",
                    "value": "西餐类制售"
                  },
                  {
                    "label": "日餐类制售",
                    "value": "日餐类制售"
                  },
                  {
                    "label": "韩餐类制售",
                    "value": "韩餐类制售"
                  },
                  {
                    "label": "冷热饮品制售",
                    "value": "冷热饮品制售"
                  }]
                if (res) {
                  let params = {}
                  for (let i in res) {
                    params[i.toUpperCase()] = res[i]
                  }
                  this.realModel.area = []
                  params.FW.split("").forEach((e, index) => {
                    if (e == '1') {
                      this.realModel.area.push(areaArray[index].value)
                    }
                  })
                  _.assign(this.realModel, params)
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //二手房合约备案卖家房屋产权证号
          case '10':
            if (this.realModel[this.sign] !== '') {
              SingleIndexService.QuerySellsHome({type: 0, value: this.realModel[this.sign]}).then(res => {
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');
                if (this.params && this.params.length > 0) {
                  this.params.forEach(item => {
                    this.realModel[item] = res.item;
                  })
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //网签合同号：先调合同变更申请方校验接口，判断是否是合格的合同号，是的话就继续调合同查询号接口并查出数据
          case '11':
            if (this.realModel[this.sign] !== '') {
              SingleIndexService.houseContractChange({contractNumber: this.realModel[this.sign]}).then(res => {
                if (res == 'success') {
                  SingleIndexService.queryContract({HTBH: this.realModel[this.sign]}).then(res => {
                    this.realModel[this.show] = true;
                    Bus.$emit('checkShowHide');
                    if (this.params && this.params.length > 0) {
                      this.params.forEach(item => {
                        this.realModel[item] = res[item];
                      })
                    }
                  })
                } else {
                  util.alert({
                    type: 'error',
                    content: "请输入合格的网签合同号！"
                  })
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //买方请求号查询
          case '12':
            if (this.realModel[this.sign] !== '') {
              //note  能力平台-房管局-FG014资格查询数据接口
              SingleIndexService.qualificationEnquiry({
                "MFZJHM":this.realModel.applyCardNumber,
                "CQZBH":this.modelData[this.sign]
              }).then(res=>{
                console.log(res);
              })

              SingleIndexService.QueryScals({groupNo: this.realModel[this.sign]}).then(res => {
                this.sellLimit();
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');
                let sellData;
                if (res.formInfos) {
                  sellData = JSON.parse(res.formInfos);
                } else {
                  return
                }
                if (this.params && this.params.length > 0) {
                  this.params.forEach(item => {
                    if (this.bindString) {
                      this.realModel[this.bindString][item] = sellData[item];
                    } else {
                      this.realModel[item] = sellData[item];
                    }
                  })
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //请求号查询
          case '13':
            if (this.realModel[this.sign] !== '') {
              console.log(this.realModel);
              console.log(this.sign);
              console.log(this.realModel[this.sign]);
              SingleIndexService.QueryScals({groupNo: this.realModel[this.sign]}).then(res => {
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');
                let sellData;
                if (res.formInfos) {
                  sellData = JSON.parse(res.formInfos);
                } else {
                  return
                }
                if (this.params && this.params.length > 0) {
                  this.params.forEach(item => {
                    if (this.bindString) {
                      this.realModel[this.bindString][item] = sellData[item];
                    } else {
                      this.realModel[item] = sellData[item];
                    }
                  })
                }
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //异地就医网上查询
          case '14':
            if (this.realModel[this.sign] !== '') {
              SingleIndexService.elsewhereSeekDoctor({applyName: this.modelData.applyName,applyCardNumber:this.modelData.applyCardNumber}).then(res => {
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');
                this.realModel['informationList']=res;
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //医保卡个人账户查询
          case '15':
            if (this.realModel[this.sign] !== '') {
                SingleIndexService.qryPersonYBAccount({carteVital: this.modelData.carteVital,identityCard:this.modelData.applyCardNumber}).then(res => {
                this.realModel[this.show] = true;
                Bus.$emit('checkShowHide');
                  if (this.params && this.params.length > 0) {
                    this.params.forEach(item => {
                      this.realModel[item] = res[item];
                    })
                  }
              }).catch(res=>{
                  this.realModel[this.show] = false;
                  Bus.$emit('checkShowHide');
              })
            } else {
              Bus.$emit('valiteFormItem', this.sign);
            }
            break;
          //出具医疗（大额）保险费、生育保险费缴费电子发票查询
          case '16':
            SingleIndexService.medicalTreatmentQuery({
              issue: this.modelData.issue,
              unitNumber: this.modelData.unitNumber,
              identityCard: this.modelData.identityCard
            }).then(res => {
              if(res.length==1){
                this.realModel[this.sign] = res[0].serialNum;
              }else if(res.length>1){
                this.data4 = res;
                this.modalInvoice = true;
              }else{
                util.alert({
                  type: 'info',
                  content: "系统中无发票流水号信息!"
                })
              }
            })
            break;
        }
      }
    },
    created() {

    }
  }
</script>

<style scoped lang="less">

</style>



