let Code = {};
Code.base=`
<template>
      <guide-info v-bind="obj"></guide-info>
</template>
<script>
    import GuideInfo from "../../../components/guide-info";
    export default {
        name: "example-guide-info",
        components:{
          GuideInfo,
        },
        data(){
            return{
              obj:{
                  qrCode:'../assets/img/license.jpg',
                  title:'市政公用基础设施施工许可',
                  btnList:[
                    {label:'在线办理',type:'primary',value:1},
                    {label:'我要咨询',type:'warning',value:2},
                    {label:'我要投诉',type:'error',value:3},
                  ]
                }

            }
        }
    }
</script>

`;
export default Code;



