  </div>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExampleBasicInfo from "./example-basic-info";

  export default {
    name: "index",
    components: {ExamplesDemo, ExampleBasicInfo},
    data() {
      return {
        code: Code
      }
    }
  }
</script>



