<template>
    <Card  :title="child.title" class="cell-item" :dis-hover="hover" >
      <span>{{child.value}}</span>
    </Card>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/10/8 19:27
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
        name: "cell-item-child",
        props:{
          hover:{
            type:Boolean,
            default:true
          },
          child:{
            type:Object
          }
        }
    }
</script>



