<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/12/15 21:54
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
        name: "example-multiple-selection",
        data() {
        return {
          pageData: {
            //页面基础数据
            page: {
              title: 'mail-licence',
              fullModel: {
                supplementType:"",
                FWQT:"",
                other:[],
                "bothItem":""
              }
            },
            //页面元素数据
            elements: [
              {
                "type":"box",
                "data":[ {
                  "type": "form",
                  "data": {
                    "extend": {
                      "span": 17,
                      "labelWidth":250
                    },
                    "items": [
                      {
                        "type": "checkboxMultiple",
                        "label": "补证类型（可多选）",
                        "prop": "other",
                        "extend":{
                          "otherBtn":"FWQT",
                          "bind":"supplementType",
                          "mockData":[
                            {"label":"补正件","value":"0"},
                            {"label":"补副件","value":"1"}
                          ]
                        }
                      },
                      {
                        "type": "input",
                        "label": "买卖双方补充条款",
                        "prop": "bothItem",
                        "extend": {
                          "placeholder": "请输入",
                          "itemWidth":350
                        },
                        "excat":[
                          {
                            "type":"input-note",
                            "text":"如出售比例为100%，不填写任何内容；如非100%，如20%，填写内容“卖方同意将房屋所拥有产权的20%部分出售给买方"
                          }
                        ]
                      }
                    ]
                  }
                }]
              }
            ]
          }
        }
      }
    }
</script>

<style scoped>

</style>



