import Uploads from './uploads';
import PreviewFileCell from './preview-file-cell'
import ExtendTable from './extend-table';
import ButtonOprator from './button-oprator';
import ContentBox from './content-box';
import PageTemplate from './page-template';
import Search from './search';
import TyContentElementLookupFee from './content-box/content-elements/content-element-lookup/content-element-lookup-fee.vue'
import TyContentElementLookupMap from './content-box/content-elements/content-element-lookup/content-element-lookup-map.vue'
import TyContentElementLookupSample from './content-box/content-elements/content-element-lookup/content-element-lookup-sample.vue'
import TyRetireInfo from './content-box/content-elements/content-elements-table/retire-info'
import TyRetireRequireState from './content-box/content-elements/content-elements-table/retire-require-state'
const components = {
  Uploads,
  PreviewFileCell,
  ExtendTable,
  ButtonOprator,
  ContentBox,
  PageTemplate,
  Search,
  TyContentElementLookupFee,
  TyContentElementLookupMap,
  TyContentElementLookupSample,
  TyRetireInfo,
  TyRetireRequireState
};

const tydic = {
  ...components
};

const install = function(Vue, opts = {}) {
  if (install.installed) return;

  Object.keys(tydic).forEach(key => {
    Vue.component(key, tydic[key]);
    if(tydic[key].name){
      Vue.component(tydic[key].name, tydic[key]);
    }
  });
};

const API = {
  install,
  ...components
};

export default API;



