<template>
  <div class="home-tab">
    <Tabs value="name1">
      <TabPane label="法人办事" name="name1">
        <ul class="home-tab-ul">
          <li  v-for="(item,index) in tabList" :key="index">
            <img class="tab-icon" :src="item.imgUrl"/>
            <div class="tab-title">{{item.title}}</div>
            <div class="tab-btn">
              <Button class="guide-btn">办理指南</Button>
            </div>
            <div class="tab-btn">
              <Button class="online-btn">在线办理</Button>
            </div>
          </li>
        </ul>
      </TabPane>
      <TabPane label="个人办事" name="name2">
        <ul class="home-tab-ul">
          <li  v-for="(item,index) in tabList2" :key="index">
            <img class="tab-icon" :src="item.imgUrl"/>
            <div class="tab-title">{{item.title}}</div>
            <div class="tab-btn">
              <Button class="guide-btn">办理指南</Button>
            </div>
            <div class="tab-btn">
              <Button class="online-btn">在线办理</Button>
            </div>
          </li>
        </ul>
      </TabPane>
    </Tabs>
  </div>

</template>

<script>
    /**
     *@author       王婷婷
     *@date         2018/10/15 10:39
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
      name: "home-index-tab",
      props:{
        obj:{
          type:Object
        },
        tabList:{
          type:Array
        },
        tabList2:{
          type:Array
        },

      },
    }
</script>

<style scoped lang="less">

  .home-tab {
    .home-tab-ul {
      border: 1px solid #e7e7e7;
      padding: 10px 0 ;
      width: 1200px;
      >li {
        padding:19px;
        float: left;
        border-right: 1px dashed #E4E4E4;
        border-bottom: 1px dashed #E4E4E4;
        text-align: center;
        .tab-icon {
          width: 60px;
          height: 60px;
        }
        .tab-title {
          margin-top: 10px;
          color: #000;
          font-size: 16px;
          line-height: 22px;
          height: 44px;
          width: 160px;
          white-space: normal;
        }
        .tab-btn {
          .guide-btn {
            width: 157px;
            height: 34px;
            line-height: 22px;
            border: 1px solid #e84709;
            background-color: #fff;
            color: #e84709;
            font-size: 14px;
            margin-top: 10px;
            &:hover {
              background-color: #FFF9F5;
              color: #e84709;
            }
          }
          .online-btn {
            width: 157px;
            height: 34px;
            line-height: 22px;
            border: 1px solid #e84709;
            background-color: #FCDFCC;
            color: #e84709;
            font-size: 14px;
            margin-top: 10px;
            &:hover {
              background-color: #F8C3A0;
              color: #e84709;
            }
          }
        }
        &:nth-child(6){
          border-right:none;
        }
      }
      &:after{
        clear: both;
        content: ' ';
        display: block;
        font-size: 0;
        line-height: 0;
        visibility: hidden;
        width: 0;
        height: 0;
      }
    }
  }

</style>



