<template>
  <div class="verification">
    <!--<Button type="primary" class="verification-btn">选择验证方式</Button>-->
    <Row class="verificationMethod" v-for="(item,index) in verifiList" :key="index">
      <Col span="6">
        <div class="card-msg" >
          <!--<Icon type="ios-at" size="36" />-->
          <!--<Icon :type="item.iconType" size="36" />-->
          <img :src="item.iconType" class="real-img"/>
        </div>
      </Col>
      <Col span="12" class="content-msg">
        <p class="main-title">{{item.mainTitle}}</p>
        <p class="main-msg">{{item.mainMsg}}</p>
        <p class="main-warn">{{item.mainWarn}}</p>
      </Col>
      <Col span="6" class="btn-msg">
        <Button type="primary" class="btn" :disabled= item.isStop?true:false v-if="item.isShow" @click="jumpHref(item.vType,item.isPersonal,item.isStop)">{{item.btnMsg}}</Button>
        <Button type="primary" class="btn1" v-if="!item.isShow"></Button>
      </Col>
    </Row>
  </div>
</template>

<script>
    /**
     *@author       王婷婷
     *@date         2018/9/26 10:00
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
      name: "verification-method",
      props:{
        verifiList:{
          type:Array
        },
      },
      methods:{
        jumpHref(vType,isPersonal,isStop){
          if(!isStop){
            this.$router.push({
              name:'home-member-center-verification-page1',
              query: {
                vType:vType,
                isPersonal:isPersonal,
                editType:this.$route.query.editType,
                userName:this.$route.query.userName,
                tel:this.$route.query.tel,
                email:this.$route.query.email,
                isId:this.$route.query.isId,
                isEmail:this.$route.query.isEmail
              }
            });

          }

        }
      }
    }
</script>

<style scoped>

</style>



