<template>
  <Row type="flex" :justify="extend.justify||'center'">
    <Col :span="extend.span||12">
    <Form class="ty-content-element-form" :label-width="extend.labelWidth||205" v-bind="extend">
      <FormItem style="position: relative">
        <span style="display: inline-block;position: absolute;left: -30px"><Checkbox
          v-model="realModel['isAgree']"></Checkbox></span> {{content}}
      </FormItem>
    </Form>
    </Col>
  </Row>
</template>

<script>
  /**
   *@author       高琛
   *@date         2018/1/9 16:57
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./content-box/mixin-page-template";

  export default {
    name: "agreeCommitment",
    mixins: [realModel],
    props: {
      top: {
        type: Number,
        default: 0
      },
      book: {
        type: Array
      },
      bookContent: {
        type: String
      },
      extend: {
        default() {
          return {};
        }
      },
      text: {
        type: String
      },
      type: {
        type: String,
        default: "1"
      },
      prop: {
        type: String,
        default: "isAgree"
      }
    },
    watch: {},
    data() {
      return {
        //是否同意
        isAgree: false,
        bookArr: this.book,
        content: "",
        modal1: false,
        //模态框标题，内容
        modalContent: '',
        modalTitle: '',
        //checkbox是否可选
        dis: true,
        //判断是否够60s可以点击取消
        canCancel: false,
        second: 0,
        waitTime: 0,
        num: [],
        index: null
      }
    },
    methods: {},
    created() {
      console.log(this.prop)
      if (this.prop == 'commitmentLetter') {
        let date = new Date()
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let day = date.getDate();
        this.content = '本公司于' + year + '年' + month + '月' + day + '日签署发布了突发环境事件应急预案，备案条件具备，备案文件齐全，先报送备案；本单位承诺，本单位在办理备案中所提供的相关文件及其信息均经本单位确认真实，无虚假，且未隐瞒事实。'
        this.realModel.commitmentLetter = this.content
      }else{
        this.content = this.bookContent
      }
    }
  }
</script>

<style scoped>


</style>



