<template>
  <examples-demo title="查看地图 lookup-map" desc="" :code="code.lookupMap">
    <example-page-template-lookup-map slot="demo"/>
  </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateLookupMap from "./example-page-template-lookup-map";

  export default {
    name: "example-page-template-lookup-map-demo",
    components: {ExamplePageTemplateLookupMap, ExamplesDemo},
    data() {
      return {
        code: Code
      }
    }
  }
</script>



