<template>
  <div class="guideTab clearfix" v-bind="$props">
    <Tabs>
      <TabPane class="tabpane" v-for="(item,index) in tabList" :name='item.value'
               :label="item.label" :key="item.value">{{item.content}}</TabPane>
    </Tabs>
  </div>
</template>
<script>
    export default {
        name: "guideTab",
        props:{
            obj:{
              type:Object
            },
            tabList:{
              type:Array
            }
        },
        data(){
          return {}
        },
        methods:{}
    }
</script>



