const env = process.env.NODE_ENV;
const isBuild = process.env.IS_BUILD;

//note 设置项目子目录路径
const baseURL = env == "local" || env == "test" || env == "localTest" || env == "uat" || env == "production" || env == "development" ? '/zdpyc/portal/' : "/";
// const baseURL='/';
//note 接口访问地址前缀
//如果和域名加二级目录不一致则需要设置prefix,否则只需要设置baseURL
const prefix = '';

// 兼容ie10不认识location.origin
if (!window.location.origin) {
  window.location.origin = window.location.protocol + "//" + window.location.hostname + (window.location.port ? ':' + window.location.port : '');
}
// note 一网
const envURL = {
  production: 'http://zwfw.xa.gov.cn',
  // uat: 'http://39.97.6.213:8001',
  uat: 'http://uat.tydicdev.com',
  test: 'http://yangwei.tydicdev.com',
  testPressure: 'http://192.168.3.120:8080',
  // development: 'http://yangwei.tydicdev.com:801',
  development: 'http://39.107.159.54:19001',
  local: 'http://39.107.159.54:19001',
  // localTest: 'http://39.107.157.8:8080',
  localTest: 'http://yangwei.tydicdev.com',
};
// note 一码扫码
const qcodeURL = getEnvURL() + 'generateCode/generateQrcode?content=';
// note 一码获取内容
const qcodeContent = getEnvURL() + '#/home-h5?reqType=process&projId=';
// note 智能客服
const customURL = {
  production: 'http://zwfw.xa.gov.cn/zdpyc/ics',
  uat: 'http://uat.tydicdev.com/zdpyc/ics',
  test: 'http://yangwei.tydicdev.com/zdpyc/ics',
  development: 'http://47.95.29.235:8080',
  local: 'http://47.95.29.235:8080',
  localTest: 'http://yangwei.tydicdev.com/zdpyc/ics',
};
// note CMS地址
const cmsURL={
  production: 'http://poccms.tydicdev.com/',
  uat: 'http://uatcms.tydicdev.com/',
  test: 'http://poccms.tydicdev.com/',
  development: 'http://devcms.tydicdev.com/',
  local: 'http://devcms.tydicdev.com/',
  localTest: 'http://poccms.tydicdev.com/',
}
//note 路由白名单 此名单路由优先不拦截
const whiteList = {
  default: [
    /home-procceeds-list/,
    /home-legal-identiy/,
    /home-hand-guide/
  ],
  testPressure: [
    /home-procceeds-list/,
    /home-legal-identiy/,
    /home-hand-guide/
  ],
  production: [
    /home-procceeds-list/,
    /home-legal-identiy/,
    /home-hand-guide/
  ],
  uat: [
    /home-procceeds-list/,
    /home-legal-identiy/,
    /home-hand-guide/
  ],
  test: [
    /home-procceeds-list/,
    /home-legal-identiy/,
    /home-hand-guide/
  ],
  development: [
    /home-procceeds-list/,
    /home-legal-identiy/,
    /home-hand-guide/
  ],
  local: [
    /home-procceeds-list/,
    /home-legal-identiy/,
    /home-procceeds-template-single-index/
  ],
  localTest: [
    /home-procceeds-list/,
    /home-legal-identiy/,
    /home-procceeds-template-single-index/
  ]
};
//note 路由黑名单 此名单路由拦截
const blackList = [
  /home-legal/,
  /home-member/,
  /home-procceeds/,
  /home-fast-register/,
  /home-hand-guide/
];
//note 回调地址黑名单 此名单地址回调为首页
const callBacBlackList = [
  /home-fast-regisiter/,
  /home-personal/,
  /home-find-pwd/,
  /home-legal/,
  /home-procceeds-template-single-index/
];
//note 第三方登录未认证白名单 此名单内路由不会跳转至第三方登录认证页面
const authoWhiteList = [
  /home-personal-create-account/,
  /home-personal-real-name/,
  /home-personal-finish/
];

//note 智能客服地址
function getCustomURL() {
  return customURL[env];
}

//note 事项二维码地址
function getQcodeURL() {
  return qcodeURL;
}

//note 获取二维码内容地址
function getQcodeContent() {
  return qcodeContent;
}
//note 获取CMS地址
function getCmsURL() {
  return cmsURL[env];
}
//note 获取环境地址
function getEnvURL() {
  if (isBuild === 'true') {
    console.log('isBuild:' + isBuild, 'url:' + location.origin + baseURL);
    return location.origin + baseURL;
  } else {
    console.log('isBuild:' + isBuild, 'url:' + envURL[env]);
    return envURL[env] + baseURL;
  }
};

// note 回到地址是否在黑名单,返回true在黑名单
function isCallBack() {
  let result = false;
  const path = location.href;
  callBacBlackList.forEach(item => {
    if (path.match(item)) {
      result = true;
    }
  });
  return result
}

const config = {
  //note 是否是编译模式
  isBuild: isBuild,
  //note 环境
  env: env,
  //note 设置项目子目录路径
  baseURL: baseURL,
  //note mock地址
  mock_uri: 'http://39.107.52.108:8001/app/mock/30/',
  //note 登录地址 默认mode=1,普通登录
  loginUrl(mode=1) {
    let result = false;
    let currentTime = new Date().getTime();
    let temp = getEnvURL() + '' + 'casRoute/skip?mode=';
    let temp2 = '&currentTime=' + currentTime + '&callBackUrl=' + location.origin;
    let temp3 = '&currentTime=' + currentTime + '&callBackUrl=' + location.href
    if (result) {
      //mode为2 时，是支付宝登陆；不为2时，是普通登陆
      if (mode && mode === 2) {
        return temp + mode + temp2 + baseURL;
      }
      else {
        return temp + '1' + temp2 + baseURL;
      }
    }
    return mode === 2 ? temp + mode + temp3 : temp + '1' + temp3;
  },
  //note 注册地址
  registerUrl() {
    switch (env) {
      case "test":
        return "http://61.185.238.209:10003/sysauth-v3/index/register?client_id=000000047&redirect_uri=" + location.origin + baseURL;
      case "uat":
        return "http://61.185.238.209:10003/sysauth-v3/index/register?client_id=000000047&redirect_uri=" + location.origin + baseURL;
      case "production":
        return "http://sfrz.shaanxi.gov.cn/sysauth-v3/index/register?client_id=000000047&redirect_uri=" + location.origin + baseURL
    }
  },

  //note 修改用户信息跳转
  editUserInfo() {
    const token = localStorage.getItem('token');
    let result = isCallBack();
    const testUrl = "http://61.185.238.209:10003/sysauth-v3/oauth2-modify?access_token=";
    const proUrl = "http://sfrz.shaanxi.gov.cn/sysauth-v3/oauth2-modify?access_token=";
    switch (env) {
      case "test":
        return result ? testUrl + token + '&redirect_uri=' + location.origin + baseURL
          : testUrl + token + '&redirect_uri=' + location.href + "&menuType=resetInfo";
      case "production":
        return result ? proUrl + token + '&redirect_uri=' + location.origin + baseURL
          : proUrl + token + '&redirect_uri=' + location.href + "&menuType=resetInfo";
    }
  },
  loginTarget: isBuild ? '_self' : '_blank',
  //note 退出地址
  logoutUrl: `${getEnvURL()}logout`,
  logoutTarget: isBuild ? '_self' : '_blank',
  //note 智能客服地址
  customURL: customURL[env],
  //note 事项二维码地址
  qcodeURL: qcodeURL,
  qcodeContent: qcodeContent,
  //note 接口基础路径
  apiBaseUrl: getEnvURL(),
  //note 接口前缀
  prefix: prefix,
  //note 白名单
  inList(path, whiteOrBlack) {
    let result = false;
    whiteOrBlack.forEach(item => {
      if (path.match(item)) {
        result = true;
      }
    });
    return result;
  },
  inWhite(path) {
    return this.inList(path, whiteList[env] || whiteList['default'])
  },
  inBlack(path) {
    return this.inList(path, blackList)
  },
  inAuthorWhite(path) {
    console.log('authorList:', path)
    console.log('是否第三方登录未认证白名单:', this.inList(path, authoWhiteList))
    return this.inList(path, authoWhiteList)
  },
  //note 二维码
  qrCode(id) {
    return getQcodeURL() + encodeURIComponent(getQcodeContent() + `${id}`)
  },
  //note 获取环境地址
  getEnvURL: getEnvURL,
  //note 获取cms地址
  getCmsURL:getCmsURL,
  api(service, type, mock = false) {
    if (service.match(/^((http|https|file)?):\/\/([\w\-]+(\.[\w\-]+)*\/)*[\w\-]+(\.[\w\-]+)*\/?(\?([\w\-\.,@?^=%&:\/~\+#]*)+)?/)) {
      return service;
    }
    if (mock) {
      return `${this.mock_uri}${type || service}`;
    }
    switch (type) {
      case 'menu':
        return this.apiBaseUrl + 'pages/user/menus';
      case 'upload':
        return this.apiBaseUrl + 'file/upload'
      case 'down':
        return this.apiBaseUrl + 'rest/download' + service
      default:
        return this.apiBaseUrl + this.prefix + service
    }
  },
}
export default config



