<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     提示警告
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-tip-alert",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '提示警告'
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'tip-alert',
                  data:
                    {
                      content:'发的发的<a>佛挡杀佛</a>发卡机的开发开放将',
                    }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



