<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/9/10 14:21
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
        name: "example-page-template-info-attachment",
        data() {
          return {
            pageData: {
              //页面基础数据
              page: {
                fullModel: {
                  infoAttachment:[
                    {
                      attrName:'上传页attrName',
                      name: "企业营业执照",
                      fileUrl: "1"
                    },
                    {
                      attrName:'上传页attrName',
                      name: "房屋租赁合同",
                      fileUrl: "2"
                    },
                    {
                      attrName:'上传页attrName',
                      name: "消防意见书",
                      fileUrl: "3"
                    },
                    {
                      attrName:'上传页attrName',
                      name: "广告未设置前整体原景彩图",
                      fileUrl: "4"
                    },
                    {
                      attrName:'上传页attrName',
                      name: "广告未设置后整体彩图",
                      fileUrl: "5"
                    }
                  ],
                }
              },
              //页面元素数据
              elements: [
                {
                  type: "box",
                  data: [
                    {
                      type: "info-attachment",
                      data: {
                        model:"infoAttachment",
                        headerTitle: "申请附件信息",
                        showHeader: true
                      }
                    }
                  ]
                }
              ]
            }
          }
        },
        methods:{

        }
    }
</script>



