<template>
    <examples-demo title="提示警告 alert" desc="" :code="code.tipAlert"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-tip-alert slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateTipAlert from "./example-page-template-tip-alert";

  export default {
    name: "example-page-template-tip-alert-demo",
    components: {ExamplePageTemplateTipAlert, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns: [
          {
            title: '属性',
            key: 'prop'
          },
          {
            title: '说明',
            key: 'desc'
          },
          {
            title: '类型',
            key: 'type'
          },
          {
            title: '默认值',
            key: 'default'
          }
        ],
        data: [
          {
            prop: 'content',
            desc: '提示警告的内容,且内容是使用v-html渲染的可以渲染与之对应的标签',
            type: 'String',
            default: '空'
          }
        ]
      }
    }
  }
</script>



