<template>
  <div class="evaluate-wrap">
    <div class="cellHeader">
      <p class="celltitle">{{title}}</p>
    </div>
    <Row>
      <Col :span="_.divide(24,span)" v-for="(item,index) in data" :key="index" >
        <Card  :bordered="bordered" :title="item.title" :dis-hover="hover" >
          <span>{{data1[item.prop]}}</span>
        </Card>
      </Col>
    </Row>
  </div>
</template>
<script>
  export default {
    name:'evaluate-detail',
    props:{
      hover:{
        type:Boolean,
        default:false
      },
      span:{
        type:Number
      },
      title:{
        type:String
      },
      data:{
        type:[Array,Object]
      },
      data1:{
        type:[Array,Object]
      }
    },
    computed:{

    },
    methods:{

    },
    data(){
      return {
        bordered:false
      }
    },
    created(){

    }
  }
</script>
<style lang="less">
  .evaluate-wrap{
    .cellHeader{
      text-align: left;
      margin-top: 10px;
      height: 25px;
      line-height: 25px;
      background: #f2f2f2b3;
      .celltitle{
        padding-left: 18px;
        font-size: 16px;
        color: #EF7F53;
      }
    }
  }
  .ivu-card-head {
      border-bottom:none;
  }
  .ivu-card-head{
    p{
     span{
       border-left: 3px solid #ed3f14;
       padding-left: 10px;
     }
    }
  }
</style>



