<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     日期选择
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-form-date-picker",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '日期选择'
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //双向绑定数据对象
                    model: {
                      normalLabel: '文本框',
                      normalInput: '输入框',
                      normalRadio: 'male',
                      normalDate:[],
                      startDate:'',
                      endDate:'',
                      normalSelect:''
                    },
                    //规则
                    rules: {
                      normalInput: [
                        {required: true, message: 'Please fill in the user name', trigger: 'blur'}
                      ],
                      // normalDate: [
                      //   {required: true, message: 'Please fill in the password.', trigger: 'blur'},
                      //   {
                      //     type: 'string',
                      //     min: 6,
                      //     message: 'The password length cannot be less than 6 bits',
                      //     trigger: 'blur'
                      //   }
                      // ]
                    },
                    //Form扩展配置
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'date-picker',
                          label: '日期组件 type:date-picker',
                          prop: 'normaDate',
                          extend:{
                            placeholder:'请选择时间',
                            type:'daterange',
                          },
                        },
                        {
                          type: 'date-picker-daterange',
                          label: '日期组件分离版 type:date-picker-daterange',
                          extend:{
                          },
                          items:[
                            {
                              type:'date-picker',
                              prop:'startDate',
                              extend:{
                                placeholder:'请选择开始时间1',
                                // width:150
                              },
                            },
                            {
                              type:'date-picker',
                              prop:'endDate',
                              extend:{
                                placeholder:'请选择结束时间',
                              },
                            }
                          ],
                        }
                      ]
                  }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



