<template>
  <div class="">
    <DatePicker
      type="datetimerange"
      :format="extend.format"
      :options="options"
      :placeholder="extend.placeholder"
      :style="{ minWidth: extend.itemWidth || '100%' }"
      :value="value"
      @on-change="handleChange"
      :editable="false"
    ></DatePicker>
  </div>
</template>

<script>
  /**
   *@author       冯朝晖
   *@date         2018-12-06 15:43
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./../content-box/mixin-page-template";
  import Options from  "./../../utils/options";
  export default {
    mixins: [realModel],
    name: 'ty-data-time-picker-special',
    props: {
      value: {
        default: ''
      },
      id: {
        required: true
      },
      extend: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed: {
      dependField() {
        return this.realModel[this.extend.dependField];
      },
      options() {
        let creatDisabledDateOptions = Options[this.extend.disabledDateOptions];
        return {
          disabledDate: creatDisabledDateOptions(this.dependField)
        }
      }
    },
    methods: {
      handleChange(duration) {
        this.$emit('on-change', duration, this.id);
      }
    }
  }
</script>

<style lang="less" scoped>

</style>



