<template>
  <div class="ty-content-element-table-retire">
    <ty-content-element-tip-alert class="ty-content-element-table-retire" :closable="false"
                                  :content="`移交单位:${transfering}`"/>
    <Input class="input-search" v-model="formData.key" search enter-button placeholder="请输入关键词">
    <div slot="append">
      <Button icon="ios-search" @click="search"/>
    </div>
    </Input>
    <Button class="btn-append" @click="advance">{{btnAdvanceLabel}}</Button>
    <Form :labelWidth="130" v-if="isAdvance">
      <FormItem label="是否适龄退休人员:">
        <RadioGroup v-model="formData.isRetire" type="button">
          <Radio size="small" label="1">是</Radio>
          <Radio label="0">否</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="性别:">
        <RadioGroup v-model="formData.sex" type="button">
          <Radio label="male">男</Radio>
          <Radio label="female">女</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="退休申请状态:">
        <RadioGroup v-model="formData.state" type="button">
          <Radio label="1">未填写</Radio>
          <Radio label="2">待审核</Radio>
          <Radio label="3">审核中</Radio>
          <Radio label="4">审核驳回</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem>
        <Button @click="advance">返回</Button>
      </FormItem>
    </Form>
    <div style="min-height: 100px">
      <IvTable ref="table" v-bind="table" @onRowClick="onRowClick"/>
    </div>
  </div>
</template>
<script>
  import IvTable from "../../../iv-table";
  import TyContentElementTipAlert from "../content-element-tip/content-element-tip-alert";

  /**
   *@author       谢辉
   *@date         2018/9/24 01:00
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description  编号查询按钮
   */
  export default {
    name: "ty-content-element-table-retire",
    components: {
      TyContentElementTipAlert,
      IvTable
    },
    props: {
      eventType: {
        type: String
      }
    },
    data() {
      return {
        btnAdvanceLabel: '高级',
        isAdvance: false,
        //移交单位
        transfering: '西安市艾斯克咨询咨询有限公司',
        formData: {
          key: '',
          isRetire: '',
          sex: '',
          state: ''
        },
        table: {
          url: 'test',
          // pageNo: 1,
          singleSelect: false,
          data: [{
            name: '郑鹏',
            phone: '601100195205081332',
            address: '陕西省西安市碑林区南大街111号5-102'
          }, {}],
          height: 300,
          queryParam: function (param) {
            console.log('queryParam:', param)
            return param
          },
          responseHandler: function (val) {
            console.log('responseHandler:', val)
            return val;
          },
          columns: [{
            width: 60,
            type: 'selection',
            align: 'center',
          },
            {
              title: '退休人员信息',
              key: 'inquiryCode',
              align: 'center',
              render(h, {row}) {
                return h('ty-retire-info',
                  {
                    props: {
                      data: row
                    }
                  });
              }
            },
            {
              title: '退休类别',
              key: 'professionalOrgName',
              align: 'center',
              width: 120
            },
            {
              title: '退休申请状态',
              key: 'professionalOrgName',
              align: 'center',
              width: 120,
              render(h, {row}) {
                return h('ty-retire-require-state',
                  {
                    props: {
                      // data:row
                    }
                  });
              }
            },
            {
              title: '操作',
              key: 'professionalOrgName',
              align: 'center',
              width: 120,
              render:(h, {row})=>{
                return h('div', [
                  h('Button', {
                      props: {
                        type: 'success'
                      },
                      on: {
                        click: () => {
                          this.applicationForm(row.id)
                        }
                      }
                    },
                    '申请表格'
                  ),
                  h('a', {
                      on: {
                        click: () => {
                          this.lookDetail(row.id)
                        }
                      },
                    },
                    '查看详情'
                  )
                ])
              }
            },
          ]
        }
      }
    },
    methods: {
      //note 监听行点击
      onRowClick(row, index) {
        this.selectIndex = index;
      },
      //note 高级
      advance() {
        this.isAdvance = !this.isAdvance;
        if (this.isAdvance) {
          this.btnAdvanceLabel = '隐藏'
        } else {
          Object.assign(this.formData,
            {
              isRetire: '',
              sex: '',
              state: ''
            });
          this.btnAdvanceLabel = '高级'
        }
      },
      //note 搜索
      search() {
        this.$refs.table.query(this.formData);
      },
      //note 查看详情
      lookDetail(id) {
        console.log('lookDetail')
      },
      //note 申请表格
      applicationForm(id) {

      }
    }
  }
</script>



