<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
  /**
   *@desc
   *@author
   *@date
   */
  export default {
    data() {
      return {
        pageData: {
          //页面基础数据
          "page": {
            "title": "select"
          },
          //页面元素数据
          "elements": [
            {
              "type": "box",
              "data": [
                {
                  "type": "steps-h5",
                  "data": {
                    "current":2,
                    "steps": [
                      {"title": "上传文件"},
                      {"title": "提交审核"},
                      {"title": "支付缴费"},
                      {"title": "已办结"}
                    ]
                  }
                }
              ]
            }
          ]
        }
      }
    }
  }
</script>



