<template>
  <ty-page-template :data="pageData"/>
</template>
<script>
  import TyFormSelect from '../../../components/select/form-select'
  /**
   *@desc
   *@author  冯朝晖
   *@date  2018.12.6
   */
  export default {
    components: {
      TyFormSelect
    },
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: 'select',
            fullModel: {
              region: ''
            }
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    rules: {
                      region: [{
                        "required": true,
                        "message": "请选择地区"
                      }]
                    },
                    //Form扩展配置
                    extend: {
                      labelWidth:200,
                      span:12,
                    },
                    items:
                      [ {
                        type: 'formSelect',
                        label: '选择',
                        prop: 'region',
                        extend: {
                          placeholder: '请选择',
                        }
                      }]
                  }
                },
              ]
            }
          ]
        },
      }
    },
    methods: {
    }
  }
</script>



