/**
 *@desc
 *@author  冯朝晖
 *@date    2019.9.3
 */
let Code = {};
Code.base = `<template>
  <ty-page-template :data="pageData"/>
</template>
<script>
  import TyDateTimePickerList from '../../../components/date-time-picker/date-time-picker-list'
  /**
   *@desc
   *@author  冯朝晖
   *@date    2018.12.6
   */
  export default {
    components: {
      TyDateTimePickerList
    },
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '日期时间选择列表',
            fullModel: {
              dataTimeRage: ['2018-12-11 00', '2018-12-20 10'],
              timeIntervals: [['', ''], ['', '']]
            }
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    rules: {
                      timeIntervals: [
                        {type: 'dateTimeRequired', message: '请选择时间间隔', trigger: 'blur'},
                        {type: "dateTimeDuration", message: '时间间隔必须满足12小时以内', trigger: 'blur'},
                        {type: 'dateTimeOverlap', message: '请确保选择的时间间隔不重叠', trigger: 'blur'},
                        {type: 'dataTimeExistInDataRange', message: '申请夜间施工时间：范围不能超出本次施工的计划起止时间', dependField: 'dataTimeRage',trigger: 'blur'},
                        {type: 'dataDurationInNight', message: '申请夜间施工时间,需在夜间22:00-6:00之间', trigger: 'blur'}
                      ],
                      dataTimeRage: [
                        {type: 'dateRangeRequired', message: '请选择时间间隔', trigger: 'blur'},
                        {type: 'dataRangeIncludeDataTime', affectField: 'timeIntervals', trigger: 'blur'}
                      ]
                    },
                    //Form扩展配置
                    extend: {
                      labelWidth:200,
                      span:12,
                    },
                    items:
                      [ {
                          type: 'dateTimeRange',
                          label: '本次施工的计划起始时间',
                          prop: 'dataTimeRage',
                          extend: {
                            placeholder: '填写申请施工的起始年、月、日、时信息至结束时的年、月、日、时信息',
                            format: 'yyyy-MM-dd HH:00',
                            itemWidth: '',
                          }
                      }, {
                          type: 'dateTimeRangeList',
                          label: '申请夜间施工时间',
                          prop: 'timeIntervals',
                          extend: {
                            maxNum: 2,
                            marginTop: '10px',
                            child: {
                              dependField: 'dataTimeRage',
                              itemWidth: '',
                              disabledDateOptions: 'outOfDateRange',
                              format: 'yyyy-MM-dd HH:00',
                              placeholder: '填写申请施工的起始年、月、日、时信息至结束时的年、月、日、时信息'
                            }
                          }
                        },
                      ]
                  }
                },
              ]
            }
          ]
        }
      }
    },
    methods: {
    }
  }
</script>
`;
export default Code;



