<template>
  <div v-if="isShow">
    <div class="ty-content-box-item" v-if="type==='title'">
      <ty-content-element-title v-bind="data" :fullModelData="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='steps'">
      <ty-steps v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='steps-h5'">
      <ty-steps-h5 v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='form'">
      <ty-content-element-form v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='tip-adv'">
      <ty-content-element-tip-adv v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='tip-alert'">
      <ty-content-element-tip-alert v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='table-attach-file'">
      <ty-attach-file-table v-bind="data" :fullModel="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='extend-table'">
      <ty-extend-table v-bind="data" v-on:extendTableData="extendTableData" :fullModel="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='time-line'">
      <ty-time-line v-bind="data" :fullModel="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='time-line-h5'">
      <ty-time-line-h5 v-bind="data" :fullModel="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='info-base'">
      <ty-cells v-bind="data" :fullModel="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='info-attachment'">
      <ty-attachment v-bind="data" :fullModel="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='pap-download-scan'">
      <ty-pap-download-scan v-bind="data" :fullModel="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='payment'">
      <ty-payment v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='rebirth-radio'">
      <ty-rebirth-radio v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='list-table'">
      <ty-list-table v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='address-select'">
      <ty-address-select v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='qualification-check'">
      <ty-qualification-check v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='identity-check'">
      <ty-identity-check v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='guide-item'">
      <ty-guide-item v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='table-list'">
      <ty-table v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='btns-oprator'">
      <ty-button-oprator v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='iframe'">
      <ty-content-element-iframe v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='table-retire'">
      <ty-content-element-table-retire v-bind="data"/>
    </div>
    <div class="ty-form-Table" v-else-if="type==='form-table'">
      <ty-form-table v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='form-radio'">
      <ty-form-radio v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='lookup-fee'">
      <ty-look-fee v-bind="data"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='mail-licence'">
      <ty-mail-licence v-bind="data" :fullModel="fullModel"/>
    </div>
    <div class="ty-property-search" v-else-if="type==='ty-property-search'">
      <ty-property-search v-bind="data" :fullModel="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='essential-info'">
      <ty-essential-info v-bind="data"></ty-essential-info>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='agree-check'">
      <Row>
        <Col span="11" offset="8">
        <ty-agree-check v-bind="data"></ty-agree-check>
        </Col>
      </Row>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='agree-commitment'">
      <ty-agree-commitment v-bind="data"></ty-agree-commitment>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='form-org-info'">
      <ty-form-org-info/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='remote-select'">
      <ty-remote-select v-bind="data" :fullModel="fullModel"/>
    </div>
    <div class="ty-content-box-item" v-else-if="type==='show-page-table'">
      <ty-show-page-table v-bind="data" ></ty-show-page-table>
    </div>
    <div class="ty-content-box-item" v-if="type==='initRequestData'">
      <ty-form-init-request-data v-bind="data"></ty-form-init-request-data>
    </div>
  </div>
</template>

<script>
  import TySteps from "../steps";
  import TyStepsH5 from "../steps-h5";
  import TyContentElementTipAdv from "./content-elements/content-element-tip/content-element-tip-adv";
  import TyContentElementTitle from "./content-elements/content-element-title";
  import TyContentElementForm from "./content-elements/content-element-form/content-element-form";
  import TyContentElementTipAlert from "./content-elements/content-element-tip/content-element-tip-alert";
  import TyAttachFileTable from "../attach-file-table";
  import TyTimeLine from "../time-line";
  import TyTimeLineH5 from "../time-line-h5";
  import TyMailLicence from "../mail-license/mail-licence";
  import TyCells from "../cells";
  import TyAttachment from "../attachment";
  import TyPapDownloadScan from "../pap-download-scan";
  import TyPayment from "../payment";
  import TyRebirthRadio from "../special-radio";
  import TyListTable from "../../components/list/list-table"
  import TyAddressSelect from "../../components/address-select"
  import TyQualificationCheck from "../../components/qualification-check"
  import TyIdentityCheck from "../../components/identity-check"
  import TyGuideItem from "../guide-item";
  import TyTable from "../table";
  import TyClassify from "../classify-list/classify";
  import TyContentElementIframe from "./content-elements/content-element-iframe";
  import TyContentElementTableRetire from "./content-elements/content-elements-table/content-element-table-retire";
  import TyExtendTable from "../extend-table"
  import TyFormTable from "../form-table/formTable"
  import TyFormRadio from "./content-elements/content-element-radio"
  import TyLookFee from "./content-elements/content-element-lookup/content-element-lookup-fee"
  import TyPropertySearch from "../propertySearch"
  import TyEssentialInfo from "../essentialInfo";
  import TyAgreeCheck from "../agreeCheck"
  import TyAgreeCommitment from "../agreeCommitment"
  import TyFormOrgInfo from "./content-elements/content-element-org-info";
  import TyRemoteSelect from "../remoteSelect"
  import TyShowPageTable from "../show-page-table"
  import TyFormInitRequestData from "../form-init-RequestData"
  /**
   *@desc    内容元素
   *@author  谢辉
   *@date    2018.9.4
   */
  import {realModel} from "./mixin-page-template";

  export default {
    mixins: [realModel],
    name: "ty-content-box-item",
    components: {
      TyContentElementTableRetire,
      TyContentElementIframe,
      TyClassify,
      TyTable,
      TyGuideItem,
      TyPayment,
      TyPapDownloadScan,
      TyAttachment,
      TyCells,
      TyTimeLine,
      TyTimeLineH5,
      TyAttachFileTable,
      TyContentElementTipAlert,
      TyContentElementForm,
      TyContentElementTitle,
      TyContentElementTipAdv,
      TySteps,
      TyStepsH5,
      TyListTable,
      TyAddressSelect,
      TyRebirthRadio,
      TyExtendTable,
      TyFormTable,
      TyFormRadio,
      TyLookFee,
      TyMailLicence,
      TyPropertySearch,
      TyEssentialInfo,
      TyAgreeCheck,
      TyAgreeCommitment,
      TyQualificationCheck,
      TyIdentityCheck,
      TyFormOrgInfo,
      TyRemoteSelect,
      TyShowPageTable,
      TyFormInitRequestData
    },
    props: {
      type: {
        type: String,
        default: ''
      },
      state: {
        type: String,
      },
      data: {
        type: Object,
        default() {
          return {}
        }
      },
      prop: {
        type: String
      }
    },
    methods: {
      extendTableData: function (data) {
        this.fullModel[this.prop] = data
      }
    },
    mounted() {
      if (this.type === 'give-date') {
        this.fullModel[this.prop] = moment(Date.now()).format('YYYY-MM-DD')
      }
    },
    computed: {
      isShow() {
        if (this.state) {
          return this.state === this.fullModel.state;
        }
        return true;
      }
    }
  }
</script>

<style scoped>

</style>



