<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     输入框
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-form-input",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '输入框'
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //双向绑定数据对象
                    model: {
                      normalInput: '输入框',
                    },
                    //规则
                    rules: {
                      normalInput: [
                        {required: true, message: 'Please fill in the user name', trigger: 'blur'}
                      ],
                    },
                    //Form扩展配置
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'input',
                          label: '普通输入框 type:input',
                          prop: 'normalInput',
                          extend:{
                            placeholder: '请输入采购单名称',
                            span:12
                          }
                        },
                        {
                          type: 'input',
                          label: '密码输入框 type:input  item-type:password',
                          prop: 'pwdInput',
                          extend: {
                            type: 'password',
                            placeholder: '请输入采购单名称',
                          }
                        },
                        {
                          type: 'input',
                          label: '多行文本输入框 type:input  item-type:textarea',
                          prop: 'mulInput',
                          extend: {
                            placeholder: '请输入采购单名称',
                            type: 'textarea',
                            rows: 2
                          }
                        },
                      ]
                  }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



