<template>
  <div>
    <RebirthRadio :current="current" :rebirthStepData="rebirthStepData"></RebirthRadio>
  </div>
</template>

<script>
  import RebirthRadio from '../../../components/special-radio'
  export default {
    name: "user-info",
    components: {RebirthRadio},
    data(){
      return {
        current:0,
        rebirthStepData: [
          {content:'夫妻生育两个子女中有子女经医学鉴定为病残儿或者因伤致残'},
          {content:'夫妻再婚前双方合计生育两个或以上子女，再婚后未生育子女'},
          {content:'再婚前一方生育过一个子女，另一方未生育过，再婚后已生育一个子女'},
          {content:'夫妻未生育依法收养两个子女后又怀孕'}
        ]
      }
    }
  }
</script>



