<template>
  <div v-bind="$props">
    <Row class="processHand" v-for="(process,index) in processList" :key="index">
      <Col class="handLeft" span="2">
          <div class="leftContent">{{process.handTitle}}</div>
      </Col>
      <Col span="22" class="handRight">
        <img   @click = "handleView(process.urlRoute)" :src="process.urlRoute" style="height: 370px;"/>
        <!--<img src="https://shewei.oss-cn-beijing.aliyuncs.com/CoreResrcFile/201810/16_195537_C38F8460C69E4075A83A41F411E16CA4_(网上流程图).jpg" style="height: 370px"/>-->
        <!--<ul ref="handRight">-->
          <!--<li v-for="(item,index) in process.handList" :key="index">{{index+1}}、{{item.handMsg}}</li>-->
        <!--</ul>-->
        <!--<template v-if="process.extend">-->
          <!--<ty-content-element-lookup-map v-if="process.extend.type==='lookup-map'"/>-->
          <!--<ty-content-element-lookup-fee v-if="process.extend.type==='lookup-fee'"/>-->
        <!--</template>-->
      </Col>
    </Row>

    <div ref="images" v-viewer.static="{navbar: 0,title: funTitle}" v-show="false">
      <img :src="urlShow" style="height: 370px;"/>
    </div>
  </div>

</template>

<script>
    /**
     *@author       王婷婷
     *@date         2018/9/13 16:46
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    import TyContentElementLookupMap from "./content-box/content-elements/content-element-lookup/content-element-lookup-map";
    import TyContentElementLookupFee from "./content-box/content-elements/content-element-lookup/content-element-lookup-fee";
    import TyContentElementLookupSample from "./content-box/content-elements/content-element-lookup/content-element-lookup-sample"
    export default {
        name: "processHand",
        components:{
          TyContentElementLookupMap,
          TyContentElementLookupFee,
          TyContentElementLookupSample
        },
        props:{
        obj:{
          type:Object
        },
        processList:{
          type:Array
        },
      },
        data(){
          return {
            h:'',
            urlShow:''
          }
        },
        methods:{
          funTitle(a) {
            let arr = a.src.split('/');
            let length = arr.length;
            return decodeURI(arr[length - 1]);
          },
          handleView(url) {
            this.urlShow = url
            const viewer = this.$refs.images.$viewer;
            viewer.show()
          }
        },
        mounted(){

        },
        created(){}

    }
</script>

<style scoped>

</style>



