<template>
  <div class="tableFixed">
      <Table stripe :border="border" v-bind="$props" ></Table>
  </div>
</template>
<script>
  export default {
    name: 'tableFixed',
    components:{},
    props: {
      color:{
        type:String
      },
      columns: {
        type: Array,
        default:[]
      },
      data: {
        type: Array,
        default:[]
      },
      border: {
        default: false
      }
    },
    data() {
     return {
     }
    },
    created(){


    }
  }
</script>



