<template>
    <examples-demo title="日期组件 date-picker" desc="" :code="code.formDatePicker">
      <example-page-template-form-date-picker slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateFormDatePicker from "./example-page-template-form-date-picker";

  export default {
    name: "example-page-template-form-date-picker-demo",
    components: {ExamplePageTemplateFormDatePicker, ExamplesDemo},
    data() {
      return {
        code: Code
      }
    }
  }
</script>



