export default class ComponentService {

  //NOTE 办件耗时查询
  static async getAcceptTakeTime(data){
    return ServiceRequest.post('certificate/getAcceptTakeTime',data);
  };

  //NOTE 办结流程完成下载证照
  static async downloadEvidence(data){
    return ServiceRequest.post('certificate/downloadEvidence',data);
  };

  //note 附件下载
  static async downloadFile(data){
    return ServiceRequest.get('file/dowloadFile',data);
  };

  //note 邮寄证照  新增
  static async addOrUpdateMailInfo(data){
    return ServiceRequest.post('post/addOrUpdateMailInfo',data);
  };
  //note 邮寄证照  查询
  static async qryMailInfoDetail(data){
    return ServiceRequest.post('post/qryMailInfoDetail',data);
  };

  //note  动态获取下拉框的值
  static async qrySelectData(url,data){
    return ServiceRequest.post(url,data);
  };

  //note treeSelect组件post查询
  static async treeSelectPost(url,data){
    return ServiceRequest.post(url,data);
  };

  //note treeSelect组件get查询
  static async treeSelectGet(url,data){
    return ServiceRequest.get(url,data);
  };

  //note 外部系统药监局 资格校验接口
  static async selectQualificatoryCheck(data){
    return ServiceRequest.post('/ability/selectQualificatoryCheck',data);
  };

  //note 外部系统药监局 食药系统组织机构的接口
  static async getSysOrganizationBaseList(data){
    return ServiceRequest.post('ability/getSysOrganizationBaseList',data);
  };

  //note 时间线二手房确认按钮
  static async processAccept(data){
    return ServiceRequest.post('certificate/processAccept',data);
  };

  //note 判断是否允许继续事项
  static async queryAllowData(url,data){
    return ServiceRequest.post(url,data);
  };

  //note 展示分页表格
  static async showPageTable(url,data){
    return ServiceRequest.post(url,data);
  };

  // note 页面下发初始化接口获取信息
  static async initRequestDataQuery(url,data) {
    return ServiceRequest.post(url, data);
  };

}



