import {realModel} from './mixin-page-template'
export const showStatus={
  mixins: [realModel],
  props: {
    isShow:{
      type:String
    },
    showFlag:{
      type:[Object,Array]
    },
    showFlagType:{
      type:String
    }
  },
  computed: {
    showStatus(){
      if(this.isShow){
        if(this.fullModel[this.isShow]===true){
          return true
        }else{
          return false
        }
      }else if(this.showFlag instanceof Array){
          let current=this.checkShowFlagArray(this.showFlag,this.showFlagType);
          return current
      }else if(this.showFlag){
        if(this.showFlag.showType&&this.showFlag.showType==='either'){
          let v1=this.handleVal('either');
          return v1;
        }else{
          let v2=this.handleVal('');
          return v2;
        }
      }
      return true
    }
  },
  methods:{
    checkShowFlagArray(array,type) {
      let result;
      if (type === 'either') {
        result = false;
        array.forEach(item => {
          if (this.fullModel[item.key] == item.value) {
            result = true;
          }
        })
      } else {
        result = true;
        array.forEach(item => {
          if (this.fullModel[item.key] != item.value) {
            result = false;
          }
        })
      }
      return result;
    },
    handleVal(sign){
      let val=this.showFlag.value,val1=this.fullModel[this.showFlag.key];
      switch (sign){
        case "either":
          let flag1=true;
          if(val.indexOf(",")!==-1){
            let splitVal=val.split(",");
            splitVal.forEach(item=>{
              if(val1==item){
                flag1= false
              }
            })
            return flag1;
          }else{
              if(val==val1){
                return false
              }
              return true
          }
          break;
        default:
          let flag2=false;
          if(val.indexOf(",")!==-1){
            let splitVal=val.split(",");
            splitVal.forEach(item=>{
              if(val1==item){
                flag2= true
              }
            })
            return flag2;
          }else{
            if(val==val1){
              return true
            }
            return false
          }
          break;
      }
    }
  }
};



