<template>
  <div class="ty-content-element-lookup">
    <div class="ty-content-element-lookup-fee">
      <a class="charge-standard" @click="open" v-show="imgUrl!= '' ||imgList.length != 0">查看材料样本</a>
      <Modal
        v-if="imgUrl != ''"
        title="Title"
        v-model="show"
        width="500px"
        class-name="i-picture">
        <img :src="imgUrl" class="img"/>
      </Modal>

      <!--示例图片的模态框-->
      <Modal class="demoPic" v-model="show" title="示例图片" width="500px" v-else-if="imgList.length != 0">
        <!--<img :src="demoPic" alt="" width="500px" height="500px">-->
        <el-carousel trigger="click" arrow="always" height="500px" width="500px">
          <el-carousel-item v-for="(item,index) in imgList" :key="index">
            <img :src="item" alt="" width="500px" height="500px">

          </el-carousel-item>
        </el-carousel>
      </Modal>
    </div>
  </div>
</template>

<script>

  /**
   *@author       谢辉
   *@date         2018/9/7 01:00
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description  查看收费标准按钮
   */
  export default {
    name: "ty-content-element-lookup-sample",
    props: {
      imgUrl: {
        type: String,
        default () {
          return ''
        }
      },
      imgList : {
        type: Array,
        default () {
          return []
        }
      }
    },
    data() {
      return {
        show:false
      }
    },
    methods: {
      open() {
        this.show=true
      },
    },
    created(){
      console.log(this.imgList)
    }
  }
</script>
<style lang="less">
  .content-model1 {
    .content-title {
      color: #333;
    }
  }

  .content-model {
    display: inline-block;
    width: 355px;
    .content-title {
      color: #333;
    }
  }
  .i-picture{
    .ivu-modal {
       top: 50px;
     }
    .ivu-modal-header{
      display: none;
    }
    .ivu-modal-footer{
      display: none;
    }
    .ivu-modal-body{
      width: 500px;
      height:500px;
      margin-bottom: 91px;
      padding: 0;
      .img {
        width: 100%;
        height: 100%;
      }
    }
  }
</style>



