<template>
  <div class="cells">
    <div v-for="(item,index) in cellData" :key="index" v-show="showStatus(item.showFlag,item.showFlagType)">
      <div class="cellHeader" v-show="item.showHeader">
        <p>
          {{item.headerTitle}}
          <span v-if="item.moreTitle=='date'">
               集体合同签订时间:{{moment(new Date(fullModel[item.moreTitle])).format("YYYY年MM月DD日")}}
          </span>
        </p>
      </div>
      <Row>
        <template v-if="item.same || item.ergodic">
          <Col v-for="(cell,index) in init" :key="index" :span="cell.span?cell.span:_.divide(24,item.col)">
            <ty-cell-item-child :child="cell" :infoData="fullModel"></ty-cell-item-child>
          </Col>
        </template>
        <template v-else>
          <Col v-for="(cell,index) in item.data" :key="index" :span="cell.span?cell.span:_.divide(24,item.col)" >
          <ty-cell-item v-bind="cell" :infoData="fullModel" :span="cell" ></ty-cell-item>
          </Col>
        </template>
      </Row>
      <div class="cellFooter" v-show="item.showFooter">
        <p v-if="realShow">
          参考费用：{{fullModel.fee|money}}元
        </p>
        <p v-else>
          支付金额：{{fullModel.fee|money}}元
        </p>
    </div>
    </div>
    <div v-if="arrow">
      <Icon type="ios-arrow-up" size="18" color="darkgray" v-show="flag===false" @click="up" />
      <Icon type="ios-arrow-down" size="18" color="darkgray"  v-show="flag===true" @click="down" />
    </div>
    <div class="cellFooter" v-show="footer">
      <p v-if="realShow">
        参考费用：{{fullModel.fee|money}}元
      </p>
      <p v-else>
        支付金额：{{fullModel.fee|money}}元
      </p>
    </div>
  </div>
</template>
<script>
  import TyCellItemChild from "./cell-item-child";
  import TyCellItem from "./cell-item";
  export default {
    name: "ty-cells",
    components: {TyCellItem,TyCellItemChild},
    props: {
      footer:{
        type:Boolean,
        default:false
      },
      arrow:{
        type:Boolean,
        default:true
      },
      basicInfo:{
        type:Object,
      },
      //note 全量绑定数据传递
      fullModel:{
        type:[Object, Array]
      },
      //note 绑定数据参数,默认$root,绑定fullModel,可自行设定绑定参数,
      //比如设置 second则绑定fullModel.second对象
      model:{
        type:[String,Object],
        default:'$root',
      }
    },
    computed:{
      baseData(){
          return this.basicInfo;
      },
      realShow(){
        if(this.fullModel.state==='0' ||this.fullModel.state==='34' ||this.fullModel.state==='23' ||this.fullModel.state==='1' ||this.fullModel.state==='2'||this.fullModel.state==='3'||this.fullModel.state==='4'||this.fullModel.state==='330'||this.fullModel.state==='211'||this.fullModel.state==='311'||this.fullModel.state==='210'||this.fullModel.state==='310'||this.fullModel.state==='390'){
            return true
        }else{
            return false
        }
      },
      init(){
        this.fullData=this.fullModel.templateCache;
        if(this.fullData){
          if(this.fullData.tableExact){
            let columns=this.fullData.tableExact.columns;
            let data=this.fullModel[this.fullData.tableExact.data];
            data.forEach(item=>{
              columns.forEach(val=>{
                this.dataArray.push({title:val.title,value:item[val.prop]});
              })
            })
            //单个枚举值处理
            if(this.fullData.tableExact.label){
              let label=this.fullData[this.fullData.tableExact.label];
              this.dataArray.forEach(item=>{
                label.forEach(val=>{
                  if(item.value==val.value){
                    console.log(val.label);
                    item.value=val.label;
                  }
                })
              })
            }
          }
        }
        return this.dataArray;
      }
    },
    methods:{
      //note 控制cell的显隐
      showStatus(showFlag,showType){
        if(showFlag instanceof  Array){
          let current=this.checkShowFlagArray(showFlag,showType);
          return current
        }
        else if(showFlag){
          if(showFlag.showType&&showFlag.showType==='either'){
            let v1=this.handleVal('either',showFlag);
            return v1;
          }else{
            let v2=this.handleVal('',showFlag);
            return v2;
          }
        }
        return true
      },
      checkShowFlagArray(array,type) {
        let result;
        if (type === 'either') {
          result = false;
          array.forEach(item => {
            if (this.fullModel[item.key] == item.value) {
              result = true;
            }
          })
        } else {
          result = true;
          array.forEach(item => {
            if (this.fullModel[item.key] != item.value) {
              this.realModel[this.prop] = '';
              result = false;
            }
          })
        }
        return result;
      },
      //note 处理数据
      handleVal(sign,showFlag){
        let val=showFlag.value,val1=this.fullModel[showFlag.key];
        switch (sign){
          case "either":
            let flag1=true;
            if(val.indexOf(",")!==-1){
              let splitVal=val.split(",");
              splitVal.forEach(item=>{
                if(val1==item){
                  flag1= false
                }
              })
              return flag1;
            }else{
              if(val==val1){
                return false
              }
              return true
            }
            break;
          default:
            let flag2=false;
            if(val.indexOf(",")!==-1){
              let splitVal=val.split(",");
              splitVal.forEach(item=>{
                if(val1==item){
                  flag2= true
                }
              })
              return flag2;
            }else{
              if(val==val1){
                return true
              }
              return false
            }
            break;
        }
      },
      //note 收起
      up(){
        this.flag=true;
        this.changeData();
      },
      //note 展开
      down(){
        this.cellData=this.cell;
        this.cellData[0].data=this.data1;
        this.flag=false;

      },
      changeData(){
        this.cellData=this.cellData.filter((item,index)=>{
          return index<1;
        })
        this.cellData[0].data=this.cellData[0].data.filter((item,index)=>{
          return index<this.num1;
        })
      },
      getData(){
        this.cell=this.baseData.cells;
        this.cellData=this.baseData.cells;
        this.data1=this.baseData.cells[0].data;
      }
    },
    data(){
      return {
        dataArray:[],
        flag:true,
        cellData:[],
        data1:[],
        cell:[],
        sum:0,
        num:0,
        sum1:0,
        num1:0,
        templateCache:'',
        fullData:''
      }
    },
    created(){
      this.getData();
      this.cellData.forEach(item=>{
          if(this.sum > 48 || this.sum >48 ){
            return false;
          }else{
            item.span? this.sum+=item.span:this.sum+=_.divide(24,this.baseData.cells[0].col);this.num++;
          }
        })
      this.cellData[0].data.forEach(item=>{
        if(this.sum1 === 48 || this.sum1 >48 ){
          return false;
        }else{
          item.span? this.sum1+=item.span:this.sum1+=_.divide(24,this.baseData.cells[0].col);this.num1++;
        }
      })
      this.changeData();
      // this.init();
    }
  }
</script>



