<template>
  <div class="propertySearch">
    <div class="title">{{propertyTitle}}</div>
    <Divider/>
    <div class="tabs-content">
      <div style="margin-bottom: 100px">
        <Button class="btn" :type="item.color" @click="selectSub(item,index)"
                v-for="(item,index) in tabs"
                :key="item.unid">{{ item.attrName.split(".")[0] }}
        </Button>
      </div>
      <!--<div>
        <pdf :src="selectUrl"></pdf>
      </div>-->
      <div class="tab-commit">
        <Button style="margin:15px" @click="downloadSelected">下载该查询结果</Button>
        <Button @click="downloadAll">下载全部查询结果</Button>
      </div>
    </div>
  </div>
</template>

<script>
  /**
   *@author       高琛
   *@date         2018/12/7 16:18
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import Config from "../utils/config.js"

  export default {
    name: "propertySearch",
    props: {
      fullModel: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed: {
      tabs() {
        return this.fullModel.fileUrlZz
      }
    },
    data() {
      return {
        propertyTitle: '查询结果',
        selectUrl: '',
      }
    },
    methods: {
      selectSub(item, index) {
        this.selectUrl = item.fileUrl;
        for (let i = 0; i < this.tabs.length; i++) {
          this.$set(this.tabs[i], 'color', 'default');
        }
        this.$set(this.tabs[index], 'color', 'primary');
        window.open(this.selectUrl)
      },
      down(outUrl) {
        let url = encodeURI(outUrl);
        let len = outUrl.split("/").length;
        let attrName = encodeURI(outUrl.split("/")[len - 1]);
        window.open(encodeURI(Config.api('file/dowloadFile?fileName=' + url + '&realfileName=' + attrName)))
      },
      downloadSelected() {
        if (this.selectUrl) {
          this.down(this.selectUrl)
        } else {
          this.$Notice.error({title: '请选择一项进行下载'})
        }
      },
      downloadAll() {
        for (let item of this.tabs) {
          this.down(item.fileUrl)
        }
      }
    },
    mounted() {
      this.selectUrl = this.tabs[0].fileUrl;
      this.$set(this.tabs[0], 'color', 'primary');
    }

  }
</script>

<style scoped>
  .propertySearch .title {
    font-size: 13px;
    color: rgb(51, 51, 51);
    font-weight: 600
  }

  .propertySearch .tabs-content {
    text-align: center
  }

  .propertySearch .tabs-content .btn {
    margin: 20px;
    width: 250px
  }

  .propertySearch .tabs-content div {
    margin-bottom: 20px
  }

  .propertySearch .tab-commit Button {
    width: 150px
  }
</style>
<style>
  .propertySearch .ivu-divider {
    margin: 15px 0
  }
</style>



