<template>
    <div class="guideInfo1 guideContent clearfix" v-bind="$props">
      <div class="fl guideLeft">
        <p class="title">{{title}}</p>
        <!--<div>-->
          <!--<span class="matter" v-for="(item,index) in matterList" :key="index">-->
          <!--<Icon :type="item.matterType" style="font-size: 14px;color: #e84709" />-->
          <!--<span class="matterMsg">{{item.matterMsg}}</span>-->
        <!--</span>-->
        <!--</div>-->
      </div>
      <div class="fr guideRight clearfix">
        <!--<div class="fl guide-btn-group" v-bind="$props" >-->
          <!--<Button class="matterBtn" :icon="item.icon"  v-for="(item,index) in btnList" :key="index" :type="item.type" ghost @click="transferClick(item.value,item.url)">{{item.label}}</Button>-->
        <!--</div>-->
        <div class="fl rightInfo" v-for="(item,index) in timeList" :key="index">
          <p>{{item.timeTitle}}</p>
          <span class="fee">{{item.timeMsg}}</span>
          <span>{{item.timeUnit}}</span>
        </div>
      </div>
    </div>
</template>
<script>
    export default {
        name: "guideInfo1",
        props:{
            obj:{
              type:Object
            },
            matterList:{
              type:Array
            },
            title:{
              type:String
            },
            timeList:{
              type:Array
            },
            btnList:{
              type:Array
            }
        },
        data(){
          return {
            serviceCode:this.$route.query.id,
          }
        },
        methods:{
          transferClick(val,url){
            this.$router.push({
              name:url,
              query:{
                step: 1,
                serviceCode: this.serviceCode || 'AF33786F4745422BAF81FF80F1AFDACE'
              }
            })
          }
        }
    }
</script>



