<template>
    <ul class="anchorUl clearfix" @click="handleScroll" :class="{isFixed: barFixed, isAbsolute: !barFixed}">
      <li :class="link.className" @click="changeActive(index)"  v-for="(link,index) in anchorList" :key="index">
        <a :href="`#${link.href}`">{{link.title}}</a></li>
    </ul>
</template>

<script>
    /**
     *@author       王婷婷
     *@date         2018/9/17 16:54
     *@Copyright   天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
        name: "ty-anchor-transversely",
        props:{
          obj:{
            type:Object
          },
          anchorList :{
            type:Array
          },
        },
        data(){
        return {
          barFixed:''
        }
      },
      mounted () {
        window.addEventListener('scroll', this.handleScroll)
      },
      destroyed () {
        window.removeEventListener('scroll', this.handleScroll)
      },
      methods:{
        changeActive(index){
          for (let i = 0; i < this.anchorList.length; i++) {
            this.anchorList[i].className='';
          }
          this.anchorList[index].className='active';
        },
        handleScroll(){
          var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
          var id = `${this.anchorList[0].href}span`;
          var offsetTop = document.getElementById(id).offsetTop;
          if (scrollTop > offsetTop) {
            this.barFixed = true
          } else {
            this.barFixed = false
          }
          // document.getElementById(this.anchorList[0].href).scrollIntoView(true);
          // document.scrollBy(0, 50)
        }
      },
      created(){


      }
    }
</script>

<style scoped lang="less">
</style>



