<template>
  <examples-demo title="基本信息 info-base" desc="" :code="code.basicInfo"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-basic-info slot="demo" />
  </examples-demo>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/9/10 11:57
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description  基本信息
     */
    import Code from './code';
    import ExamplesDemo from "../../components/demo";
    import ExamplePageTemplateBasicInfo from "./example-page-template-basic-info";
    export default {
        name: "example-page-template-basic-info-demo",
        components: {ExamplePageTemplateBasicInfo, ExamplesDemo},
        data() {
          return {
            code: Code,
            columns:[
              {
                title: '属性',
                key: 'prop'
              },
              {
                title: '说明',
                key: 'desc'
              },
              {
                title: '类型',
                key: 'type'
              },
              {
                title: '默认值',
                key: 'default'
              }
            ],
            data:[
              {
                prop: 'fullModel',
                desc: '全局绑定对象,',
                type: '*',
                default: '空'
              },
              {
                prop: 'model',
                desc: '无fullModel情况下,给model赋值对象直接绑定model,' +
                '有fullModel情况下,默认$root取fullModel根对象绑定,如果去内部需要设置model,' +
                '例如"a.b.c",就会绑定fullModel.a.b.c',
                type: 'String,Object',
                default: '$root'
              },
              {
                prop:'arrow',
                desc:'箭头的显隐',
                type:'Boolean',
                default:'true'
              },
              {
                prop:'footer',
                desc:'多块区域公用一个footer',
                type:'Boolean',
                default:'false'
              },
              {
                prop:'basicInfo:cells',
                desc:'基本信息中所有模块的集合',
                type:'Array',
                default:'[]'
              },
              {
                prop:'cells:col',
                desc:'此处使用了栅格，24/col决定了每一块所占一行的份数',
                type:'Number',
                default:'空'
              },
              {
                prop:'cells:headerTitle',
                desc:'单个模块的header名称',
                type:'String',
                default:''
              },
              {
                prop:'cells:showHeader',
                desc:'控制单个模块的header显隐',
                type:'Boolean',
                default:'空'
              },
              {
                prop:'cells:showFooter',
                desc:'控制单个模块的footer显隐',
                type:'Boolean',
                default:'空'
              },
              {
                prop:'cells:data',
                desc:'单个模块所有内容',
                type:'Array',
                default:'空'
              },
              {
                prop:'cells:same',
                desc:'针对需要循环的模块 例：212-3事项再生育 ',
                type:'*',
                default:'空'
              },
              {
                prop:'data:title',
                desc:'每块内容的标题',
                type:'String',
                default:'空'
              },
              {
                prop:'data:prop',
                desc:'与fullModel中对应的prop绑定',
                type:'*',
                default:'空'
              },
              {
                prop:'data:label',
                desc:'可选值为 Reform、isOut、date、single、select、table',
                type:'String',
                default:'空'
              },
              {
                prop:'data:itemProp',
                desc:'与label=select结合使用',
                type:'String',
                default:'空'
              },
              {
                prop:'data:unit',
                desc:'针对显示的时候需要增加单位的模块',
                type:'String',
                default:'空'
              }
            ]
          }
        }
    }
</script>

<style scoped>

</style>



