<template>
  <div class="timeLine" v-bind="realModel">
    <Row type="flex">
      <template>
        <!--不予许可-->
        <Col span="6" v-if="realState==='0'||realState==='23'||realState==='34'" class="notPass">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice">
          <Icon type="ios-close-circle" color="#F85555" size="52"/>
          <p class="p1">不予许可</p>
        </div>
        </Col>
        <!--审批中-->
        <Col span="6"
             v-else-if="realState==='1' ||realState==='3'||realState==='330' || realState==='320'||realState==='311'||realState==='390'||realState==='331'||realState==='321'||realState==='204'||realState==='205'||realState==='206'||realState==='207'"
             class="examine">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <p class="p1">提交成功，等待审核！</p>
        <div class="notice">
          <p class="p2">
            <template v-if="fullModel.deal_type=='02'">
              即办件，请稍后
            </template>
            <template v-else>
              <template v-if="fullModel.promiseComptime=='无'">
                承诺一天内办结
              </template>
              <template v-else>
                承诺{{fullModel.promiseComptime}}内办结
              </template>
            </template>
          </p>
          <p class="p2"></p>
        </div>
        <div class="time">
          <Icon type="md-alarm" size="20" color="orange"/>
          <span>已用{{fullModel.days}}个工作日</span>
        </div>
        <div class="methods" v-if="fullModel.portArea">
          <Button type="text" @click="commitMethod(1)">交件方式</Button>
          <Button type="text" @click="commitMethod(2)">取件方式</Button>
        </div>
        </Col>
        <!--缴费-->
        <Col span="6" v-else-if="realState==='4'" class="complete">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice">
          <Icon type="ios-checkmark-circle" color="#2BA738" size="52"/>
          <p class="p1">审核通过</p>
        </div>
        <div class="pay">
          <Button type="warning" ghost @click="pay">去付款</Button>
        </div>
        </Col>
        <!--待办结-->
        <Col span="6" v-else-if="realState==='480'" class="complete">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice">
          <Icon type="ios-checkmark-circle" color="#2BA738" size="52"/>
          <p class="p1">待办结</p>
        </div>
        <div class="methods" v-if="fullModel.portArea">
          <Button type="text" @click="commitMethod(1)">交件方式</Button>
          <Button type="text" @click="commitMethod(2)">取件方式</Button>
        </div>
        </Col>
        <!--办结-->
        <Col span="6"
             v-else-if="realState==='501'||realState==='6'||realState==='5'||realState==='98'||realState==='99'"
             class="handle">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <p class="title">已 办 结</p>
        <div class="notice">
          <p>您的事项已办结</p>
          <!--<p v-if="this.$route.query.name=='单位参保开户'">-->
          <!--单位参保编码:{{accountOpenCode}}-->
          <!--</p>-->
        </div>
        <div class="btn">
          <!--<Button class="bt1" @click="detail">查看发票详情</Button>-->
          <Button class="bt1" @click="payUrl" v-if="fullModel.isYiBaoPay">线上支付</Button>
          <Button class="bt1" @click.native="handleView" v-if="fullModel.isYiBaoPay">线下支付</Button>
          <span v-if="fullModel.zdy_info && fullModel.zdy_info.unitCode">单位参保编码：{{fullModel.zdy_info.unitCode}}</span>
          <Button class="bt2" type="warning" ghost @click="evaluate" :disabled="fullModel.isEvaluate=='1'?true:false">
            {{fullModel.isEvaluate=='1'?"已评价":"去评价"}}
          </Button>
        </div>
        <div class="notice" style="text-align: left" v-if="fullModel.estateMatter">
          <p>地址一：西安市未央区凤城八路保亿隆基大厦1层（029-89241525）</p>
          <p>地址二：西安市雁塔区朱雀大街21号（朱雀云天）5楼（029-85376010）</p>
          <p style="color:red">温馨提示：请您带齐上传材料原件至就近办事大厅办理</p>
        </div>
        <div class="methods" v-if="fullModel.portArea">
          <Button type="text" @click="commitMethod(2)">取件方式</Button>
        </div>
        </Col>
        <!--已上报状态-->
        <Col span="6"
             v-else-if="realState==='450'"
             class="handle">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <p class="title">已 上 报</p>
        <div class="notice">
          <p>您的事项已上报</p>
        </div>
        <div class="btn">
          <Button class="bt1" @click="payUrl" v-if="fullModel.isYiBaoPay">去缴费</Button>
          <Button class="bt2" type="warning" ghost @click="evaluate" :disabled="fullModel.isEvaluate=='1'?true:false">
            {{fullModel.isEvaluate=='1'?"已评价":"去评价"}}
          </Button>
        </div>
        </Col>
        <!--补正补齐-->
        <Col span="6" v-else-if="realState==='210' ||realState==='310'||realState==='202'" class="supplement">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice" color="#F93">
          <Icon type="ios-alert" size="52"/>
          <p class="p1">补齐补正</p>
        </div>
        <div class="perfect">
          <Button type="warning" ghost @click="perfect">去完善信息</Button>
        </div>
        </Col>
        <!--卖家状态码-->
        <Col span="6" v-else-if="realState==='8002'" class="houseFeedback">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1">提交成功，等待反馈</p>
          <p class="p2">您的申请已提交，等待确认方确认中</p>
        </div>
        <div class="time">
          请求号：{{fullModel.askNum}}
        </div>
        </Col>
        <Col span="6" v-else-if="realState==='8003'" class="houseConfirmed">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1">待确认</p>
          <p class="p2">等待申请方进行确认</p>
        </div>
        <div class="time">
          请求号：{{fullModel.askNum}}
        </div>
        <div class="perfect">
          <Button type="warning" ghost @click="sure">确认</Button>
        </div>
        </Col>
        <!--买家状态码-->
        <Col span="6" v-else-if="realState==='8102'" class="houseFeedback">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1">提交成功，等待反馈</p>
          <p class="p2">等待申请方确认中</p>
        </div>
        <div class="time">
          请求号：{{fullModel.askNum}}
        </div>
        </Col>
        <!--预受理-->
        <Col span="6" v-else-if="realState==='201'||realState==='203' " class="ConfirmContract">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1">预受理</p>
        </div>
        </Col>
        <!--受理中-->
        <Col span="6" v-else-if="realState==='2'||realState==='211' " class="ConfirmContract">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1">受理中</p>
        </div>
        </Col>
        <!--受理驳回-->
        <Col span="6" v-else-if="realState==='24'" class="ConfirmContract">
        <span class="code">办件编号:{{fullModel.projId}}</span>
        <div class="notice">
          <p class="p1">受理驳回</p>
        </div>
        </Col>
        <!--无效状态-->
        <Col span="6" class="empty" v-else>
        <span class="code">办件编号:{{fullModel.projId}}</span>
        </Col>
      </template>
      <Col span="16">
      <Timeline :pending="pending">
        <TimelineItem v-for="(item,index) in realModel " :key="index" :color="item.color">
          <!--<p v-html="item.dealTimeStr+'  '+item.operateType+', 处理意见：'+item.content"></p>-->
          <p v-html="item.dealTimeStr+' 处理意见：'+item.content"></p>
        </TimelineItem>
        <div v-if="qrCodeShow===true" class="eBox">
          <!--<img :src="fullModel.qrCode">-->
          <img :src="codeUri">
          <template
            v-if="realState==='501'||realState==='6'||realState==='5'||realState==='98'||realState==='99'">
            <p>事项二维码</p>
          </template>
          <template v-else>
            <p>审批流程跟踪</p>
          </template>
        </div>
        <div class="openMail" @click="changeMailStatus" v-if="sendMailStatus">
          <span>邮寄证照</span>
          <Icon type="ios-arrow-up" v-show="flags===false"/>
          <Icon type="ios-arrow-down" v-show="flags===true"/>
        </div>
      </Timeline>
      </Col>
    </Row>
    <!--note 医疗保险线下支付图片弹出框-->
    <div ref="images" v-viewer="{movable: false}" v-show="false">
      <img src="../assets/img/jm011.jpg" width="500px"></img>
      <img src="../assets/img/jm022.jpg" width="500px"></img>
    </div>
  </div>
</template>
<script>
  import Service from '../service/component-service'
  import ServiceDetail from '../service/home-member-center-evaluate-service'
  import config from '../utils/config'

  export default {
    name: "ty-time-line",
    inject: ['template'],
    props: {
      data: {
        type: Object,
        default() {
          return {}
        }
      },
      step: {
        type: String
      },
      qrCodeShow: {
        type: Boolean
      },
      mailShow: {
        type: Boolean,
        default: false
      },
      pending: {
        type: Boolean,
        default: false
      },
      //note 全量绑定数据传递
      fullModel: {
        type: [Object, Array]
      },
      //note 绑定数据参数,默认$root,绑定fullModel,可自行设定绑定参数,
      model: {
        type: [String, Object],
        default: '$root',
      },
    },
    data() {
      return {
        day: this.fullModel.day,
        flags: true,
        accountOpenCode:''
      }
    },
    computed: {
      //note state状态码
      realState() {
        return this.fullModel.state;
      },
      //note 二维码生成
      codeUri() {
        return config.qrCode(this.fullModel.projId)
      },
      //note 控制邮寄证照按钮是否显示
      sendMailStatus() {
        if (this.mailShow) {
          if (this.realState === '0' || this.realState === '34' || this.realState === '23') {
            return false
          } else {
            return true
          }
        }
        return false
      },
      //note 最终真是绑定对象获取
      realModel() {
        if (this.fullModel === undefined) {
          return {};
        }
        if (typeof(this.model) === 'string') {
          if (this.model === '$root') {
            return this.fullModel;
          }
          let params = this.model.split('.');
          let result = this.fullModel;
          while (params.length > 0) {
            result = result[params.shift()];
          }
          return result;
        } else if (typeof(this.model) === 'object') {
          return this.model;
        } else if (typeof(this.model) === 'array') {
          return this.model;
        }
        return {};
      },
      //note 开始时间
      applyTime() {
        if (this.realModel.length > 0) {
          return this.realModel[0].dealTimeStr
        } else {
          return ''
        }
      },
      //note 结束时间
      handleTime() {
        if (this.realModel.length > 0) {
          return this.realModel[this.realModel.length - 1].dealTimeStr
        } else {
          return ''
        }
      }
    },
    methods: {
      //note 单位参保开户
      accountOpen(){
        if(this.$route.query.name=='单位参保开户'){
          Service.treeSelectPost('certificate/getRSKeyValueInfo',{acceptKey:'info',applyNo:this.fullModel.projId}).then(res=>{
            this.accountOpenCode=res.acceptValue.unitCode;
          })
        }
      },
      //note 支付方式，取件方式点击事件
      commitMethod(val) {
        if (val == 1) {
          window.open(`http://new.zwfw.itl.gov.cn:8888/wdysjj.htm?projId=${this.fullModel.projId}`)
        } else if (val == 2) {
          window.open(`http://new.zwfw.itl.gov.cn:8888/wdysqj.htm?projId=${this.fullModel.projId}`)
        }
      },
      //note 医疗保险去缴费
      payUrl() {
        window.open("http://113.140.18.123/xawt/resident/residentPayAction.do")
      },
      //note 居民医疗保险线下支付图片弹出
      handleView() {
        const viewer = this.$refs.images.$viewer;
        viewer.show()
      },
      //note sure 确认
      sure() {
        Service.processAccept({projId: this.fullModel.projId, originalState: this.fullModel.state}).then(res => {
          let url = window.location.href.split('?')[0] + '?' + `projId=${this.$route.query.projId}&step=${Number(this.$route.query.step) + 1}&serviceCode=${this.$route.query.serviceCode}&name=${this.$route.query.name}`;
          util.navigate({href: url});
        })
      },
      //note 触发mail-licence显隐
      changeMailStatus() {
        this.flags = !this.flags
        Bus.$emit('showMail');
      },
      //note 完善信息
      perfect() {
        let url = window.location.href.split('?')[0] + '?' + `projId=${this.$route.query.projId}&step=${2}&serviceCode=${this.$route.query.serviceCode}&name=${this.$route.query.name}&sceneId=${this.$route.query.sceneId}`;
        util.navigate({href: url});
      },
      //note 支付
      pay() {
        Bus.$emit('nextStep')
      },
      //note 查看详情
      detail() {
        this.$router.push({
          name: 'home',
          query: {...this.params}
        })
      },
      //note 评价
      evaluate() {
        //note 列表传评价状态 status
        if (this.$route.query.status) {
          //列表传来status“0”，未评价
          if (this.$route.query.status == '0') {
            this.$router.push({
              name: 'home-member-center-evaluate-want',
              query: {
                projId: this.$route.query.projId,
                serviceName: this.$route.query.name,
                serviceCode: this.$route.query.serviceCode,
                step: this.$route.query.step,
                areaCode: this.$route.query.areaCode,
                sceneId: this.$route.query.sceneId,
                applyTime: this.applyTime,
                handleTime: this.handleTime
              }
            })
            //列表传来status“1”，已评价
          } else if (this.$route.query.status == '1') {
            this.$router.push({
              name: 'home-member-center-evaluate-detail',
              query: {
                projId: this.$route.query.projId,
                isItem: true
              }
            })
          }
          //note 调评价详情服务，传projId
        } else {
          ServiceDetail.getData({
            applyNo: this.$route.query.projId,
          }).then(data => {
            let res = data.date;
            this.$router.push({
              name: 'home-member-center-evaluate-want',
              query: {
                applyNo: res.applyNo,
                itemName: res.itemName,
                startTime: this.applyTime,
                handleTime: res.finishTime,
                itemNo: res.itemNo,
                projId: this.$route.query.projId,
                serviceName: this.$route.query.name,
                serviceCode: this.$route.query.serviceCode,
                step: this.$route.query.step,
                areaCode: this.$route.query.areaCode,
                sceneId: this.$route.query.sceneId
              }
            })

            /*else if(data.date.itemStatus==1){
                            this.$router.push({
                              name: 'home-member-center-evaluate-detail',
                              query: {
                                projId: this.$route.query.projId,
                                isItem:true
                              }
                            })
                          }*/
          });
        }
      },
      //note 初始化跳转
      initJump() {
        if (this.fullModel.step) {
          if (this.$route.query.step != this.fullModel.step) {
            let url = window.location.href.split('?')[0] + '?' + `projId=${this.fullModel.projId}&step=${this.fullModel.step}&serviceCode=${this.fullModel.serviceCode}&name=${this.$route.query.name}`;
            util.navigate({href: url});

          }
        }
      }
    },
    created() {
      this.initJump();
      // this.accountOpen();
    }
  }
</script>



