<template>
  <div>
    <div v-for="(item,index) in realModel[this.prop]">
      <Select v-model="item[extend.key]" style="width:100px" @on-change="selectType($event,index)">
        <Option v-for="i in cardType" :value="i.value" :key="i.value">{{ i.label }}</Option>
      </Select>
      <span v-if="item[extend.key]=='1'">
      <Input v-model="model2[index]" style="width: 100px" @on-change="changeData(index)"/>
      <Select v-model="model3[index]" style="width:50px" @on-change="changeData(index)" clearable>
        <Option v-for="item in numType" :value="item.value" :key="item.value">{{ item.label }}</Option>
      </Select>
      -
      <Input v-model="model4[index]" style="width: 50px" @on-change="changeData(index)"/>
      -
      <Input v-model="model5[index]" style="width: 30px" @on-change="changeData(index)"/>
      -
      <Input v-model="model6[index]" style="width: 50px" @on-change="changeData(index)"/>
      -
      <Input v-model="model7[index]" style="width: 50px" @on-change="changeData(index)"/>
      ～
      <Input v-model="model8[index]" style="width: 30px" @on-change="changeData(index)"/>
    </span>
      <span v-if="item[extend.key]=='2'">
      <Input v-model="model9[index]" placeholder="陕（2019）西安市不动产权第xxx号" style="width: 219px"
             @on-change="changeData(index)"/>
    </span>
      <span v-if="index>=1||index>=extend.minNum">
        <Button @click="del(index)">删除</Button>
      </span>
    </div>
    <div style="margin-top: 10px">
      <Button @click="add">添加</Button>
    </div>
  </div>
</template>

<script>
  /**
   *@author       高琛
   *@date         2019/1/11 16:13
   *@Copyright    天源迪科信息技术股份有限公司
   *@Description
   */
  import {realModel} from "./content-box/mixin-page-template";

  export default {
    name: "",
    mixins: [realModel],
    props: {
      prop: {
        type: [String, Array]
      },
      extend: {
        type: Object,
        default() {
          return {}
        }
      },
    },
    data() {
      return {
        model1: [],
        model2: [],
        model3: [],
        model4: [],
        model5: [],
        model6: [],
        model7: [],
        model8: [],
        model9: [],
        cardType: [
          {
            value: "1",
            label: "房产证号"
          },
          {
            value: "2",
            label: "不动产证号"
          }
        ],
        numType: [
          {
            value: "Ⅰ",
            label: "Ⅰ"
          },
          {
            value: "Ⅱ",
            label: "Ⅱ"
          },
          {
            value: "Ⅲ",
            label: "Ⅲ"
          },
          {
            value: "Ⅳ",
            label: "Ⅳ"
          }
        ]
      }
    },
    methods: {
      add() {
        this.realModel[this.prop].push({[this.extend.key]: "1", [this.extend.prop]: ""})
        console.log(this.realModel[this.prop])
      },
      del(index) {
        this.realModel[this.prop].splice(index, 1);
        this.model2.splice(index, 1);
        this.model3.splice(index, 1);
        this.model4.splice(index, 1);
        this.model5.splice(index, 1);
        this.model6.splice(index, 1);
        this.model7.splice(index, 1);
        this.model8.splice(index, 1);
        this.model9.splice(index, 1);
        Bus.$emit('valiteFormItem', this.prop)
      },
      selectType(val, index) {
        console.log(val, index)
        this.realModel[this.prop][index][this.extend.key] = val;
        this.realModel[this.prop][index][this.extend.prop] = '';
        this.model2[index] = "";
        this.model3[index] = "";
        this.model4[index] = "";
        this.model5[index] = "";
        this.model6[index] = "";
        this.model7[index] = "";
        this.model8[index] = "";
        this.model9[index] = "";
      },
      changeData(index) {
        if (this.realModel[this.prop][index][this.extend.key] == '1') {

          if(!this.model3[index]){
            this.model3[index] = ""
            console.log(this.model3[index])
          }
          console.log(this.model3)
          this.realModel[this.prop][index][this.extend.prop] = this.model2[index] + this.model3[index] + '-' + this.model4[index] + '-' + this.model5[index] + '-' + this.model6[index] + '-' + this.model7[index] + '～' + this.model8[index];
          console.log(this.realModel[this.prop][index][this.extend.prop] )
          Bus.$emit('valiteFormItem', this.prop)
        } else {
          //note 不动产证全角转半角，去空格
          // this.model9[index] = this.ToCDB(this.model9[index])
          this.model9[index] = this.model9[index].replace(/\s*/g, "")
          this.model9[index] = this.model9[index].replace(/\(/g, "（")
          this.model9[index] = this.model9[index].replace(/\)/g, "）")
          this.realModel[this.prop][index][this.extend.prop] = this.model9[index]
          Bus.$emit('valiteFormItem', this.prop)
        }
      },
      //note 全角转换为半角
      // ToCDB(str) {
      //   let tmp = "";
      //   for (let i = 0; i < str.length; i++) {
      //     if (str.charCodeAt(i) == 12288) {
      //       tmp += String.fromCharCode(str.charCodeAt(i) - 12256);
      //       continue;
      //     }
      //     if (str.charCodeAt(i) > 65280 && str.charCodeAt(i) < 65375) {
      //       tmp += String.fromCharCode(str.charCodeAt(i) - 65248);
      //     }
      //     else {
      //       tmp += String.fromCharCode(str.charCodeAt(i));
      //     }
      //   }
      //   return tmp
      // }
    },
    mounted() {
      if (this.realModel[this.prop].length != 0) {
        for (let i = 0; i < this.realModel[this.prop].length; i++) {
          if (!this.realModel[this.prop][i][this.extend.prop]) {
            return
          }
          let key = this.realModel[this.prop][i][this.extend.key];
          if (key == '1') {
            let arr = this.realModel[this.prop][i][this.extend.prop].split('-');
            if (arr[0][arr[0].length - 1] == 'Ⅰ' || arr[0][arr[0].length - 1] == 'Ⅱ' || arr[0][arr[0].length - 1] == 'Ⅲ' || arr[0][arr[0].length - 1] == 'Ⅳ') {
              this.model2[i] = arr[0].substring(0, arr[0].length - 1);
              this.model3[i] = arr[0].charAt(arr[0].length - 1);
            } else {
              this.model2[i] = arr[0].substring(0, arr[0].length);
              this.model3[i] = '';
            }
            this.model4[i] = arr[1];
            this.model5[i] = arr[2];
            this.model6[i] = arr[3];
            this.model7[i] = arr[4].substring(0, arr[4].indexOf("～"));
            this.model8[i] = arr[4].substring(arr[4].indexOf("～") + 1, arr[4].length)
          } else if (key == '2') {
            this.model9[i] = this.realModel[this.prop][i][this.extend.prop]
          }
        }
      } else {
        this.realModel[this.prop].push({[this.extend.key]: "1", [this.extend.prop]: ""})
      }
    }
  }
</script>

<style scoped>

</style>



