<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
  /**
   *@desc     标签
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-form-label",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '页面标题',
            fullModel:{
              normalLabel: '文本框1',
              applyName:'企业名称',
              customerFullName:'',
              applyName1:'企业名称1'
            }
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //双向绑定数据对象
                    // model: {
                    //   normalLabel: '文本框',
                    // },
                    //Form扩展配置
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'label',
                          label: '普通文本框 type:label',
                          prop: 'normalLabel',
                          //Form Item 扩展配置
                          extend: {
                            labelWidth: 180
                          }
                        },
                        {
                          type: "label",
                          label: "企业名称",
                          prop: "applyName",
                          extend: {
                            linkageChangeValue: [
                              {
                                type: "out",
                                changeKey: "customerFullName",
                                noChangeExist: true,
                                values: [
                                  {
                                    changeParam: "applyName"
                                  }
                                ]
                              }
                            ]
                          }
                        },
                        {
                          type: "input",
                          label: "缴款人全称",
                          prop: "customerFullName",
                          extend: {
                            placeholder: "请输入缴款人全称",
                            itemWidth: 322
                          }
                        }
                      ]
                  }
                },
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



