<template>
    <div class="agreeCheck" :style="{marginTop:top+'px'}">
      <template v-if="type==='1'">
        <Checkbox v-model="realModel[prop]" :disabled="dis">我已阅读并同意</Checkbox>
        <a v-for="(item,index) in bookArr" @click="seeBook(index)" :key="index" >《{{item}}》</a>
      </template>
     <template v-else-if="type==='2'">
       <Checkbox v-model="realModel[prop]"></Checkbox>
       <span style="word-wrap:break-word;word-break:break-all; overflow: hidden;">{{text}}</span>
     </template>
      <Modal
        v-model="modal1"
        class-name="agree-modal"
        :mask-closable="false"
        title="Common Modal dialog box title">
        <p class="p1" style="text-align: center">《{{modalTitle}}》</p>
        <p class="p2" style="text-indent:2em; margin-top: 10px" v-html="modalContent"></p>
        <div class="btn">
          <div @click="cancel" class="leftBtn">不同意</div>
          <div @click="sure"
               :class='[ second < waitTime ? "disabled" : "" ,"rightBtn"]'>
            <span v-show="second >= waitTime">
              同意
            </span>
            <span v-show="second < waitTime">
              {{count}}s
            </span>
          </div>
        </div>
      </Modal>
    </div>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/12/4 16:57
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    import {realModel} from "./content-box/mixin-page-template";
    export default {
        name: "agreeCheck",
        mixins:[realModel],
        props:{
          top:{
            type:Number,
            default:0
          },
          book:{
            type:Array
          },
          bookContent:{
            type:Array
          },
          text:{
            type:String
          },
          type:{
            type:String,
            default:"1"
          },
          prop:{
            type:String,
            default:'isAgree'
          }
        },
        watch:{

        },
        data(){
          return{
            //是否同意
            isAgree:false,
            bookArr:this.book,
            content:this.bookContent,
            modal1:false,
            //模态框标题，内容
            modalContent:'',
            modalTitle:'',
            //checkbox是否可选
            dis:true,
            //判断是否够60s可以点击取消
            canCancel:false,
            second:0,
            waitTime:0,
            num:[],
            index:null
          }
        },
        computed:{
          count() {
            return this.waitTime - this.second;
          }
        },
        methods:{
          //note 查看承诺书
          seeBook(index){
            this.index=index;
            this.second=0;
            this.modal1=true;
            let timeout = setTimeout(()=>{
              this.canCancel=true;
              clearTimeout(timeout);
            },this.waitTime*1000)
             this.interval = setInterval(()=>{
                this.second++;
                if (this.second >= this.waitTime) {
                  clearInterval(this.interval);
                }
            },1000)
            this.modalTitle=this.bookArr[index];
            this.modalContent=this.content[index];
          },
          //note 取消
          cancel(){
            this.modal1=false;
            this.canCancel=false;
            this.second=0;
            clearInterval(this.interval);
          },
          //note 确定
          sure(){
              if(this.canCancel===true){
                this.modal1=false;
                this.canCancel=false;
                this.num.push(this.index);
                let arr=new Set(this.num);
                if(this.book.length===arr.size){
                  this.dis=false;
                  this.realModel[this.prop]=true;
                }
              }else{
                util.alert({type: 'warning', content: `阅读时间不足${this.waitTime}s,再过${this.waitTime -this.second}s可以确认`});
              }
          }
        },
        created(){

        }
    }
</script>

<style scoped>



</style>



