<template>
  <div :class="warpClasses" :style="styles" v-show="showStatus">
    <template v-for="item,index in data">
      <ty-content-box-item v-bind="item"/>
    </template>
    <slot name="step"></slot>
    <slot name="form"></slot>
    <slot></slot>
  </div>
</template>

<script>
  import TyContentBoxItem from "./content-box-item";
  /**
   *@desc    内容盒子
   *@author  谢辉
   *@date    2018.9.4
   */
  const prefixCls = 'ty-content-box'
  export default {
    name: "ty-content-box",
    components: {TyContentBoxItem},
    props: {
      minHeight:{
        default:0
      },
      nopadding: {
        default() {
          return false;
        }
      },
      nobackground: {
        default() {
          return false;
        }
      },
      border:{
        default(){
          return 'default'; //'none','default'
        }
      },
      borderColor:'',
      data: {
        type: Array,
        default() {
          return []
        }
      },
      fullModelData:{
        type:Object,
        default() {
          return {}
        }
      },
      isShow:{
        type:String
      },
      showFlag:{
        type:[Object,Array]
      },
      showFlagType:{
        type:String
      }
    },
    computed: {
      styles(){
        let styles={};
        styles.minHeight=`${this.minHeight}px`;
        if(!!this.borderColor){
          styles.borderColor=this.borderColor;
        }
        return styles;
      },
      warpClasses() {
        return [
          `${prefixCls}`,
          {
            [`${prefixCls}-nopadding`]: this.nopadding,
            [`${prefixCls}-nobackground`]: this.nobackground,
            [`${prefixCls}-border-${this.border}`]: this.border
          }
        ];
      },
      showStatus(){
        if(this.isShow){
          if(this.fullModelData[this.isShow]===true){
            return true
          }else{
            return false
          }
        }else if(this.showFlag instanceof Array){
          let current=this.checkShowFlagArray(this.showFlag,this.showFlagType);
          return current
        }else if(this.showFlag){
          if(this.showFlag.showType&&this.showFlag.showType==='either'){
            let v1=this.handleVal('either');
            return v1;
          }else{
            let v2=this.handleVal('');
            return v2;
          }
        }
        return true
      }
    },
    methods:{
      handleVal(sign){
        let val=this.showFlag.value,val1=this.fullModelData[this.showFlag.key];
        switch (sign){
          case "either":
            let flag1=true;
            if(val.indexOf(",")!==-1){
              let splitVal=val.split(",");
              splitVal.forEach(item=>{
                if(val1==item){
                  flag1= false
                }
              })
              return flag1;
            }else{
              if(val==val1){
                return false
              }
              return true
            }
            break;
          default:
            let flag2=false;
            if(val.indexOf(",")!==-1){
              let splitVal=val.split(",");
              splitVal.forEach(item=>{
                if(val1==item){
                  flag2= true
                }
              })
              return flag2;
            }else{
              if(val==val1){
                return true
              }
              return false
            }
            break;
        }
      },
      checkShowFlagArray(array,type) {
        let result;
        if (type === 'either') {
          result = false;
          array.forEach(item => {
            if (this.fullModel[item.key] == item.value) {
              result = true;
            }
          })
        } else {
          result = true;
          array.forEach(item => {
            if (this.fullModel[item.key] != item.value) {
              result = false;
            }
          })
        }
        return result;
      }
    },
    created(){
      // console.log(this.fullModelData);
    }
  }
</script>



