<template>
    <examples-demo title="输入框组件 input" desc="" :code="code.formInput"
                   :table-columns="columns"
                   :table-data="data">
      <example-page-template-form-input slot="demo"/>
    </examples-demo>
</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import ExamplePageTemplateFormInput from "./example-page-template-form-input";

  export default {
    name: "example-page-template-form-input-demo",
    components: {ExamplePageTemplateFormInput, ExamplesDemo},
    data() {
      return {
        code: Code,
        columns:[
            {
              title: '属性',
              key: 'prop'
            },
            {
              title: '说明',
              key: 'desc'
            },
            {
              title: '类型',
              key: 'type'
            },
            {
              title: '默认值',
              key: 'default'
            }
          ],
        data:[
          {
            prop: 'fullModel',
            desc: '全局绑定对象,',
            type: '*',
            default: '空'
          },
          {
            prop: 'model',
            desc: '无fullModel情况下,给model赋值对象直接绑定model,' +
            '有fullModel情况下,默认$root取fullModel根对象绑定,如果去内部需要设置model,' +
            '例如"a.b.c",就会绑定fullModel.a.b.c',
            type: 'String,Object',
            default: '$root'
          },
          {
            prop:'prop',
            desc:'双向绑定某项数据，相当于v-model',
            type:'String',
            default:'空'
          },
          {
            prop:'rules',
            desc:'表单验证规则',
            type:'Object|Array',
            default:'空'
          },
          {
            prop:'extend:type',
            desc:'输入框类型，可选值为 text、password、textarea、url、email、date',
            type:'String',
            default:'空'
          },
          {
            prop:'extend:size',
            desc:'输入框尺寸，可选值为large、small、default或者不设置',
            type:'String',
            default:'空'
          },
          {
            prop:'extend:placeholder',
            desc:'占位文本',
            type:'String',
            default:'空'
          },
          {
            prop:'extend:clearable',
            desc:'是否显示清空按钮',
            type:'String',
            default:'空'
          },
          {
            prop:'extend:disabled',
            desc:'设置输入框为禁用状态',
            type:'String',
            default:'空'
          },
          {
            prop:'extend:readonly',
            desc:'设置输入框为只读',
            type:'String',
            default:'空'
          },
          {
            prop:'extend:maxlength',
            desc:'最大输入长度',
            type:'String',
            default:'空'
          }
        ]

      }
    }
  }
</script>



