<template>
  <div class="show-page-table" v-show="showStatus">
    <div class="table-header" v-show="header">
      <p>{{header}}</p>
    </div>
    <Table :stripe="stripe" ref="table" v-bind="$props" :data=" m_data || realModel"
           :no-data-text="noDataText">
    </Table>
    <Page show-elevator show-total v-show="pagination" class="tr"
          @on-change="handleChangePageNo"
          :current="m_pageNo"
          :page-size="m_pageSize"
          :total="m_total" style="padding:10px 20px;">
    </Page>
  </div>
</template>

<script>
  import {showStatus} from "./content-box/mixin-showStatus";
    /**
     *@author       刘文军
     *@date         2019/1/28 14:32
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
        name: "show-page-table",
        mixins:[showStatus],
        props:{
          columns:{
            type:Array
          },
          header:{
            type:String
          },
          pagination:{
            type:Boolean,
            default:true
          },
          noDataText:{
            default:"暂无数据"
          },
          stripe:{
            type: [Boolean, String],
            default: false
          },
          autoLoad: {
            type: [Boolean, String],
            default: true
          },
          url:{
            type:String,
            default:""
          },
          params:{
            type:Object,
            default(){
              return {}
            }
          },
          pageNo: {
            type: Number,
            default: 1
          },
          //每页显示数据条数
          pageSize: {
            type: Number,
            default: 10,
          },
          queryType:{
            type:String,
            default:""
          },
          mock:{
            type:Boolean,
            default:false
          }
        },
        data(){
            return {
              m_url:this.url,
              m_data:[],
              m_pageNo:this.pageNo,
              m_pageSize:this.pageSize,
              m_total:null
            }
        },
        methods:{
          //note 查询数据
          query(arg) {
            return new Promise((resolve, reject) => {
              if(!!this.m_url) {
                let mixture;
                if(this.fullModel.state==='5'){
                  mixture={applyStatus:"1",projId:this.fullModel.projId};
                }else{
                  mixture={applyStatus:"2",projId:this.fullModel.projId};
                }
                let m_query={pageNo: this.m_pageNo, pageSize: this.m_pageSize};
                let param = _.assign(this.params,m_query,mixture,arg || {})
                this.axios.post(appConfig.api(this.m_url,this.queryType,this.mock),param)
                  .then(res => {
                    this.m_pageNo=res.pageNo;
                    this.m_pageSize=res.total;
                    this.m_total=res.recordsTotal;
                    this.m_data=res.rows;
                    resolve(res.rows)
                  })
                  .catch(error => {
                    console.log(error)
                  });
              } else {
                alert('查询列表数据url不能为空!')
              }
            })
          },
          //note 分页
          handleChangePageNo(val){
            this.m_pageNo=val;
            this.query({pageNo: this.m_pageNo}).then((data) => {
              this.m_data = data;
            })
          }
        },
        created(){
          if(this.autoLoad){
            this.query();
          }
        }
    }
</script>

<style scoped lang="less">
  .show-page-table{
    border: 1px solid #CCCCCC;
    padding: 12px 16px 12px 16px;
    .table-header{
      text-align: left;
      margin-top: 10px;
      height: 30px;
      line-height: 30px;
      background: #f2f2f2b3;
      margin-bottom: 15px;
      p{
        padding-left: 18px;
        font-size: 15px;
        color: #4A89DC;
      }
    }
    .ivu-page {
      .ivu-page-total{
        color: #999;
      }
      .ivu-page-item-active{
        background: #4a89dc !important;
        a {
          color: #fff !important;
        }
      }
    }
  }
</style>



