<template>
  <div class="user-form">
      <Row class="user-row" v-for="(item,index) in setList" :key="index">
        <Col span="6" class="userLeft" style="margin-top: 2px">
          <Icon :type="item.iconType" size="36" :color="item.userColor" style="font-weight:500"/>
          <!--<Icon type="ios-information-circle-outline"  v-if="item.isBind" size="36" :color="item.userColor"/>-->
          <!--<Icon type="ios-checkmark-circle-outline" size="36" v-else />-->
        </Col>
        <Col span="12" class="userCenter">
          <p class="user-title">{{item.userTitle}}</p>
          <p class="user-msg">{{item.userMsg}}</p>
        </Col>
        <Col span="6" class="userRight" style="margin-top: 2px">
          <!--<Button @click="href(item.url,item.editType)" v-if="item.isBind" type="warning">{{item.userOper}}</Button>-->
          <Button @click="href(item.url,item.editType)" v-if="item.isStop" :disabled=item.isStop?true:false type="error">{{item.userOper}}</Button>
          <a @click="href(item.url,item.editType,item.isPersonal,item.isValidate,item.isLook)" v-else>{{item.userOper}}</a>
        </Col>
      </Row>
  </div>
</template>

<script>
  import Service from '../service/home-member-center-verification'

  /**
     *@author       王婷婷
     *@date         2018/9/18 20:18
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
      name: "user-form",
      props:{
        obj:{
          type:Object
        },
        setList:{
          type:Array
        }
      },
      data () {
        return {
          isId:true,
          isEmail:true,
          baseInfo:{
            userName:'',
            cellPhone:'',
            email:''
          }
        }
      },
      methods:{
        href(url,editType,isPersonal,isValidate,isLook){
          if(isLook){
            //认证不认证，查看不查看
            if(isPersonal){
              // 自然人
              // if(isValidate){
              //   this.$router.push({
              //     name:'home-member-center-identityInfo',
              //     query:{
              //       userName:this.baseInfo.userName,
              //       tel:this.baseInfo.cellPhone,
              //       email:this.baseInfo.email,
              //       isId:this.isId,
              //       isEmail:this.isEmail,
              //       isPersonal:true
              //     }
              //   })
              // }else {
                this.$router.push({
                  name:'home-member-center-verification-completion',
                  query:{
                    userName:this.baseInfo.userName,
                    tel:this.baseInfo.cellPhone,
                    email:this.baseInfo.email,
                    isId:this.isId,
                    isEmail:this.isEmail,
                    isPersonal:true
                  }
                })
              // }
            }else {
              // 法人
              if(isValidate){
                this.$router.push({
                  name:'home-member-center-identityInfo',
                  query:{
                    userName:this.baseInfo.userName,
                    tel:this.baseInfo.cellPhone,
                    email:this.baseInfo.email,
                    isId:this.isId,
                    isEmail:this.isEmail,
                    isPersonal:false
                  }
                })
              }else {
                this.$router.push({
                  name:'home-legal-identiy',
                  query:{
                    userName:this.baseInfo.userName,
                    tel:this.baseInfo.cellPhone,
                    email:this.baseInfo.email,
                    isId:this.isId,
                    isEmail:this.isEmail,
                    isPersonal:false
                  }
                })
              }
            }
          }else {
            this.$router.push({
              name:url,
              query:{
                editType:editType,
                userName:this.baseInfo.userName,
                tel:this.baseInfo.cellPhone,
                email:this.baseInfo.email,
                isId:this.isId,
                isEmail:this.isEmail
              }
            });
          }



        },
        getPersonIQuery(){
          Service.getPersonIQuery({}).then(data=>{
            this.isId=data.caLevelFlag=='1'?true:false;
            this.isEmail=data.emailFlag=='1'?true:false;
            this.baseInfo.userName=data.loginName;
            this.baseInfo.cellPhone=data.cellPhone;
            this.baseInfo.email=data.email;
          })
        },
      },
      created(){
        this.getPersonIQuery();
      }
    }
</script>

<style scoped>

</style>



