<template>

</template>

<script>
    /**
     *@author           lijie
     *@date             2019/1/30 14:34
     *@Copyright       天源迪科信息技术股份有限公司
     *@Description
     */
    import {realModel} from './content-box/mixin-page-template'
    import Service from "../service/component-service"
    export default {
        name: "form-init-RequestData",
        mixins:[realModel],
        props:{
          url: {
            type: String,
            default() {
              return ""
            }
          },
          params:{
            type: Object,
            default() {
              return {}
            }
          },
          responseData:{
            type: Object,
            default() {
              return {}
            }
          }
        },
        created(){
          let paramsData = {};
          for(let key of Object.keys(this.params)){
            paramsData[key] = this.realModel[this.params[key]];
          }
          Service.initRequestDataQuery(this.url,paramsData).then(res => {
              for(let key of Object.keys(this.responseData)){
                this.realModel[key] = res[this.responseData[key]];
              }
          });
        }
    }
</script>

<style scoped>

</style>



