<template>
  <ty-page-template :data="pageData"/>
</template>
<script>
  /**
   *@desc
   *@author  刘文军
   *@date  2018.12.15
   */
  export default {
    name:"example-essential-info",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: 'select',
            fullModel: {
              "askNum":"",
              "rasonChange":"",
              "makeCode":"",
              "sellName":"",
              "sellCardType":"",
              "sellCardNumber":"",
              "sellAddress":"",
              "sellPhone":"",
              "buyName":""

            }
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  "type": "essential-info",
                  "data":{
                    "infoData":[
                      {
                        "title": "申请信息",
                        "data": [
                          {
                            "title": "监管协议号",
                            "prop": "askNum"
                          },
                          {
                            "title": "变更原因",
                            "prop": "rasonChange"
                          },
                          {
                            "title": "收款人身份证号",
                            "prop": "makeCode"
                          }
                        ]
                      }
                    ]
                  }
                },
                {
                  "type": "essential-info",
                  "data":{
                    "infoData":[
                      {
                        "title": "资金监管协议",
                        "data": [
                          {
                            "title": "卖方姓名",
                            "prop": "sellName"
                          },
                          {
                            "title": "卖方证件类型",
                            "prop": "sellCardType"
                          },
                          {
                            "title": "卖方证件号码",
                            "prop": "sellCardNumber"
                          },
                          {
                            "title": "通讯地址",
                            "prop": "sellAddress"
                          },
                          {
                            "title": "卖方联系电话",
                            "prop": "sellPhone"
                          },
                          {
                            "title": "买方姓名",
                            "prop": "buyName"
                          },
                          {
                            "title": "买方证件类型",
                            "prop": "buyCardType"
                          },
                          {
                            "title": "买方证件号码",
                            "prop": "buyCardNumber"
                          }
                        ]
                      }
                    ]
                  }
                }
              ]
            }
          ]
        },
      }
    },
    methods: {
    }
  }
</script>



