
<template>
  <div>
    <Row>
      <Col span="24">
        <Divider orientation="left">{{title}}</Divider>
      </Col>
      <Col span="24" class="examples-desc">
        <p>{{desc}}</p>
      </Col>
      <Col span="24" class="examples-demo">
        <slot name="demo"></slot>
      </Col>
      <Col span="24" class="examples-code">
        <highlight-code lang="vue">{{code}}</highlight-code>
      </Col>
      <Col span="24">
        <Table class="example-table" stripe :columns="tableColumns" :data="tableData" :border="true"></Table>
      </Col>
      <Col span="24">
        <slot></slot>
      </Col>
    </Row>
  </div>
</template>
<script>
  export default{
    name:'examples-demo',
    props:{
      title:'',
      desc:'',
      code:'',
      tableColumns:{
        default(){
          return []
        }
      },
      tableData:{
        default(){
          return []
        }
      }
    }
  }
</script>
<style lang="less">
  .examples-desc {
    margin: 10px 0;
    background-color: #F7F7F7;
    >p{
      font-size: 14px;
      padding: 5px;
    }
  }
  .examples-code{
    .examples-desc()
  }
  .examples-demo{
    padding: 5px;
  }
  pre{
    font-size: 14px;
    padding: 15px !important;
  }
  .ivu-divider-inner-text{
    font-size: 18px;
  }
  .ivu-divider-horizontal.ivu-divider-with-text-left:before {
    width:2%;
  }
</style>



