                  "content": "发布1.0版本"
                },
                {
                  "color": "green",
                  "dealTimeStr": "2018-05-05 14:22:10",
                  "workerName": "张三",
                  "operateType": "流转",
                  "content": "发布1.0版本"
                },
                {
                  "color": "red",
                  "dealTimeStr": "2018-05-05 14:22:10",
                  "workerName": "张三",
                  "operateType": "处理",
                  "content": "发布1.0版本"
                },
                {
                  "color": "blue",
                  "dealTimeStr": "2018-05-05 14:22:10",
                  "workerName": "张三",
                  "operateType": "派发",
                  "content": "发布1.0版本"
                }
              ]
            }
          },
          //页面元素数据
          "elements": [
            {
              "type": "box",
              "data": [
                {
                  "type": "time-line-h5",
                  "data": {
                    "model": "timeLineData",
                    "qrCodeShow": true,
                    "pending": false,
                    "mailShow": false
                  }
                }
              ]
            }
          ]
        }
      }
    }
  }
</script>



