<template>
  <ty-page-template :data="pageData"/>
</template>
<script>
  import TyDateTimePicker from '../../../components/date-time-picker/date-time-picker'
  /**
   *@desc
   *@author  冯朝晖
   *@date  2018.12.6
   */
  export default {
    components: {
      TyDateTimePicker
    },
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '日期时间选择',
            fullModel: {
              dataTimeRage: []
            }
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    rules: {
                    },
                    //Form扩展配置
                    extend: {
                      labelWidth:200,
                      span:12,
                    },
                    items:
                      [ {
                        type: 'dateTimeRange',
                        label: '本次施工的计划起始时间',
                        prop: 'dataTimeRage',
                        extend: {
                          placeholder: '填写申请施工的起始年、月、日、时信息至结束时的年、月、日、时信息',
                          format: 'yyyy-MM-dd HH',
                          itemWidth: '',
                        }
                      }]
                  }
                },
              ]
            }
          ]
        },
      }
    },
    methods: {
    }
  }
</script>



