export default class HomeMemberCenterEvaluateService {

  //note 获取信息
  static async getData(data){
    return ServiceRequest.post('evaluateAccept/qryEvaluateDetail',data);
  }
  //note 新增评价
  static async addEval(data){
    return ServiceRequest.post('evaluateAccept/addEvaluate',data);
  }
}



