<template>
  <div class="foot-tip-msg">
    <Icon type="ios-alert-outline" size="24" color="#FF9933"/>
    <span class="reminder">{{title||realModel.title}}</span>
    <p class="foot-msg">收费类型：{{feeType||realModel.feeType}}</p>
    <p class="foot-msg">收费标准：{{feeType||realModel.feeScale}}{{feeUnit||realModel.feeUnit}}</p>
    <p class="foot-tip">此为参考费用，最终费用由审核人员确认之后再定</p>
    <div class="foot-money">
      <span class="foot-msg">参考费用：</span>
      <span class="money">{{fee||amountFee | money}}元</span>
    </div>
  </div>
</template>

<script>
  /**
   *@desc    内容元素-广告事项-温馨提示
   *@author  谢辉
   *@date    2018.9.4
   */
  import {realModel} from "../../mixin-page-template";

  export default {
    mixins:[realModel],
    name: "ty-content-element-tip-adv",
    props:{
      //note 总线事件名称
      eventType:{
        type:String,
      },
      //note 应用场景类型 高新:gaoxin 市基础:city 默认高新
      type:{
          type:String,
          default:'gaoxin'
        },
      //标题
      title:{
        type:String
      },
      //收费类型
      feeType:{
        type:String
      },
      //收费标准
      feeScale:{
        type:String
      },
      //单位
      feeUnit:{
        type:String
      },
      //总价
      fee:{
        type:String
      }
    },
    computed:{
      //note 计算总价
      amountFee(){
        return this.fullModel.fee=util.mathFormatStr(this.realModel,this.realModel.feeExpression);
      },
    },
    methods:{
      advTipUpdate(){
        if(this.type=='city'){
          const mediaType=this.fullModel.mediaType;
          const addressRange=this.fullModel.addressRange;
          const amount=this.fullModel.amount||0;
          const count=this.fullModel.count||0;
          const width=this.fullModel.width;
          const length=this.fullModel.length;
          const select=_.find(this.fullModel.templateCache.advList,{mediaType:mediaType,addressRange:addressRange})
          if(select){
            this.fullModel.templateCache.tipAdvData={
              count:count,
              amount:amount,
              width:width,
              length:length,
              feeType:select.feeType,
              feeScale:select.feeScale,
              feeUnit:select.feeUnit,
              feeExpression:select.feeExpression
            }
          }
        }else{
          const advType=this.fullModel.advType;
          const isOut=this.fullModel.isOut;
          const amount=this.fullModel.amount||1;
          const width=this.fullModel.width;
          const length=this.fullModel.length;
          const select=_.find(this.fullModel.templateCache.advList,{value:advType})
          if(advType==0){
            this.fullModel.accessoryGroupId=this.fullModel.templateCache.linkageCode0;
            this.fullModel.isOut=0;
          }else if(advType==1){
            if(isOut==0){
              this.fullModel.accessoryGroupId=this.fullModel.templateCache.linkageCode0;
            }else if(isOut==1){
              this.fullModel.accessoryGroupId=this.fullModel.templateCache.linkageCode1;
            }
          }
          if(select){
            this.fullModel.templateCache.tipAdvData={
              amount:amount,
              width:width,
              length:length,
              feeType:select.feeType,
              feeScale:select.feeScale,
              feeUnit:select.feeUnit,
              feeExpression:select.feeExpression
            }
          }
        }
      }
    },
    created(){
      Bus.$on('advTipUpdate', this.advTipUpdate);
      //note 回显数据初始化显示,通过发送总线事件实现
      if(this.eventType){
        Bus.$emit(this.eventType);
      }
    },
    destroyed() {
      Bus.$off('advTipUpdate', this.advTipUpdate);
    }
  }
</script>

<style lang="less">
  .foot-tip-msg {
    overflow: hidden;
    zoom: 1;
    border: 1px solid #FF9933;
    background-color: #FEF4E8;
    margin-top: 20px;
    padding: 20px;
    .reminder {
      font-size: 16px;
      color: #FF9933;
      padding-left: 10px
    }
    .foot-msg {
      font-weight: 400;
      font-style: normal;
      font-size: 14px;
      color: #666666;
      line-height: 28px;
    }
    .foot-tip {
      font-size: 14px;
      color: #CC0000;
      line-height: 28px;
    }
    .foot-money {
      float: right;
      .money-icon {
        color: #CC0000;
        padding-left: 5px;
        font-weight: 700;
        font-size: 16px;
      }
      .money {
        font-weight: 700;
        font-style: normal;
        color: #CC0000;
        font-size: 24px;
        line-height: 24px;
      }
    }
  }
</style>



