<template>
  <div class="modalIcon">
    <Modal v-model="iconShow" width="300" :closable="false" :styles="{top: '250px'}"  :mask-closable="false">
      <Input v-model="baseInfo.icon" prop="icon" placeholder="请输入图形验证码 " style="margin-bottom: 10px">
        <Icon type="ios-checkmark-circle" slot="suffix" style="color:#2BA738" v-show="rightIcon"/>
        <Icon type="ios-information-circle" slot="suffix" style="color:#D75328" v-show="errorIcon"/>
      </Input>
      <div>
        <img :src="captcha"></img>
        <div style="float: right;line-height: 40px;font-size: 14px;margin-right: 30px">
          <span style="color: #999">看不清？</span><a @click="clickCaptcha">换一张</a>
        </div>
      </div>
      <div slot="footer" style="text-align: center;">
        <Button type="primary" @click="okIcon">确定</Button>
      </div>
    </Modal>
    <Button @click="getMsg">获取短信验证码</Button>
  </div>

</template>

<script>
    /**
     *@author       王婷婷
     *@date         2018/10/26 23:25
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description
     */
    export default {
        name: "modal-icon",
      data(){
          return{
            iconShow:false,
            errorIcon:false,
            rightIcon:false,
            baseInfo:{
              icon:''
            },
            randomCaptcha:'',
          }
      },
      computed:{
        captcha(){
          return `${util.getCaptcha()}?t=${this.randomCaptcha}`
        }
      },
      methods:{
        getMsg(){
          this.iconShow=true;
          this.clickCaptcha();
          this.baseInfo.icon='';
          this.errorIcon=false;
          this.rightIcon=false;
        },
        clickCaptcha(){
          this.randomCaptcha=Date.now();
        },
        okIcon(){
          this.iconShow=false;
        }



      }
    }
</script>

<style scoped>

</style>



