<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
  /**
   *@desc     下拉列表
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-form-select",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '下拉列表'
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'form',
                  data: {
                    //双向绑定数据对象
                    model: {
                      normalSelect1:'female',
                      normalSelect2:'female',
                      normalSelect3:'female',
                    },
                    //规则
                    rules: {
                    },
                    //Form扩展配置
                    extend: {
                      labelWidth:300,
                      span:15,
                    },
                    items:
                      [
                        {
                          type: 'select',
                          label: '下拉选择 type:select',
                          prop: 'normalSelect1',
                          extend:{
                            placeholder:'placeholder',
                            span:12
                          },
                          items: [
                            {
                              label: '男',
                              value: 'male',
                              extend:{
                              }
                            },
                            {
                              label: '女',
                              value: 'female'
                            }
                          ],
                        },
                        {
                          type: 'select',
                          label: '下拉选择 type:select',
                          prop: 'normalSelect2',
                          excat:[{
                            type:'lookup-map'
                          }],
                          extend:{
                            placeholder:'placeholder',
                            span:12
                          },
                          items: [
                            {
                              label: '男',
                              value: 'male',
                              extend:{
                              }
                            },
                            {
                              label: '女',
                              value: 'female'
                            }
                          ],
                        },
                        {
                          type: 'select',
                          label: '下拉选择 type:select',
                          prop: 'normalSelect3',
                          excat:[{
                            type:'lookup-fee'
                          }],
                          extend:{
                            placeholder:'placeholder',
                            span:12
                          },
                          items: [
                            {
                              label: '男',
                              value: 'male',
                              extend:{
                              }
                            },
                            {
                              label: '女',
                              value: 'female'
                            }
                          ],
                        },
                        {
                          type: "select",
                          label: "下拉查询",
                          prop: 'normalSelect3',
                          dynamic:{
                            url:"/post/qryMailInfoDetail",//查询接口
                            params:{ //入参
                              aa:11,
                              bb:22
                            },
                            label:"title", //把后台返回的值转为对应的label或value，不需要转的时候不需要写
                            value:"val"
                          },
                          extend: {
                            eventType: "advTipUpdate",
                            placeholder: "请选择",
                            itemWidth: 320
                          }
                        }
                      ]
                  }
                }
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



