      columns1() {
        if (this.infoData.templateCache) {
          let cache = this.infoData.templateCache;
          if (this.num) {
            return cache[this.num]
          } else if (this.itemProp) {
            let params = this.itemProp.split('.')[1];
            return cache[params]
          }
        }
      },
      //note 显示表格时候的表数据
      data2() {
        let real = this.infoData;
        if (this.prop.indexOf('.') != -1) {
          let params = this.prop.split('.');
          while (params.length > 0) {
            real = real[params.shift()];
          }
        } else {
          real = this.infoData[this.prop];
        }
        if (real instanceof Array) {
          real.forEach(item => {
            for (let i in item) {
              if (item[i] instanceof Array) {
                item[i] = `${moment(item[i][0]).format("YYYY年MM月DD日")}-${moment(item[i][1]).format("YYYY年MM月DD日")}`;
              }
            }
          })
          this.columns1.forEach((item, index) => {
            if (item.label) {
              this.handleTable(index, item, real);
            }
          })
        }
        return real;
      },
