<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/9/10 10:16
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description  时间线
     */

    export default {
        name: "example-page-template-time-line",
        data() {
          return {
            pageData: {
              //页面基础数据
              page: {
                fullModel: {
                  timeLineData:[
                    {
                      color: "blue",
                      dealTimeStr: "2018-05-05 14:22:10",
                      workerName:"张三",
                      operateType:"派发",
                      content:"发布1.0版本"
                    },
                    {
                      color: "blue",
                      dealTimeStr: "2018-05-05 14:22:10",
                      workerName:"张三",
                      operateType:"派发",
                      content:"发布1.0版本"
                    },
                    {
                      color: "blue",
                      dealTimeStr: "2018-05-05 14:22:10",
                      workerName:"张三",
                      operateType:"派发",
                      content:"发布1.0版本"
                    },
                    {
                      color: "blue",
                      dealTimeStr: "2018-05-05 14:22:10",
                      workerName:"张三",
                      operateType:"派发",
                      content:"发布1.0版本"
                    }
                  ],
                  state:'3',//根据不同的state值显示不同结果
                }
              },
              //页面元素数据
              elements: [
                {
                  type: 'box',
                  data: [
                    {
                      type: 'time-line',
                      data: {
                          model:'timeLineData',
                          qrCodeShow:true, //二维码是否显示
                          pending:false //指定是否最后一个节点为幽灵节点
                        }
                    }
                  ]
                }
              ]
            }
          }
        }
    }
</script>



