<template>
  <ty-page-template :data="pageData"/>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/9/10 14:41
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description  证件下载扫描
     */
    export default {
        name: "example-page-template-pap-download-scan",
        data() {
          return {
            pageData: {
              //页面基础数据
              page: {
                fullModel: {
                  //后台返回，证照下载的地址
                  fileUrlZz:''
                }
              },
              //页面元素数据
              elements: [
                {
                  type: "box",
                  nopadding: true,
                  border: "none",
                  data: [
                    {
                      type: "pap-download-scan",
                      data: {
                        model:"advertisement",
                        title: "西安高新区广告设置许可证", //标题名称
                        btnName:"下载证照", //按钮名称
                        detailWarn: "下载前请仔细阅读《西安高新区户外广告设置使用安全责任书》，然后点击“同意并下载”按钮",
                        detail: "请下载本许可证，打印并张贴在广告设置位附近。该证照有效期为一年，到期继续设置的，请提前再次申请审批"
                      }
                    }
                  ]
                }
              ]
            }
          }
        }
    }
</script>




