<template>
  <div class="ty-component-element-alert" v-if="show">
    <Alert :type="type" v-bind="extend" show-icon :closable="closable">
      <div v-html="content"/>
    </Alert>
  </div>
</template>

<script>
  /**
   *@desc
   *@author
   *@date
   */
  import {realModel} from "../../mixin-page-template";
  import consts from "../../../../utils/consts";
  export default {
    name: "ty-content-element-tip-alert",
    mixins: [realModel],
    props: {
      //note alert类型
      type: {
        default:'warning'
      },
      closable: {
        default:true
      },
      //note 内容
      content: {
        default: '请点击<a>“下载申请表”</a> 按钮下载本次事项的申请表，并与物业、业主或业主委员会确认。'
      },
      //根据isShow动态控制显隐
      isShow:{
        type:String
      },
      extend:{
      }
    },
    computed:{
      show(){
        /*console.log(this.realModel);
        if(this.isShow){
          if(this.realModel[this.isShow]===true){
            return true
          }else{
            return false
          }
        } else if(_.isArray(this.$route.query.name,consts.ERSHOUFANG)&&this.realModel.state=='480'){
          return true
        }*/
        console.log(consts.ERSHOUFANG.includes(this.$route.query.name));
        if(consts.ERSHOUFANG.includes(this.$route.query.name)) {
          if (this.realModel.state=='480') {
            return true
          } else {
            return false
          }
        } else {
          return true
        }
      }
    }
  }
</script>

<style lang="less">
  .ty-component-element-alert{
    .ivu-alert {
      border-radius: 0;
    }
    .ivu-alert-warning {
      border:1px solid rgba(255, 153, 51, 1);
      background-color: rgba(252, 237, 230, 1);
    }
    .ivu-alert-none {
      background-color: rgba(252, 237, 230, 1);
    }
    .ivu-icon{
      color: orange;
    }
  }
</style>



