                        click: () => {
                          this.demoPic1 = params.row.enclosureUrl;
                          this.modal3 = true
                        }
                      }
                    })
                  } else {
                    return h('a', {
                      on: {
                        click: () => {
                          window.open(params.row.enclosureUrl)
                        }
                      }
                    }, '下载查看文件')
                  }

                }
              }
            ]
        }
      }

    },
    watch: {
      singleData: {
        handler(newObj, oldObj) {
          this.imgOcrAbility(newObj)
        },
        deep: true
      }
    },
    methods: {
      // note 图片识别
      imgOcrAbility(data) {
        console.log(this.realModel[this.current].isAutoCheck)
        let params = {};
        if (this.realModel[this.current].isAutoCheck == "1") {
          params = {
            ...data,
            checkType: this.realModel[this.current].autoCheckType,
            headName: this.realModel[this.current].formHandle
          };
          this.spinShow = true;
          Service.ocrAbility({...params}).then(res => {
            this.queryGroupOcrFlag();
          }).catch(res => {
            this.spinShow = false;
          })
        }
      },
      // note 图片识别之后回显
      queryGroupOcrFlag() {
        Service.queryGroupOcrFlag({
          projId: this.fullModel.projId,
          attrId: this.realModel[this.current].materialsId
        }).then(res => {
          this.spinShow = false;
          this.realModel[this.current].codeFlag = res.codeFlag;
          this.realModel[this.current].headFlag = res.headFlag;
          this.realModel[this.current].qianziFlag = res.qianziFlag;
          this.realModel[this.current].sealFlag = res.sealFlag;
        })
      },
      //note 选择资料库里的文件点击确定之后
      chooseData() {
        //  note 单个上传
        if ( this.realModel[this.current].upLimitNum == '0') {
          this.realModel[this.current].upLimitNum = "9"
        }

        if (this.fullModel.state == '202' || this.fullModel.state == '210' || this.fullModel.state == '310') {
          let pizhuPicNum = 0;
          for (let i = 0; i <this.realModel[this.current].applicationAttrInfoList.length; i++ ) {
            if (this.realModel[this.current].applicationAttrInfoList[i].attachmentStatus == '3') {
              pizhuPicNum++
            }
          }
          this.limitNum = this.realModel[this.current].upLimitNum ? this.realModel[this.current].upLimitNum + pizhuPicNum : '999';
          console.log(this.limitNum)
        } else {
          this.limitNum = this.realModel[this.current].upLimitNum
        }

        console.log(this.dataBase);

        if (JSON.stringify(this.dataBase) != '[]') {

          if (this.dataBase.enclosureSize / 1024 <=this.realModel[this.current].upLimitSize ) {
            if (this.realModel[this.current].applicationAttrInfoList.length < this.limitNum) {
              if (JSON.stringify(this.dataBase) != '[]') {
                this.uploadProp.serviceCode = this.fullModel.serviceCode;
                this.uploadProp.takeType = 3;
                this.uploadProp.name = this.dataBase.enclosureName;
                this.uploadProp.fileUrl = this.dataBase.enclosureUrl;
                this.uploadProp.ossId = this.dataBase.enclosureUrl;
                this.uploadProp.amount = this.realModel[this.current].applicationAttrInfoList.length + 1;
                this.uploadProp.ext = this.dataBase.enclosureUrl.split('.')[this.dataBase.enclosureUrl.split('.').length - 1];
                this.uploadProp.enclosureSource = this.dataBase.enclosureSource;
                this.uploadProp.bucketUrl = this.dataBase.bucketUrl;
                this.uploadProp.enclosureSize = this.dataBase.enclosureSize;

                Service.uploadAttach(this.uploadProp).then(data => {
                  util.alert({type: 'success', content: "上传成功!"});
                  this.realModel[this.current].applicationAttrInfoList.push(data);
                  this.imgOcrAbility(data)
                });
                this.dataBase = []
              }
            } else {
              util.alert({type: 'error', content: '最多上传' + this.realModel[this.current].upLimitNum + '个文件'});
            }
          } else {
            util.alert({type: 'error', content: '上传文件最大不能超过' + this.realModel[this.current].upLimitSize + 'k'});
          }
        }



      },
      //note 获取我的证照库的数据
      getAutoData() {
        Service.autoFindAttr(this.autoFindQuery).then(res => {
          console.log(res);
          this.ziliaoData = res
          // let upData = res.splice(0,canUp);
          // for (let i = 0; i< upData.length; i++) {
          //   this.realModel[params.row._index].applicationAttrInfoList.push(upData[i])
          // }
        })
      },

      //note 选择我的证照库的文件点确定之后
      chooseLicenseData() {
        console.log(this.dataBase);
        console.log(this.realModel[this.current].upLimitNum);
        if ( this.realModel[this.current].upLimitNum == '0') {
          this.realModel[this.current].upLimitNum = "9"
        }

        this.realModel[this.current].upLimitNum ? this.realModel[this.current].upLimitNum : '8';

        if (this.fullModel.state == '202' || this.fullModel.state == '210' || this.fullModel.state == '310') {
          let pizhuPicNum = 0;
          for (let i = 0; i <this.realModel[this.current].applicationAttrInfoList.length; i++ ) {
            if (this.realModel[this.current].applicationAttrInfoList[i].attachmentStatus == '3') {
              pizhuPicNum++
            }
          }
          this.limitNum = this.realModel[this.current].upLimitNum ? this.realModel[this.current].upLimitNum + pizhuPicNum : '999';
          console.log(this.limitNum)
        } else {
          this.limitNum = this.realModel[this.current].upLimitNum
        }

        if (JSON.stringify(this.dataBase) != '[]') {
          if (this.dataBase.enclosureSize / 1024 <= this.realModel[this.current].upLimitSize ) {
            if (this.realModel[this.current].applicationAttrInfoList.length < this.limitNum) {
              let params = {};
              params = {
                ...this.dataBase,
                enclosureEnumType: this.realModel[this.current].materialsSubType,
                projId:this.fullModel.projId
              };
              Service.uploadAttach(params).then(data => {
                util.alert({type: 'success', content: "上传成功!"});
                this.realModel[this.current].applicationAttrInfoList.push(data)
                this.imgOcrAbility(data)
              });
            } else {
              util.alert({type: 'error', content: '最多上传' + this.realModel[this.current].upLimitNum + '个文件'});
            }
          } else {
             util.alert({type: 'error', content: '上传文件最大不能超过' + this.realModel[this.current].upLimitSize + 'k'});
          }
        }
      },


      // //note 当前选中的资料库
      // chooseKu(name) {
      //   console.log(name)
      //   this.currentKu = name
      //
      //   if (name == 'dataLicense') {
      //     this.getAutoData()
      //   }
      // },
      //note 这是选择了资料库的文件
      onCurrentChange(data) {
        this.dataBase = data
        console.log(data)
      },
      //  note 搜索
      searchAttach() {
        if (this.searchName != '') {
          this.$refs.table.query({enclosureName: this.searchName, pageNo: 1})
        } else {
          this.$refs.table.query({enclosureName: '', pageNo: 1})
        }
      }
    },
    created() {
      this.autoFindQuery.serviceCode = this.fullModel.serviceCode;
      this.autoFindQuery.customerId = this.storeUserInfo.customerNo;
      if (this.type == 'noAction') {
        this.columns1 = this.columns1.splice(this.columns1.length - 1, 1)
      }

      if (this.fullModel.state == '202' || this.fullModel.state == '210' || this.fullModel.state == '310') {
        if (this.fullModel.type == '1') {
          this.tipContent = '请点击<a>下载申请表</a>按钮下载本次事项的申请表。上传照片与扫描件，图片必须清晰，必须与主题相符，支持png、jpg、jpeg、pdf、docx、doc、xlsx、xls文件格式 <br/>' +
            '温馨提示：请您先删除原文件后，再按照批注提示或备注信息将修改后的文件上传至需补正的文件处！'
        } else {
          this.tipContent = '上传照片与扫描件，图片必须清晰，必须与主题相符，支持png、jpg、jpeg、pdf、docx、doc、xlsx、xls文件格式 <br/>' +
            '温馨提示：请您先删除原文件后，再按照批注提示或备注信息将修改后的文件上传至需补正的文件处！'
        }
      } else {
        if (this.fullModel.type == '1') {
          this.tipContent = '请点击<a>下载申请表</a>按钮下载本次事项的申请表。上传照片与扫描件，图片必须清晰，必须与主题相符，支持png、jpg、jpeg、pdf、docx、doc、xlsx、xls文件格式'
        } else {
          this.tipContent = '上传照片与扫描件，图片必须清晰，必须与主题相符，支持png、jpg、jpeg、pdf、docx、doc、xlsx、xls文件格式'
        }
      }

    }
  }
</script>

<style lang="less">
  .attachFileTable {
    .demo-spin-icon-load {
      animation: ani-demo-spin 1s linear infinite;
    }
    .ivu-spin {
      background-color: rgba(241, 241, 241, 0.5);
      z-index: 9999;
    }
    .ivu-spin-fix {
      position: fixed;
    }
  }
</style>



