<template>
  <ty-page-template :data="pageData"/>
</template>

<script>

  /**
   *@desc     标题
   *@author   谢辉
   *@date     2018.9.5
   */
  export default {
    name: "example-page-template-title",
    data() {
      return {
        pageData: {
          //页面基础数据
          page: {
            title: '标题'
          },
          //页面元素数据
          elements: [
            {
              type: 'box',
              data: [
                {
                  type: 'title',
                  data: {
                    title: '标题组件 type:title'
                  }
                }
              ]
            }
          ]
        }
      }
    }
  }
</script>

<style scoped>

</style>



