<template>
  <examples-demo title="时间线组件 timeLine" desc="" :code="code.timeLine"
                 :table-columns="columns"
                 :table-data="data">
    <example-page-template-time-line slot="demo" />
  </examples-demo>
</template>

<script>
    /**
     *@author       刘文军
     *@date         2018/9/10 10:18
     *@Copyright    天源迪科信息技术股份有限公司
     *@Description  时间线
     */
    import Code from './code';
    import ExamplesDemo from "../../components/demo";
    import ExamplePageTemplateTimeLine from "./example-page-template-time-line";

    export default {
        name: "example-page-template-time-line-example",
        components: {ExamplePageTemplateTimeLine, ExamplesDemo},
        data() {
          return {
            code: Code,
            columns:[
              {
                title: '属性',
                key: 'prop'
              },
              {
                title: '说明',
                key: 'desc'
              },
              {
                title: '类型',
                key: 'type'
              },
              {
                title: '默认值',
                key: 'default'
              }
            ],
            data:[
              {
                prop: 'fullModel',
                desc: '全局绑定对象,',
                type: '*',
                default: '空'
              },
              {
                prop: 'model',
                desc: '无fullModel情况下,给model赋值对象直接绑定model,' +
                '有fullModel情况下,默认$root取fullModel根对象绑定,如果去内部需要设置model,' +
                '例如"a.b.c",就会绑定fullModel.a.b.c',
                type: 'String,Object',
                default: '$root'
              },
              {
                prop: 'state',
                desc: '控制办件状态的值',
                type: 'String Number',
                default:'空'
              },
              {
                prop: 'qrCodeShow',
                desc: '二维码是否显示',
                type: 'Boolean',
                default:'false'
              },
              {
                prop: 'pending',
                desc: '指定是否最后一个节点为幽灵节点',
                type: 'Boolean',
                default:'false'
              },
              {
                prop: '',
                desc: '',
                type: '',
                default:''
              },
              {
                prop: '',
                desc: '',
                type: '',
                default:''
              }
            ]
          }
        }
    }
</script>



