let Code = {};
Code.base=`
<template>
    <UserInfo :userData="userData"></UserInfo>
</template>

<script>
  import UserInfo from '../../../components/userInfo'
  export default {
    name: "user-info",
    components: {UserInfo},
    data(){
      return {
        userId:'',
        userData: {
          userName:'吴帆',
          userNameSpell:'wufan',
          userCompany:'西安市陕西秦农农村商业银行股份有限公司锦业支行',
          handing:2,
          checking:12,
          needPay:8
        }
      }
    }
  }
</script>
`;
export default Code;



