</template>

<script>
  import Code from './code';
  import ExamplesDemo from "../../components/demo";
  import Instance from "./instance";

  /**
   *@desc
   *@author
   *@date
   */
  export default {
    name: "index",
    components: {ExamplesDemo,Instance},
    data() {
      return {
        code: Code
      }
    }
  }
</script>



