<template>
  <div class="paymentPay" v-bind="$props" >
    <div class="chat clearfix">
      <div class="chat-img">
        <img src="../assets/img/icon-chat.png"/>
        <!--<img :src="chatImgUrl"/>-->
      </div>
      <ul class="chat-msg">
        <li class="msg-title">{{msgTitle}}</li>
        <li class="msg-content">{{msgContent}}办件编号：{{applicationNumber}} 收款方：{{receivables}}</li>
        <!--<li class="msg-alipay">推荐使用<img :src="chatSmallImgUrl"/> 支付宝扫码支付 | 请您在24小时内完成支付</li>-->
        <li class="msg-alipay">推荐使用<img src="../assets/img/icon-small-chat.png"/> {{paymentApp}}扫码支付 | 请您在24小时内完成支付</li>
      </ul>
      <div class="chat-money">
        应付金额<span class="alipay-money">{{alipayMoney}}</span>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
      name: "ty-payment-pay",
      props:{
        msgTitle:{
          type:String
        },
        msgContent:{
          type:String
        },
        applicationNumber:{
          type:String
        },
        receivables:{
          type:String
        },
        alipayMoney:{
          type:Number
        },
        chatImgUrl:{
          type:String
        },
        chatSmallImgUrl:{
          type:String
        },
        paymentApp:{
          type:String
        }

      },
      data(){
        return {

        }
      },
      methods:{},
      created(){}
    }
</script>



